.collection-refine-by {
	display: none;

	.boost-pfs-filter-refine-by {
		display: flex;
		align-items: center;
		margin: 0;

		> span {
			display: none;
		}

		.boost-pfs-filter-clear-all {
			position: relative;
			float: none;
			display: inline-block;
			flex-shrink: 0;
			margin-bottom: 8px;
			margin-left: 12px;
			font-family: $font-avenir-next-condensed-light;
			font-size: 12px;
			font-weight: 500;
			line-height: 1.67;
			letter-spacing: 0.07em;
			text-transform: uppercase;
			text-decoration: none;
			color: $brand-primary;

			&::after {
				content: "";
				position: absolute;
				bottom: 0;
				left: 0;
				display: block;
				width: 100%;
				height: 1px;
				background-color: $brand-primary;
				transition: width 0.25s ease;
			}

			@include hover {
				&::after {
					width: 0;
				}
			}
		}
	}

	.boost-pfs-filter-refine-by-items {
		overflow-x: auto;
		padding-bottom: 8px;
		white-space: nowrap;

		&::-webkit-scrollbar {
			height: 4px;
		}

		&::-webkit-scrollbar-track {
			border-radius: 2.5px;
			background-color: $grey-light;
		}

		&::-webkit-scrollbar-thumb {
			border-radius: 2.5px;
			background-color: $grey-middle;
		}

		.refine-by-item {
			position: relative;
			float: none !important;
			display: inline-block;
			height: initial !important;
			margin-bottom: 0;
			margin-right: 8px;
			padding: 0;
			border-radius: 0 !important;
			background-color: $background-light-grey !important;
			line-height: normal !important;

			&:last-of-type {
				margin-right: 0;
			}

			.boost-pfs-filter-button.boost-pfs-filter-clear {
				position: relative;
				padding-top: 7px;
				padding-bottom: 7px;
				padding-left: 8px;
				padding-right: 28px;

				&::before,
				&::after {
					top: 50%;
					right: 12px;
					width: 10px;
					height: 1px;
					border: 0;
					background-color: $paragraph-secondary;
					transition: background-color 0.25s ease-out;
				}

				&::before {
					transform: translateY(-50%) rotate(-45deg);
				}

				&::after {
					transform: translateY(-50%) rotate(45deg);
				}

				.refine-by-type {
					padding: 0;
					font-size: 12px;
					line-height: 1.5;
					color: $paragraph-secondary;
					transition: color 0.25s ease-out;
				}
			}

			.refine-by-option {
				display: none;
			}

			@include hover {
				.boost-pfs-filter-button.boost-pfs-filter-clear {
					&::before,
					&::after {
						background-color: $paragraph-primary;
					}

					.refine-by-type {
						color: $paragraph-primary;
					}
				}
			}
		}
	}
}

@include breakpoint(large) {
	.collection-refine-by {
		display: block;
	}
}
