.breadcrumbs {
	a,
	span {
		font-size: 12px;
		line-height: 1.5;
		color: $brand-primary;
		transition: color 0.25s ease-out;
	}

	a {
		text-decoration: none;
		color: $brand-secondary;

		@include hover {
			color: $brand-primary;
		}
	}

	.breadcrumbs__container {
		overflow-x: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
	}

	.breadcrumbs__separator {
		padding-left: 10px;
		padding-right: 10px;
	}

	&.breadcrumbs--subcolor {
		a,
		span {
			font-family: $font-avenir-next-cyr;
			font-size: 12px;
			line-height: 1.5;
			transition: color 0.25s ease-out;
		}

		a {
			opacity: 0.6;
			transition: opacity 0.3s ease-in-out;

			@include hover {
				opacity: 1;
			}
		}
	}
}

.breadcrumbs--customized {
	.breadcrumbs__separator {
		padding-left: 8px;
		padding-right: 9px;
		font-size: 15px;
	}
}
