.account-order-details {
	.account-content__header {
		margin-bottom: 4px;
	}

	.account-content__header-title {
		display: flex;
		flex-direction: row;
		align-items: center;
	}

	.account-content__header-icon {
		width: 43px;
		height: 48px;

		svg {
			width: 100%;
			height: 100%;
		}
	}

	.account-content__back-link-container {
		display: block;
		margin-bottom: 19px;
	}

	.account-content__back-link-icon {
		padding-right: 12px;

		svg.icon {
			width: 18px;
			height: 16px;
		}
	}

	.account-content__back-link {
		display: flex;
		width: max-content;
		font-family: $font-avenir-next-condensed;
		font-size: 12px;
		line-height: 1.67;
		letter-spacing: 0.04em;
		text-transform: uppercase;
		text-decoration: none;
		flex-direction: row;
	}
}

.account-order-details__content {
	margin-bottom: 40px;
}

.account-order-details__table-title {
	padding-top: 15px;
	padding-bottom: 6px;
	padding-left: 16px;
	font-family: $font-avenir-next-condensed-light;
	font-size: 12px;
	font-weight: 600;
	line-height: 1.5;
	letter-spacing: 0.06em;
	text-align: start;
	text-transform: uppercase;
	font-style: normal;
}

.account-order-details__table-title--no-padding {
	padding-left: 0;
}

.account-order-details__table {
	width: 100%;
	table-layout: fixed;
	border-collapse: collapse;
	border-spacing: 0;

	tr {
		border-bottom: 1px solid $border-primary;

		&:last-child {
			border-bottom: unset;
		}
	}
}

.account-order-details__table-row {
	border: 1px solid $border-primary;
}

.account-order-details__fulfillment-status {
	display: inline-block;
	width: max-content;
	padding: 2px 8px;
	color: $paragraph-primary;
	border-radius: 2px;
}

.account-order-details__fulfillment-status--unfulfilled {
	background-color: rgba($status-unfulfilled, 0.4);
}

.account-order-details__fulfillment-status--fulfilled {
	background-color: rgba($status-fulfilled, 0.4);
}

.account-order-details__fulfillment-status--partially-fulfilled {
	background-color: rgba($status-partially-fulfilled, 0.4);
}

.account-order-details__fulfillment-status--scheduled {
	background-color: rgba($status-scheduled, 0.4);
}

.account-order-details__fulfillment-status--on-hold {
	background-color: rgba($status-on-hold, 0.4);
}
//Start mobile top table
.account-order-details__mobile-order {
	display: flex;
	margin-bottom: 30px;
	padding-top: 16px;
	padding-bottom: 14px;
	padding-left: 16px;
	padding-right: 16px;
	border: 1px solid $border-primary;
	flex-direction: row;
	flex-wrap: wrap;
}

.account-order-details__mobile-order-item {
	&:nth-child(2n + 1) {
		padding-top: 24px;
	}

	&:first-child {
		padding-top: 0;
	}
}

.account-order-details__mobile-order-item--wide {
	width: 53%;
}

.account-order-details__mobile-order-title {
	padding-bottom: 7px;
	font-family: $font-avenir-next-condensed-light;
	font-size: 12px;
	font-weight: 500;
	line-height: 1.5;
	letter-spacing: 0.06em;
	text-transform: uppercase;
	color: $brand-primary;
	font-style: normal;
}

.account-order-details__mobile-order-field {
	font-size: 12px;
	font-weight: normal;
	line-height: 1.5;
	color: $brand-secondary;
	font-style: normal;
}
//End mobile top table
.account-order-details__table-item--product {
	display: flex;
	flex-direction: row;
	align-items: center;
}

.account-order-details__table-item--quantity {
	vertical-align: middle;
	text-align: start;
}

.account-order-details__table-item--subtotal {
	vertical-align: middle;
	text-align: start;
}

.account-order-details__table-item-image {
	position: relative;
	overflow: hidden;
	display: block;
	width: 104px;
	padding-top: 25.67%;

	img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: 50% 50%;
	}
}

.account-order-details__table-item-vendor {
	font-size: 14px;
	font-weight: normal;
	line-height: 1.43;
	font-style: normal;
}

.account-order-details__table-item-name {
	font-size: 12px;
	font-weight: normal;
	line-height: 1.5;
	letter-spacing: 0.05em;
	color: $brand-secondary;
	font-style: normal;
}

.account-order-details__table-item-fields {
	display: flex;
	flex-direction: column;
	padding-left: 12px;
}

.account-order-details__table-item-options {
	display: flex;
	padding-top: 6px;
	font-size: 12px;
	font-weight: normal;
	line-height: 1.5;
	color: $brand-secondary;
	flex-direction: column;
	font-style: normal;
}

.account-order-details__addresses-container {
	display: flex;
	flex-direction: column;
	margin-bottom: 40px;
}

.account-order-details__addresses-item {
	+ .account-order-details__addresses-item {
		margin-top: 40px;
	}
}

.account-order-details__addresses-item-title {
	padding-bottom: 12px;
	font-size: 16px;
	font-weight: bold;
	line-height: 1.37;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	font-style: normal;
}

.account-order-details__addresses-item-text {
	p {
		font-size: 14px;
		font-weight: normal;
		line-height: 1.43;
		color: $brand-secondary;
		font-style: normal;
	}
}

.account-order-details__return-title {
	margin-bottom: 22px;
	font-size: 20px;
	font-weight: bold;
	line-height: 1.1;
	letter-spacing: 0.07em;
	text-transform: uppercase;
	font-style: normal;
}

.account-order-details__return-info {
	margin-bottom: 20px;
	font-size: 14px;
	font-weight: normal;
	line-height: 20px;
	color: $brand-secondary;
	font-style: normal;

	p {
		margin-bottom: 20px;
		font-size: 14px;
		font-weight: normal;
		line-height: 20px;
		color: $brand-secondary;
		font-style: normal;

		a {
			font-weight: 500;
			text-decoration: none;
			color: $brand-primary;
		}
	}
}

.account-order-details__return-info-link {
	font-weight: 500;
	text-decoration: none;
	color: $brand-primary;
}

.account-order-details__return-link {
	max-width: 335px;
	width: 100%;
}

.account-order-details__table--mobile-hiden {
	display: none;
}

//mobile table ( bottom table)

.account-order-details__mobile-table-row {
	display: flex;
	flex-direction: row;
	border: 1px solid $border-primary;

	+ .account-order-details__mobile-table-row {
		border-top: unset;
	}
}

.account-order-details__mobile-table-item {
	width: 128px;
	max-width: 128px;
}

.account-order-details__mobile-table-image {
	position: relative;
	overflow: hidden;
	display: block;
	width: 128px;
	padding-top: 133%;

	img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: 50% 50%;
	}
}

.account-order-details__mobile-table-fields {
	padding-top: 6px;
	padding-bottom: 6px;
	padding-left: 6px;
	padding-right: 12px;
}

.account-order-details__mobile-item-top {
	padding-bottom: 6px;
}

.account-order-details__mobile-item-bottom {
	padding-bottom: 8px;
}

.account-order-details__mobile-table-product-title {
	font-size: 12px;
	font-weight: normal;
	line-height: 1.5;
	color: $brand-secondary;
	font-style: normal;
}

.account-order-details__mobile-item-options {
	font-size: 12px;
	font-weight: normal;
	line-height: 1.5;
	color: $brand-secondary;
	font-style: normal;
}
.account-order-details__mobile-item-fulfillment {
	padding-bottom: 4px;
	font-size: 14px;
	font-weight: normal;
	line-height: 1.43;
	color: $brand-primary;
	font-style: normal;
}

.account-order-details__mobile-item-quantity {
	padding-bottom: 4px;
	font-size: 14px;
	font-weight: normal;
	line-height: 1.43;
	color: $brand-primary;
	font-style: normal;
}

.account-order-details__mobile-item-subtotal {
	font-size: 14px;
	font-weight: normal;
	line-height: 1.43;
	color: $brand-primary;
	font-style: normal;
}

.account-order-details__totals-row {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.account-order-details__totals-row--total {
	padding-top: 8px;
}

.account-order-details__totals-row--shipping-price {
	padding-top: 8px;
}

.account-order-details__totals-text {
	font-size: 14px;
	font-weight: 500;
	line-height: 1.43;
	font-style: normal;
}

.account-order-details__totals-text--secondaty {
	font-weight: normal;
	color: $brand-secondary;
}

.account-order-details__totals {
	width: 100%;
	margin-left: auto;
	padding-top: 21px;
	padding-bottom: 18px;
	padding-left: 26px;
	padding-right: 26px;
	border: 1px solid $border-primary;
	border-top: unset;
}

@include breakpoint(small) {
	.account-order-details__addresses-item {
		+ .account-order-details__addresses-item {
			margin-top: 0;
			margin-left: 70px;
		}
	}

	.account-order-details__addresses-container {
		flex-direction: row;
	}
}

@include breakpoint(medium) {
	.account-order-details__return-block {
		padding-left: 4px;
	}

	.account-order-details__top-table {
		max-width: 331px;

		.account-order-details__table-title--fulfillment {
			width: 40%;
		}
	}

	.account-order-details__mobile-order {
		display: none;
	}

	.account-order-details {
		.account-content__header {
			margin-bottom: 22px;
		}
	}

	.account-order-details__content {
		margin-bottom: 76px;
	}

	.account-order-details__table--margin-bottom {
		margin-bottom: 40px;
	}

	.account-order-details__totals {
		max-width: 38.32%;
	}

	.account-order-details__table-item--date {
		padding-top: 9px;
		padding-bottom: 13px;
		padding-left: 16px;
		font-size: 12px;
		font-weight: normal;
		line-height: 1.5;
		color: $brand-secondary;
		font-style: normal;
	}

	.account-order-details__table-title--order-date {
		width: 48%;
	}

	.account-order-details__table-item--fulfillment {
		width: 37.54%;
		padding-bottom: 13px;
		padding-left: 0;
	}

	.account-order-details__table-item--payment {
		width: 24.03%;
		padding-left: 0;
		padding-right: 16px;
	}

	.account-order-details__table-row--unborder {
		border-top: unset;

		+ .account-order-details__table-row--unborder {
			border-top: unset;
		}
	}

	.account-order-details__table-title--product {
		width: 39.75%;
	}
	.account-order-details__table-title--fulfillment-date {
		width: 25.96%;
	}

	.account-order-details__table-title--quantity {
		width: 17.69%;
	}

	.account-order-details__table-title--subtotal {
		width: 16.6%;
	}

	.account-order-details__return-title {
		margin-bottom: 16px;
		font-size: 24px;
		line-height: 1.17;
		letter-spacing: 0.08em;
	}

	.account-order-details__return-link {
		max-width: 240px;
	}

	.account-order-details__table--mobile-hiden {
		display: table;
	}

	.account-order-details__mobile-table {
		display: none;
	}

	.account-order-details__table-item-image {
		padding-top: 39.6%;
	}

	.account-order-details__table-item-fields {
		padding-left: 15px;
	}

	.account-order-details__table--bottom {
		.account-order-details__table-title {
			padding-top: 16px;
			padding-bottom: 16px;
		}
	}

	.account-order-details__table-item--fulfillment-date {
		padding-left: 19px;
		vertical-align: middle;
	}
}

@include breakpoint(large) {
	.account-order-details__table-title--product {
		width: 39.75%;
	}
	.account-order-details__table-title--fulfillment-date {
		width: 25.9%;
	}

	.account-order-details__table-title--quantity {
		width: 21.69%;
	}

	.account-order-details__table-title--subtotal {
		width: 12.6%;
	}

	.account-order-details__totals {
		padding-left: 35px;
		padding-right: 36px;
	}
}
