.footer-social-icons {
	border-top: 1px solid $border-primary;
	padding-top: 30px;
	padding-bottom: 30px;

	background-color: #fafafa;
}

.footer-social-icons__container {
	max-width: 1350px;
}

.footer-social-icons__row {
	justify-content: center;

	margin: 0 -17px;
}

.footer-social-icons__column {
	padding: 0 17px;
}

.footer-social-icons__link {
	svg.icon {
		display: block;
		width: unset;
		max-width: 20px;
		height: 20px;
	}
}

@include breakpoint(medium) {
	.footer-social-icons {
		border-top: 1px solid $border-primary;
		padding-top: 31px;
		padding-bottom: 30px;

		background-color: #fafafa;
	}

	.footer-social-icons__container {
		max-width: 1364px;
	}

	.footer-social-icons__row {
		justify-content: center;

		margin: 0 -20px;
	}

	.footer-social-icons__column {
		padding: 0 20px;
	}

	.footer-social-icons__link {
		svg.icon {
			max-width: 20px;
			height: 20px;
		}
	}
}

@include breakpoint(large) {
	.footer-social-icons {
		padding-top: 34px;
		padding-bottom: 34px;
	}

	.footer-social-icons__link {
		svg.icon {
			display: block;
			width: unset;
			max-width: 30px;
			height: 30px;
		}
	}
}
