.store-overview-map {
	.store-overview-map__map-wrapper {
		position: relative;
		width: 100%;
	}

	.store-overview-map__map {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		width: 100%;
		height: 100%;
		background-color: $white;

		.gm-style {
			.gm-style-iw-c {
				padding-top: 25px;
				padding-left: 20px;
				border-radius: 0;
				box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
				max-height: 200px !important;
				max-width: 230px !important;
			}

			.gm-style-iw-d {
				padding-right: 0;
				max-height: 160px !important;
			}

			.gm-style-iw-t::after {
				box-shadow: -2px 2px 2px 0 rgba(0, 0, 0, 0.1);
			}
		}
	}

	.store-overview-map__modal-image-wrapper {
		position: relative;
		overflow: hidden;
		width: 100%;
		margin-top: auto;
		margin-bottom: 20px;
	}

	.store-overview-map__modal-image {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: 50% 50%;
	}

	.store-overview-map__modal-text {
		strong {
			//
		}

		p {
			//
		}
	}

	@include breakpoint(small) {
		.store-overview-map__map {
			.gm-style {
				.gm-style-iw-c {
					padding-top: 25px;
					padding-left: 20px;
					border-radius: 0;
					box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
					max-height: 350px !important;
					max-width: 310px !important;
				}

				.gm-style-iw-d {
					padding-right: 0;
					max-height: 310px !important;
				}

				.gm-style-iw-t::after {
					box-shadow: -2px 2px 2px 0 rgba(0, 0, 0, 0.1);
				}
			}
		}
	}

	@include breakpoint(large) {
		padding-bottom: 100px;

		.store-overview-map__container {
			padding-left: 40px;
			padding-right: 40px;
		}
	}
}
