.cart-footer-wrapper {
	margin-top: auto;
}

.cart-footer {
	padding: 28px 0;
	border-top: 1px solid $border-primary;
}

.cart-footer__container {
	max-width: 1440px;
	margin: 0 auto;
	padding: 0 16px;
}

.cart-footer__row {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	margin: 0 -16px;
}

.cart-footer__col {
	flex: 0 0 100%;
	max-width: 100%;
	padding: 0 16px;
}

.cart-footer__col--icons {
	margin-top: 10px;
}

.cart-footer__links {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	margin-left: -8px;
	margin-right: -8px;
}

.cart-footer__links-item {
	margin: 0 8px;
}

.cart-footer__link {
	display: flex;
	align-items: center;
	font-size: 14px;
	font-weight: 400;
	line-height: 1.72;
	text-decoration: none;
	transition: color 0.25s ease-out;

	@include hover {
		color: $brand-secondary;
	}
}

.cart-footer__link-icon {
	color: inherit;

	svg {
		display: block;
		width: 24px;
		margin-right: 8px;

		path {
			fill: currentColor;
		}
	}
}

.cart-footer__icons {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	margin-left: -12px;
	margin-right: -12px;
}

.cart-footer__icons-item {
	margin-left: 12px;
	margin-right: 12px;

	svg {
		display: block;
		width: 38px;
		height: 38px;
	}
}

@include breakpoint(medium) {
	.cart-footer__col--links {
		flex: 0 0 70%;
		max-width: 70%;
	}

	.cart-footer__col--icons {
		flex: 0 0 30%;
		max-width: 30%;
		margin-top: 0;
	}

	.cart-footer__links {
		justify-content: flex-start;
	}

	.cart-footer__icons {
		justify-content: flex-end;
	}
}

@include breakpoint(large) {
	.cart-footer__container {
		padding: 0 48px;
	}
}
