.services-banner {
	margin-bottom: 40px;
}

.services-banner__breadcrumbs {
	padding-top: 5px;
	padding-bottom: 15px;
}

.services-banner__video {
	margin-bottom: 16px;
}

.services-banner__video-wrapper {
	position: relative;
	overflow: hidden;
	display: block;
	padding-top: 68%;
}

.services-banner__video-banner {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	-o-object-fit: cover;
	object-fit: cover;
	-o-object-position: 50% 50%;
	object-position: 50% 50%;
}

.services-banner__content {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.services-banner__title {
	margin-bottom: 14px;
	font-size: 26px;
	line-height: 32px;
	letter-spacing: 0.06em;
}

.services-banner__text {
	margin-bottom: 16px;
	font-size: 14px;
	line-height: 20px;
	color: $brand-secondary;
}

.services-banner__button {
	width: 100%;
	border: 1px solid $black;
	max-width: 335px;
}

@include breakpoint(small) {
	.services-banner__text {
		margin-bottom: 26px;
		text-align: center;
		max-width: 515px;
	}
}

@include breakpoint(medium) {
	.services-banner__video {
		position: relative;
		margin-bottom: 50px;
	}

	.services-banner__breadcrumbs {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;

		span {
			color: $white;
		}
	}

	.services-banner {
		margin-bottom: 66px;
	}

	.services-banner__title {
		font-size: 30px;
		line-height: 35px;
		letter-spacing: 0.05em;
		text-align: center;
	}

	.services-banner__button {
		max-width: 268px;
	}

	.services-banner__video-wrapper {
		padding-top: 43%;
	}
}
