.help-center-content__container {
	.table-wrapper {
		padding-bottom: 20px;
		overflow-x: auto;

		&::-webkit-scrollbar {
			width: 2px;
			height: 3px;
		}

		&::-webkit-scrollbar-track {
			width: 3px;
			background-color: $text-field-border;
		}

		&::-webkit-scrollbar-thumb {
			width: 3px;
			background-color: $black;
		}

		+ h2 {
			padding-top: 40px;
		}
	}

	h1 {
		padding-bottom: 32px;
		font-size: 26px;
		line-height: 1.26;
		letter-spacing: 0.06em;
		text-transform: uppercase;
	}

	h2 {
		padding-bottom: 10px;
		font-size: 20px;
		line-height: 1.1;
		letter-spacing: 0.07em;
		text-transform: uppercase;

		+ h5 {
			padding-top: 18px;
		}
	}

	h3 {
		padding-bottom: 10px;
		font-size: 14px;
		font-weight: 500;
		line-height: 1.43;
		letter-spacing: 0.085em;
		text-transform: none;
		font-style: normal;
		max-width: 303px;
	}

	h5 {
		padding-bottom: 10px;
		font-size: 16px;
		line-height: 1.37;
		letter-spacing: 0.08em;
		text-transform: uppercase;
	}

	a {
		text-decoration: none;
	}

	a[download] {
		display: flex;
		align-items: center;

		&::before {
			content: "";
			display: inline-block;
			width: 30px;
			height: 30px;
			padding-right: 10px;
			background-image: url("data:image/svg+xml;base64,");
			background-repeat: no-repeat;
			background-size: 30px 30px;
		}
	}

	.accordion__item-toggler {
		padding-top: 14px;
		padding-bottom: 4px;
		font-size: 14px;
		font-weight: 500;
		line-height: 1.43;
		letter-spacing: 0.08em;
		text-transform: none;
		font-style: normal;
		cursor: pointer;
	}

	ul {
		list-style: disc;
		padding-inline-start: 24px;
		color: $brand-secondary;

		+ h2 {
			padding-top: 40px;
		}

		+ h3 {
			padding-top: 32px;
		}

		a {
			color: $brand-secondary;
			transition: all 0.25s ease-out;

			@include hover {
				color: $black;
				cursor: pointer;
			}
		}

		li {
			@include hover {
				width: max-content;
				color: $black;
				cursor: default;
			}
		}
	}

	ol {
		list-style: auto;
		padding-inline-start: 20px;
		color: $brand-secondary;

		+ h2 {
			padding-top: 40px;
		}

		+ h3 {
			padding-top: 32px;
		}

		a {
			color: $brand-secondary;
			transition: all 0.25s ease-out;

			@include hover {
				color: $black;
				cursor: pointer;
			}
		}

		li {
			@include hover {
				width: max-content;
				color: $black;
				cursor: default;
			}
		}
	}

	.accordion__item-toggler::before,
	.accordion__item-toggler::after {
		right: 10px;
		width: 12px;
	}

	p {
		font-size: 14px;
		font-weight: normal;
		line-height: 1.43;
		letter-spacing: 0;
		color: $brand-secondary;
		font-style: normal;

		+ h1 {
			padding-top: 40px;
		}

		+ h2 {
			padding-top: 40px;
		}

		+ h3 {
			padding-top: 32px;
		}

		+ h5 {
			padding-top: 24px;
		}

		b,
		strong {
			color: $black;
		}

		+ .table-wrapper {
			padding-top: 16px;
		}
	}

	table {
		padding: 0;
		border: 1px solid $border-primary;

		tr {
			border-bottom: 1px solid $border-primary;
		}

		td {
			font-size: 12px;
			font-weight: normal;
			line-height: 1.5;
			color: $brand-secondary;
			font-style: normal;
		}

		b,
		strong {
			font-weight: 500;
			letter-spacing: 0.06em;
			text-transform: uppercase;
			color: $black;
		}

		+ h2 {
			padding-top: 40px;
		}

		+ p {
			margin-top: 16px;
		}
	}
}

.contact-us {
	h1 {
		padding-bottom: 10px;
	}
}

.help-center-content__question {
	+ h2 {
		margin-top: 40px;
	}
}

.help-center-content__question-text {
	padding-top: 4px;
	padding-bottom: 16px;
}

.help-center-menu__list--desktop {
	display: none;
}

.help-center__col--menu {
	.help-center-contact-info {
		display: none;
	}
}

.help-center__col--content {
	width: 100%;

	.help-center-contact-info {
		display: flex;
		justify-content: center;
	}
}

.help-center {
	padding-bottom: 60px;
}

.help-center-content {
	padding-bottom: 40px;
}

.help-center-menu {
	position: relative;
	padding-top: 17px;
	padding-bottom: 32px;
}

.help-center-menu__active-link-container {
	position: relative;
	display: flex;
	padding-top: 14px;
	padding-bottom: 14px;
	padding-left: 12px;
	border: 1px solid $border-primary;
	flex-direction: row;
}

.help-center-menu__active-link-container.help-center-menu__active-link-container--open
.help-center-menu__active-link-arrow {
	transform: translateY(-50%) rotate(180deg);
}

.help-center-menu__list--mobile {
	position: absolute;
	z-index: 10;
	display: none;
	width: 100%;
	background-color: white;
	overflow-y: scroll;
	max-height: 200px;
	border-left: 1px solid $border-primary;
	border-bottom: 1px solid $border-primary;
	border-right: 1px solid $border-primary;

	&::-webkit-scrollbar {
		width: 4px;
	}

	&::-webkit-scrollbar-thumb {
		background-color: $text-field-border;
	}
}

.help-center-menu__list--mobile--open {
	display: flex;
	flex-direction: column;

	a {
		text-decoration: none;
	}
}

.help-center-menu__list-link {
	display: inline-block;
	width: 100%;
	padding-top: 14px;
	padding-bottom: 14px;
	padding-left: 12px;
}

.help-center-menu__active-link {
	width: calc(100% - 25px);
	font-size: 16px;
	font-weight: normal;
	line-height: 18px;
	text-transform: capitalize;
	font-style: normal;
}

.help-center-menu__active-link-arrow {
	position: absolute;
	top: 50%;
	right: 14px;
	display: block;
	width: 10px;
	height: 5px;
	background-image: url("data:image/svg+xml,%3Csvg width='11' height='5' viewBox='0 0 11 5' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.736026 0.109303C0.563525 -0.0403333 0.288591 -0.0360708 0.121945 0.118822C-0.0447016 0.273715 -0.0399551 0.520584 0.132546 0.67022L4.76694 4.69031C4.9353 4.83635 5.20227 4.8363 5.37056 4.69019L10.0009 0.670102C10.1734 0.520399 10.178 0.273528 10.0113 0.1187C9.84454 -0.036128 9.5696 -0.0402827 9.39717 0.10942L5.06855 3.86753L0.736026 0.109303Z' fill='black'/%3E%3C/svg%3E%0A");
	background-repeat: no-repeat;
	background-size: 10px 5px;
	transform: translateY(-50%);
	transition: transform 0.25s ease-out;
}

.help-center-content__question-container {
	display: flex;
	flex-direction: column;
}

.help-center-content__question-container:not(.active) {
	border-bottom: 1px solid $border-primary;
	margin-bottom: -1px;
}

.help-center-contact-info__container {
	width: 100%;
	margin-bottom: -1px;
	margin-left: -1px;
	margin-right: -1px;
	padding-top: 14px;
	padding-bottom: 7px;
	padding-left: 14px;
	padding-right: 14px;
	border: 1px solid $border-primary;
	max-width: 335px;
}

.help-center-contact-info__item {
	display: flex;
	flex-direction: row;
	align-items: center;
	padding-top: 7px;
	padding-bottom: 6px;
	font-size: 12px;
	font-weight: normal;
	line-height: 1.5;
	text-decoration: none;
	font-style: normal;
}

.help-center-contact-info__svg-container {
	display: flex;
	align-items: center;
	padding-right: 8px;
	flex-direction: row;

	svg.icon {
		width: 16px;
	}
}

.help-center-contact-info__item-link {
	display: flex;
	font-size: 12px;
	font-weight: normal;
	line-height: 1.5;
	text-decoration: none;
	font-style: normal;
	flex-direction: row;
}

.help-center-contact-info__title {
	padding-bottom: 3px;
	font-family: $font-avenir-next;
	font-size: 14px;
	font-weight: 500;
	line-height: 1.43;
	letter-spacing: 0;
	text-transform: inherit;
	font-style: normal;
}

@include breakpoint(medium) {
	.help-center-content__container {
		h3 {
			max-width: 528px;
		}
	}
}

@include breakpoint(large) {
	.help-center {
		padding-top: 40px;
		padding-bottom: 100px;
	}

	.help-center-content__container {
		max-width: 560px;

		h1 {
			padding-bottom: 37px;
			font-size: 30px;
			line-height: 1.17;
			letter-spacing: 0.05em;
		}

		h2 {
			padding-bottom: 8px;
			font-size: 24px;
			line-height: 1.17;
			letter-spacing: 0.08em;
		}

		p {
			+ h3 {
				padding-top: 24px;
			}
		}
	}

	.contact-us {
		h1 {
			padding-bottom: 7px;
		}
	}

	.help-center__col--content {
		width: 100%;
		max-width: calc(100% - 343px);
		margin-left: auto;

		.help-center-contact-info {
			display: none;
		}
	}

	.help-center__col--menu {
		max-width: 217px;
		padding-top: 2px;

		.help-center-contact-info {
			display: flex;
			flex-direction: row;
			justify-content: center;
		}
	}

	.help-center-menu__list--desktop {
		display: block;
	}

	.help-center-menu__active-link-container {
		display: none;
	}

	.help-center__row {
		display: flex;
		flex-wrap: wrap;
	}

	.help-center__container {
		padding-left: 97px;
		padding-right: 97px;
	}

	.help-center-menu {
		padding-top: 0;
		padding-bottom: 24px;
	}

	.help-center-menu__list-item {
		border-left: 2px solid transparent;
	}

	.help-center-menu__list-link {
		padding-top: 8px;
		padding-bottom: 8px;
		padding-left: 12px;
		font-size: 14px;
		font-weight: normal;
		line-height: 1.43;
		text-decoration: none;
		color: $brand-secondary;
		font-style: normal;
	}

	.help-center-menu__list-item--active {
		border-left: 2px solid $black;

		.help-center-menu__list-link {
			font-weight: 600;
			letter-spacing: -0.03em;
			color: $black;
		}
	}

	.help-center-contact-info__svg-container {
		svg.icon {
			width: 16px;
		}
	}

	.help-center-contact-info__title {
		font-size: 14px;
		font-weight: 500;
		line-height: 1.43;
		text-transform: inherit;
		font-style: normal;
	}

	.help-center-contact-info__container {
		margin-top: -1px;
		min-width: 217px;
		max-width: 217px;
	}
}
