.collection-header {
	padding-top: 20px;
	padding-bottom: 12px;
}

.collection-header__breadcrumbs {
	margin-bottom: 25px;

	.breadcrumbs__container {
		max-width: 100%;
		padding-left: 0;
		padding-right: 0;
	}
}

.collection-header__title {
	text-align: center;
}

@include breakpoint(large) {
	.collection-header {
		padding-top: 7px;
		padding-bottom: 37px;
	}
}
