.footer {
	display: block;
	padding-top: 36px;
	padding-bottom: 30px;
	border-top: 1px solid $border-primary;
}

.footer__container {
	padding: 0 34px;
}

.footer__logo {
	display: inline-block;
	margin-bottom: 29px;
}

.footer__logo--svg svg {
	display: block;
	width: 91px;
}

.footer__logo-image {
	display: block;
	width: 91px;
}

.footer__column {
	&.footer__column--main {
		display: flex;
		flex: 0 0 100%;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		max-width: 100%;
	}
	&.footer__column--menu {
		flex: 0 0 100%;
		max-width: 100%;
	}
}

.footer__contacts-title {
	margin-bottom: 6px;
	font-size: 12px;
	line-height: 1.5;
}

.footer__contacts-content {
	font-size: 11px;
	line-height: 1.45;
	color: $paragraph-secondary;
	p {
		font-size: inherit;
		line-height: inherit;
		color: inherit;
	}
	a {
		font-size: inherit;
		line-height: inherit;
	}
	span {
		font-size: inherit;
		line-height: inherit;
	}
}

.footer__bottom {
	margin-top: 20px;
}

.footer__copywrite {
	font-size: 12px;
	line-height: 1.5;
	color: $paragraph-secondary;
	p {
		color: inherit;
	}
}

.footer__bottom-row {
	justify-content: space-between;
	align-items: center;
}

.footer__bottom-column {
	&.footer__bottom-column--copywrite {
		flex: 0 0 100%;
		max-width: 100%;
		padding-right: 25px;
		order: 2;
	}
	&.footer__bottom-column--payments {
		flex: 0 0 100%;
		max-width: 100%;
		order: 1;
	}
}

@include breakpoint(medium) {
	.footer__logo {
		margin-bottom: 27px;
	}
	.footer__contacts {
		width: 100%;
	}
}

@include breakpoint(large) {
	.footer {
		display: block;
		padding-top: 28px;
		padding-bottom: 30px;
		border-top: 1px solid $border-primary;
	}
	.footer__container {
		padding: 0 80px;
	}
	.footer__logo {
		margin-bottom: 14px;
	}
	.footer__column {
		&.footer__column--main {
			flex: 0 0 43.4%;
			justify-content: flex-start;
			align-items: flex-start;
			max-width: 43.4%;
			padding-right: 239px;
		}
		&.footer__column--menu {
			flex: 0 0 56.6%;
			max-width: 56.6%;
		}
	}
	.footer__contacts-title {
		margin-bottom: 12px;
		font-size: 13px;
		line-height: 1.25;
	}
	.footer__contacts-content {
		font-size: 12px;
		line-height: 1.5;
		color: $paragraph-secondary;
		p {
			font-size: inherit;
			line-height: inherit;
			color: inherit;
		}
		a {
			font-size: inherit;
			line-height: inherit;
		}
	}
	.footer__bottom {
		margin-top: 24px;
	}
	.footer__copywrite {
		font-size: 12px;
		line-height: 1.5;
		color: $paragraph-secondary;
		p {
			color: inherit;
		}
	}
	.footer__bottom-row {
		justify-content: space-between;
		align-items: center;
	}
	.footer__bottom-column {
		&.footer__bottom-column--copywrite {
			flex: 0 0 43.4%;
			max-width: 43.4%;
			padding-right: 25px;
			order: 1;
		}
		&.footer__bottom-column--payments {
			display: flex;
			flex: 0 0 56.6%;
			justify-content: flex-end;
			max-width: 56.6%;
			order: 2;
		}
	}
}

@include breakpoint(extra-large) {
	.footer__bottom-column {
		&.footer__bottom-column--payments {
			display: flex;
			flex: 0 0 calc(35% + 10px);
			justify-content: flex-end;
			max-width: calc(35% + 10px);
			padding-right: 20px;
			order: 2;
		}
	}
}

@include breakpoint(wide) {
	.footer__container {
		padding: 0 80px;
		max-width: 2030px;
	}
	.footer__column {
		&.footer__column--main {
			flex: 0 0 39.3%;
			max-width: 39.3%;
			padding-right: 415px;
		}
		&.footer__column--menu {
			flex: 0 0 60.7%;
			max-width: 60.7%;
		}
	}
}
