.account-content__container {
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.account-information {
	.account-content__header-icon {
		width: 37px;
		height: 34px;

		svg {
			width: 100%;
			height: 100%;
		}
	}

	.account-content__header-wrapper {
		margin-top: 12px;
		margin-bottom: 25px;
	}

	.checkbox + label::before,
	input[type="checkbox"] + label::before {
		width: 18px;
		height: 18px;
	}
}

.account-information__input-container {
	height: 50px;
}

.account-information__change-password {
	margin-top: 24px;

	form {
		display: flex;
		flex-direction: column;
		max-width: 335px;
		width: 100%;
	}
}

.account-information__change-password-item {
	display: none;
	margin-top: 20px;
}

.account-information__change-password-button {
	max-width: 274px;
	margin-top: 20px;
}

.account-information__change-password-label {
	width: max-content;
}

#change-password:checked ~ .account-information__change-password-item {
	display: flex;
	flex-direction: row;
	justify-content: center;
}

.account-information__submit-button {
	width: 100%;
	margin-top: 18px;
	max-width: 335px;
	margin-left: auto;
	margin-right: auto;
}

.account-information__error-message {
	padding-top: 12px;
	padding-bottom: 12px;
	padding-left: 18px;
	padding-right: 18px;
	border: 1px solid $black;
	background-color: #ffd1cb;
}

.account-information__resset-password-button {
	max-width: 335px;
}

.account-information__change-password--success {
	display: none;
	justify-content: center;
	width: 100%;
	margin-top: 20px;
	margin-left: auto;
	margin-right: auto;
	border: 1px solid $black;
	background-color: $black;
	color: $white;
	max-width: 335px;
	flex-direction: row;
}

.account-information__change-password--error {
	display: none;
	width: 100%;
	margin-top: 20px;
	margin-left: auto;
	margin-right: auto;
	border: 2px solid $state-error;
	background-color: #ffd9d4;
	color: $black;
	max-width: 274px;
	max-width: 335px;
}

.account-information__change-password-text {
	width: 100%;
	padding-top: 18px;
	padding-bottom: 16px;
	padding-left: 32px;
	padding-right: 32px;
	font-family: $font-avenir-next-condensed-light;
	font-size: 15px;
	font-weight: 500;
	letter-spacing: 0.06em;
	text-align: center;
	font-style: normal;
}

.account-information__change-password-text--success {
	background-image: url("data:image/svg+xml,%3Csvg width='20' height='14' viewBox='0 0 20 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.7741 0.215183C18.0694 -0.0752108 18.5443 -0.0711761 18.8347 0.224195C19.1251 0.519566 19.1211 0.994423 18.8257 1.28482L6.53525 13.3682C6.2435 13.655 5.7757 13.6551 5.48384 13.3683L0.224271 8.20122C-0.0712078 7.91093 -0.0754179 7.43608 0.214867 7.1406C0.505152 6.84512 0.980007 6.84091 1.27549 7.1312L6.00925 11.7818L17.7741 0.215183Z' fill='%2300BC37'/%3E%3C/svg%3E%0A");
	background-repeat: no-repeat;
	background-position: calc(100% - 11px) 50%;
	color: $white;
}

.account-information__input {
	height: 50px;
}

.account-information__row {
	flex-direction: column;
}

.account-information__col {
	+ .account-information__col {
		margin-top: 14px;
	}
}

.account-information__select-container {
	.select2-selection--single {
		height: 50px;
	}
}

@include breakpoint(medium) {
	.account-information {
		.account-content__header {
			margin-bottom: 5px;
		}
	}

	.account-content__header-title {
		margin-right: 18px;
	}

	.account-information__content {
		max-width: 536px;
	}

	.account-information__submit-button {
		max-width: 274px;
		margin-left: unset;
		margin-right: unset;
	}

	.account-information__row {
		flex-direction: row;
		margin-left: -12px;
		margin-right: -12px;
	}

	.account-information__col {
		padding-left: 12px;
		padding-right: 12px;

		+ .account-information__col {
			margin-top: 0;
		}
	}

	.account-information__resset-password-button {
		max-width: 274px;
	}

	.account-information__change-password--success {
		max-width: 274px;
		margin-left: unset;
		margin-right: unset;
	}

	.account-information__change-password--error {
		max-width: 274px;
		margin-left: unset;
		margin-right: unset;
	}

	#change-password:checked ~ .account-information__change-password-item {
		justify-content: unset;
	}
}

.account-select__stores {
	.select2-container .select2-selection.select2-selection--single {
		font-size: 14px;
		color: #808080;
	}
}
