.about-us-blocks__wrapper {
	display: flex;
	flex-wrap: wrap;
	padding: 35px 0 12px;
}

.about-us-blocks__wrapper-left {
	flex-basis: 100%;
	max-width: 100%;
	border-bottom: 1px solid $brand-secondary;
	margin-bottom: 40px;
}

.about-us-blocks__wrapper-left-inner {
	position: relative;
	width: 100%;
	height: 100%;
}

.about-us_text_with_icon_wrapper {
	position: relative;
}

.about-us-blocks__text-with-icon {
	margin-top: 25px;
}

.about-us-blocks__text-with-icon-text {
	margin-bottom: 46px;

	p {
		font-family: $font-avenir-next-condensed-medium;
		font-size: 12px;
		font-weight: 500;
		line-height: 18px;
		letter-spacing: 0.06em;
		text-align: center;
		text-transform: uppercase;
	}
}

.about-us-blocks__text-with-icon-icon {
	max-width: 149px;
	margin: 0 auto;
}

.about-us-blocks__title {
	font-family: $font-avenir-next-condensed-medium;
	font-size: 24px;
	font-weight: 500;
	line-height: 50px;
	letter-spacing: 0.12em;
	text-transform: uppercase;
}

.about-us-blocks__image-wrapper {
	margin-bottom: 47px;
}

.about-us-brands__image {
	width: 100%;
}

.about-us-blocks__text {
	margin-bottom: 34px;
	max-width: 100%;

	p {
		font-family: $font-avenir-next;
		font-size: 20px;
		font-weight: 500;
		line-height: 34px;
		color: #4f4f4f;
	}
}

.about-us-blocks__wrapper-right {
	flex-basis: 100%;
	max-width: 100%;
}

.about-us-blocks__link {
	display: block;
	min-width: 100%;
	padding: 19px 16px 17px;
	background-color: $black;
	font-size: 14px;
	font-weight: 500;
	line-height: 16px;
	text-align: center;
	text-transform: uppercase;
	text-decoration: none;
	color: $white;
}

@include breakpoint(large) {
	.about-us-blocks__wrapper {
		padding: 73px 0 47px;
	}

	.about-us-blocks__wrapper-left {
		flex-basis: 31.4%;
		max-width: 31.4%;
		margin-bottom: 0;
		border: none;
	}

	.about-us-blocks__wrapper-left-inner {
		width: 261px;
	}

	.about-us-blocks__title {
		font-size: 16px;
	}

	.about-us-blocks__wrapper-right {
		flex-basis: 68.6%;
		max-width: 68.6%;
	}

	.about-us-blocks__text {
		margin-bottom: 47px;
		max-width: 86%;

		p {
			font-size: 33px;
			line-height: 55px;
		}
	}

	.about-us-blocks__image-wrapper {
		margin-bottom: 73px;
	}

	.about-us-blocks__text-with-icon {
		position: absolute;
		top: 55.5%;
		left: 0;
		margin-top: 0;
		transform: translateY(-50%);
	}

	.about-us-blocks__text-with-icon-icon {
		margin: 0;
	}

	.about-us-blocks__text-with-icon-text {
		p {
			text-align: left;
		}
	}

	.about-us-blocks__link {
		max-width: 263px;
	}
}

@include breakpoint(medium) {
	.about-us-blocks__link {
		min-width: 263px;
	}
}
