.cart-banner__wrapper {
	position: relative;
	display: flex;
	min-height: 136px;
	padding: 20px 0;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		opacity: 0.5;
		width: 100%;
		height: 100%;
		background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, $black 93.75%);
	}
}

.cart-banner__image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.cart-banner__content-wrapper {
	position: relative;
	z-index: 1;
	align-self: flex-end;
	padding: 0 20px;
}

.cart-banner__title {
	font-size: 24px;
	line-height: 1.25;
	letter-spacing: 0.06em;
	color: $white;
}

.cart-banner__button {
	min-width: 140px;
	margin-top: 16px;
	border-color: $white;
	color: $white;

	@include hover {
		background-color: $white;
		color: $brand-primary;
	}
}

.cart-banner__link-wrapper {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
	width: 100%;
	height: 100%;
}

@include breakpoint(small) {
	.cart-banner__wrapper {
		min-height: 320px;
	}
}

@include breakpoint(medium) {
	.cart-banner__title {
		font-size: 28px;
		line-height: 1.28;
		letter-spacing: 0.07em;
	}
}
