#fs-detail-products {
	display: flex;
	padding-left: 16px;
	padding-right: 16px;
}

.fs-detail-nav-bar-arrows {
	position: relative;
}

.fs-detail-nav-bar-close {
	position: relative;
}

.fs-shopify-add-cart.fs-shopify-add-cart-container {
	margin-top: 18px;
	margin-bottom: 57px;
	padding: 16px 19px;
	min-width: 100px;
	border: 1px solid $black !important;
	background-color: $white;
	font-family: $font-avenir-next-condensed-medium;
	font-size: 14px;
	letter-spacing: 0.06em;
	color: $black;
	white-space: unset;
	max-width: unset;
}

.fs-product-name {
	font-size: 14px;
	text-transform: capitalize;
}

a.fs-share {
	font-size: 22px;
	color: $black !important;
}

.fs-post-info {
	display: none;
}

.fs-detail-product-container {
	padding-bottom: 0;
	padding-left: 0;
}

.fs-shopify-options {
	margin-bottom: 0;
	margin-left: 0;

	& > a {
		margin-top: 22px !important;
		margin-bottom: 61px !important;
		padding: 16px 19px !important;
		border: 1px solid $black !important;
		background-color: $white !important;
		font-family: $font-avenir-next-condensed-medium;
		font-size: 14px !important;
		line-height: 1;
		letter-spacing: 0.06em;
		text-decoration: none;
		color: $black;
		white-space: unset;
		transition: all 0.25s;
		min-width: 160px;
		max-width: unset;

		@include hover {
			background-color: #444 !important;
			color: #eee !important;
		}
	}
}

@include breakpoint(extra-large) {
	.fs-detail-nav-bar-close {
		position: absolute;
		top: -19px;
		right: -62px;
		svg {
			path {
				stroke: $white;
			}
		}
	}
}
