.wishlist__empty-message {
	//
}

.wishlist__row {
	display: flex;
	flex-wrap: wrap;
	margin-top: -60px;
	margin-left: -15px;
	margin-right: -15px;
}

.wishlist__col {
	flex: 0 0 50%;
	max-width: 50%;
	margin-top: 60px;
	padding-left: 15px;
	padding-right: 15px;
	text-align: center;
}

.wishlist__actions {
	margin-top: 12px;
}

.wishlist__actions-button {
	+ .wishlist__actions-button {
		margin-left: 20px;
	}
}

.wishlist__load-more {
	margin-top: 60px;
}

.wishlist__load-more-button {
	min-width: 240px;
	padding: 18px 20px;
}

@include breakpoint(medium) {
	.wishlist__row {
		margin-left: -20px;
		margin-right: -20px;
	}

	.wishlist__col {
		flex: 0 0 33.33%;
		max-width: 33.33%;
		padding-left: 20px;
		padding-right: 20px;
	}

	.wishlist__load-more {
		text-align: center;
	}
}
