.milestones {
	margin: 24px 0 11px;
}

.milestones__title {
	margin-bottom: 24px;
	text-align: center;
}

.milestones__slide-wrapper {
	width: 50%;
	height: 100%;
	margin: 0;
	padding: 31px 32px 37px;
	border: 1px solid #d9d9d9;
	min-height: 176px;
	flex-direction: column;
}

.milestones__slider-container {
	position: relative;
	border-bottom: 1px solid $black;
}

.milestones__slide-text {
	overflow: hidden;
	display: -webkit-box;
	-webkit-line-clamp: 5;
	-webkit-box-orient: vertical;
	text-overflow: ellipsis;
	text-align: left;
	min-height: 100px;
	max-height: 100px;
}

.milestones__slider-arrow {
	display: block;
	background-color: $black;
	color: $white;
}

.milestones__slider-button-link {
	display: block;
	margin: 20px auto 10px;
	padding: 19px 16px 17px;
	border: 1px solid $black;
	background-color: $white;
	font-family: $font-avenir-next-condensed-medium;
	font-size: 14px;
	font-weight: 500;
	line-height: 16px;
	letter-spacing: 0.06em;
	text-align: center;
	text-transform: uppercase;
	text-decoration: none;
	max-width: 263px;
	color: $black;
}

@include breakpoint(medium) {
	.milestones {
		margin: 36px 0 63px;
	}

	.milestones__slide-wrapper {
		width: 25%;
	}
	.milestone__slider-container {
		border-bottom: 1px solid $black;
	}
}

@include breakpoint(large) {
	.milestones__slide-wrapper {
		width: 20%;
	}
}

@include breakpoint(extra-large) {
	.milestones__slide-wrapper {
		width: 15%;
	}
}
