.services-list__slider-slide {
	width: 66.67%;
	height: auto;
}

.services-list__item {
	display: flex;
	flex-direction: column;
	height: 100%;
	text-align: center;
}

.services-list__item-title {
	margin-bottom: 10px;
	font-size: 26px;
	line-height: 1.23;
	letter-spacing: 0.06em;
	color: $paragraph-primary;
}

.services-list__item-image-wrapper {
	position: relative;
	overflow: hidden;
	width: 100%;
	margin-top: auto;
	padding-top: 124.88%;
}

.services-list__item-image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

@include breakpoint(medium) {
	.services-list__item-title {
		margin-bottom: 16px;
		font-size: 30px;
		line-height: 1.67;
		letter-spacing: 0.05em;
	}

	.services-list__item-image-wrapper {
		padding-top: 126.3%;
	}
}

@include breakpoint(extra-large) {
	.services-list__slider-wrapper {
		width: calc(100% - 52px);
	}

	.services-list__slider-slide {
		width: 33.33%;
		margin-right: 26px;
	}
}
