.denim-banner {
	position: relative;
	max-height: 590px;
}

.denim-banner__breadcrumbs {
	position: relative;
}

.breadcrumbs--subcolor a,
.breadcrumbs--subcolor span {
	font-size: 12px;
	line-height: 1.5;
	color: $brand-primary;
	transition: color 0.25s ease-out;
}

.denim-banner__breadcrumbs .breadcrumbs--subcolor a {
	color: $brand-secondary;

	@include hover {
		color: $brand-primary;
	}
}

.denim-banner__img {
	max-height: 590px;
	width: 100%;
	height: auto;
	object-fit: cover;
}

.denim-banner__img-wrapper {
	position: relative;
}

.denim-banner-head {
	position: absolute;
	top: 47%;
	left: 50%;
	transform: translate(-50%, -47%);
}

.denim-banner__title {
	display: flex;
	align-items: center;
	font-family: $font-avenir-next-condensed-medium;
	font-size: 26px;
	font-style: normal;
	font-weight: 800;
	line-height: 34px;
	letter-spacing: 0.04em;
	text-align: center;
	text-transform: uppercase;
	text-shadow: 0 4px 16px rgba(0, 0, 0, 0.17);
	color: $white;
}

.denim-banner__subtitle {
	display: flex;
	justify-content: center;
	align-items: center;
	font-family: $font-avenir-next-cyr;
	font-size: 14px;
	font-style: normal;
	font-weight: 500;
	line-height: 20px;
	color: $white;
}

@include breakpoint(large) {
	.denim-banner__breadcrumbs {
		position: absolute;
	}

	.breadcrumbs--subcolor a,
	.breadcrumbs--subcolor span {
		color: $white;
	}

	.denim-banner__breadcrumbs .breadcrumbs--subcolor a {
		color: $white;

		@include hover {
			color: white;
		}
	}

	.denim-banner__title {
		margin-bottom: 12px;
		font-size: 36px;
		line-height: 40px;
	}
}
