.footer-content-blocks {
	border-top: 1px solid $border-primary;
}

.footer-content-blocks__row {
	justify-content: center;
}

.footer-content-blocks__column {
	flex: 0 0 100%;
	max-width: 100%;

	border-bottom: 1px solid $border-primary;

	&:last-child {
		border-bottom: none;
	}
}

.footer-content-blocks__block {
	display: flex;
	flex-direction: column;
	align-items: center;

	padding-top: 46px;
	padding-bottom: 46px;

	text-align: center;
}

.footer-content-blocks__image-wrapper {
	max-height: 80px;
	margin-bottom: 36px;
}

.footer-content-blocks__image {
	display: block;
	width: 100%;
	max-width: 100%;
	max-height: 100%;
}

.footer-content-blocks__title {
	margin-bottom: 10px;

	font-size: 20px;
	line-height: 1;
	letter-spacing: 0;
}

.footer-content-blocks__content {
	font-size: 14px;
	line-height: 1.4;
	color: $paragraph-secondary;

	p {
		color: inherit;
	}
}

@include breakpoint(small) {
	.footer-content-blocks__block {
		padding-bottom: 43px;
		padding-left: 25px;
		padding-right: 25px;
	}
}

@include breakpoint(large) {
	.footer-content-blocks__container {
		padding: 0 80px;
	}

	.footer-content-blocks__row {
		margin: 0 -80px;
	}

	.footer-content-blocks__column {
		flex: 0 0 33.33%;
		max-width: 33.33%;

		border-right: 1px solid $border-primary;
		border-bottom: none;

		&:last-child {
			border-right: none;
		}
	}

	.footer-content-blocks__block {
		padding-top: 46px;
		padding-bottom: 36px;
		padding-left: 40px;
		padding-right: 40px;
	}

	.footer-content-blocks__image-wrapper {
		margin-bottom: 35px;
	}

	.footer-content-blocks__title {
		font-size: 16px;
		line-height: 1.35;
	}
}

@include breakpoint(extra-large) {
	.footer-content-blocks__block {
		padding-top: 56px;
		padding-bottom: 58px;
		padding-left: 80px;
		padding-right: 80px;
	}
}

@include breakpoint(wide) {
	.footer-content-blocks__container {
		padding: 0 220px;
		max-width: 2310px;
	}

	.footer-content-blocks__row {
		margin: 0 -220px;
	}

	.footer-content-blocks__block {
		padding-top: 56px;
		padding-bottom: 58px;
		padding-left: 207px;
		padding-right: 207px;
	}
}
