.collection-subcategories {
	margin-bottom: 0;
}

.collection-subcategories__title {
	margin-bottom: 14px;
	font-size: 18px;
	line-height: 24px;
	text-align: center;
	text-transform: uppercase;
}

.collection-subcategories__slide {
	width: 47.75%;
	margin-right: 5px;
}

.collection-subcategories__card {
	position: relative;

	&::after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0;
		width: 100%;
		height: 100%;
		border: 1px solid $border-primary;
		transition: opacity 0.25s ease-out;
		box-sizing: border-box;
	}

	@include hover {
		&::after {
			opacity: 1;
		}
	}
}

.collection-subcategories__card-link-wrapper {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
	width: 100%;
	height: 100%;
}

.collection-subcategories__card-image-wrapper {
	position: relative;
	overflow: hidden;
	padding-top: 133.75%;
}

.collection-subcategories__card-image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: contain;
	object-position: 50% 50%;
}

.collection-subcategories__card-description {
	margin-top: 4px;
	text-align: center;
}

.collection-subcategories__card-title {
	font-size: 18px;
	line-height: 1.33;
	letter-spacing: 0.07em;
	text-align: center;
	color: $black;
}

.collection-subcategories__card-subtitle {
	margin-top: 2px;
	font-size: 14px;
	line-height: 1.43;
	color: $paragraph-secondary;
}

@include breakpoint(medium) {
	.collection-subcategories__slider-container {
		&:not(.initialized) {
			&[data-slider-counter="less"][data-centered-slides="true"] {
				.slider__wrapper {
					overflow: hidden;
					justify-content: center;
				}
			}
		}
	}

	.collection-subcategories__slide {
		width: 22.7%;
		margin-left: 0;
		margin-right: 15px;
	}

	.collection-subcategories__slider-pagination {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 23px;
	}
}

@include breakpoint(large) {
	.collection-subcategories__slider-container {
		padding: 0;
	}

	.collection-subcategories__slide {
		width: 17.3%;
		margin-right: 15px;
	}

	.collection-subcategories__title {
		margin-bottom: 5px;
		font-size: 20px;
		line-height: 26px;
	}

	.collection-subcategories__card {
		position: relative;
		padding: 12px;
	}

	.collection-subcategories__card-image-wrapper {
		padding-top: 137%;
	}

	.collection-subcategories__card-description {
		max-width: 208px;
		margin-left: auto;
		margin-right: auto;
	}

	.collection-subcategories__slider-pagination {
		display: none;
	}
}

@include breakpoint(extra-wide) {
	.collection-subcategories__slider-container {
		padding: 0 40px;
	}

	.collection-subcategories__slide {
		width: 13.3%;
		margin-right: 0;
	}

	.collection-subcategories__card-image-wrapper {
		padding-top: 137%;
	}
}
