a.boost-pfs-filter-scroll-to-top {
	top: unset;
	bottom: 20px;
	right: 20px;
	opacity: 1 !important;
	width: 46px;
	height: 46px;
	border-radius: 50%;
	background-color: $brand-primary;
	line-height: 46px;
	transition: background-color 0.25s ease-out;

	&::before {
		color: $white;
	}

	&:hover {
		background-color: $brand-secondary;
	}
}

.boost-pfs-filter-top-sorting {
	margin-bottom: 0;
}

.boost-pfs-filter-tree-mobile-button {
	display: block;

	button {
		display: inline-block;
		margin: 0;
		padding-top: 18px;
		padding-bottom: 18px;
		padding-left: 45px;
		padding-right: 16px;
		border: 1px solid $brand-primary;
		background-color: transparent;
		background-image: url("data:image/svg+xml,%3Csvg width='18' height='15' fill='none' xmlns='http://www.w3.org/2000/svg' class='icon icon-filter'%0A%3E%3Cpath d='M12 0v5h1V3h5V2h-5V0h-1zM0 2h11v1H0V2zm0 10h7v1H0v-1zm18-5H7v1h11V7zm0 5h-8v-2H9v5h1v-2h8v-1zM0 7h5V5h1v5H5V8H0V7z' fill='%23000' /%3E%3C/svg%3E%0A");
		background-repeat: no-repeat;
		background-position: 16px 50%;
		font-family: $font-avenir-next-condensed-light;
		font-size: 14px;
		font-weight: 400;
		line-height: 1.15;
		letter-spacing: 0.06em;
		text-align: center;
		text-transform: uppercase;
		text-decoration: none;
		color: $brand-primary;
	}
}

.boost-pfs-filter-load-more {
	padding: 0;

	.boost-pfs-filter-load-more-total {
		display: none;
		margin-top: 0;
		margin-bottom: 16px;
		font-size: 13px;
		font-weight: 400;
		line-height: 1.54;
		letter-spacing: normal;
		text-transform: none;
		color: $paragraph-secondary;
	}

	.boost-pfs-filter-load-more-button {
		display: inline-block;
		min-width: 240px;
		padding: 18px 20px;
		border: 1px solid $brand-primary;
		background-color: $brand-primary;
		font-family: $font-avenir-next-condensed-light;
		font-size: 14px;
		font-weight: 400;
		line-height: 1.15;
		letter-spacing: 0.06em;
		text-align: center;
		text-transform: uppercase;
		text-decoration: none;
		color: $white;
		transition-property: border-color, background-color, box-shadow, color;
		transition-duration: 0.25s;
		transition-timing-function: ease-out;

		@include hover {
			border-color: $brand-secondary;
			background-color: $brand-secondary;
		}
	}
}

.boost-loading {
	opacity: 0;
}

@media screen and (max-width: 374px) {
	.boost-pfs-filter-load-more {
		.boost-pfs-filter-load-more-button {
			min-width: 100%;
		}
	}
}

@include breakpoint(medium) {
	a.boost-pfs-filter-scroll-to-top {
		bottom: 32px;
		right: 32px;
	}
}

@include breakpoint(large) {
	.boost-pfs-filter-tree-mobile-button {
		display: none;
	}

	a.boost-pfs-filter-scroll-to-top {
		bottom: 40px;
		right: 40px;
	}
}
