$negativeHeaderTopLinksHeight: calc(var(--header-top-links-height, 0) * -1);

/* Main Header Layout */

.header {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 21;
	display: block;
	width: 100%;
	background-color: $background-white;
	transform: translateZ(0);
	transition-property: transform;
	transition-duration: 0.25s;
}

.header__icon--wishlist {
	position: relative;
}

.header--top-links-hidden {
	transform: translateY($negativeHeaderTopLinksHeight);
}

.header--border-bottom {
	border-bottom: 1px solid $background-light-grey;
}

.header__wrapper {
	position: relative;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-top: 0;
	padding-bottom: 3px;
	background-color: $background-white;
	min-height: 64px;
	.link::after {
		display: none;
	}
}

.header__wrapper *[tabindex="-1"]:focus {
	outline: none;
}

.header__top-links {
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom: 0.5px solid $border-primary;
	.link::after {
		display: none;
	}
}

.header__top-inner {
	display: flex;
	align-items: center;
}

.header__store-links {
	width: 100%;
	@include breakpoint(small) {
		width: auto;
	}
}

.store-link {
	width: 100%;
	padding: 6px 0;
	min-width: 129px;
	font-size: 12px;
	line-height: 18px;
	letter-spacing: 0.06em;
	text-align: center;
	border-right: 0.5px solid $border-primary;
	color: #a1a1a1;
	&.is-active {
		color: $brand-primary;
		border-top: 1px solid $brand-primary;
	}
	@include breakpoint(small) {
		width: auto;
	}
}

.header__additional-links {
	display: none;
	padding-right: 32px;
	@include breakpoint(large) {
		display: block;
	}
}

.additional-link {
	padding-left: 40px;
	font-size: 14px;
	line-height: 16px;
	letter-spacing: 0.08em;
}

.header__heading {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 50%;
	right: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0;
	line-height: 0;
}

.header__heading-link.mobile-logo {
	display: block;
	@include breakpoint(large) {
		display: none;
	}
}

.header__heading-link.desktop-logo {
	display: none;
	@include breakpoint(large) {
		display: block;
	}
}

.header__heading-logo--svg {
	svg {
		display: block;
		width: var(--mobile-logo-width, 100%);
		@include breakpoint(large) {
			width: var(--desktop-logo-width, 100%);
		}
	}
}

.header__wrapper > .header__heading-link {
	line-height: 0;
}

.header__heading,
.header__heading-link {
	grid-area: heading;
	justify-self: center;
}

.header__heading-link {
	position: relative;
	left: -50%;
	display: inline-block;
	padding: 0.75rem;
	text-decoration: none;
	word-break: break-word;
}

.header__heading-link .h2 {
	line-height: 1;
	color: $brand-primary;
}

.account-icon-wrapper {
	position: relative;
	@include hover {
		.customer-popup {
			display: block;
		}
	}
}

@include breakpoint(large) {
	.header__heading-link {
		margin-left: -0.75rem;
	}
	.header__heading,
	.header__heading-link {
		justify-self: start;
	}
	.header--top-center .header__heading-link,
	.header--top-center .header__heading {
		justify-self: center;
	}
}

/* Header icons */

.header__icons {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	width: 50%;
	padding-left: 10%;
	grid-area: icons;
}

.header__icon {
	z-index: 1;
	margin-left: 22px;
	svg {
		width: 20px;
		height: 16px;
	}
}

.header__icon:first-child {
	margin-left: 0;
}

.header__icon:not(.header__icon--summary),
.header__icon span {
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
}

.header__icon span {
	height: 100%;
}

.header__icon::after {
	content: none;
}

.header__icon,
.modal__close-button {
	@include hover {
		.icon {
			transform: scale(1.07);
		}
	}
}

.header__icon .icon {
	fill: none;
	vertical-align: middle;
}

.header__icon--cart {
	position: relative;
}

.header__icon.header__icon--count {
	position: absolute;
	top: -5px;
	right: -10px;
	width: 16px;
	height: 16px;
	border-radius: 50%;
	background-color: $brand-primary;
	font-family: $font-avenir-next-condensed-light;
	font-size: 10px;
	font-weight: 600;
	color: $white;
}

.header__icon--account-mobile {
	display: block;
	@include breakpoint(large) {
		display: none;
	}
}

.header__icon.header__icon--account {
	display: none;
	@include breakpoint(large) {
		display: flex;
	}
}

@media screen and (max-width: 989px) {
	menu-drawer ~ .header__icons .header__icon--account {
		display: none;
	}
}

/* Search */

.header__icon--search {
	display: none;
}

.header__icon--search-mobile {
	display: block;
	padding-bottom: 11px;
	padding-left: 20px;
	padding-right: 20px;
}

.header__wrapper > .header__search {
	grid-area: left-icon;
	justify-self: start;
}

.header__search {
	display: inline-flex;
	width: 100%;
	margin: 0;
	line-height: 0;
	border-bottom: 1px solid #000000;
	text-align: center;
}

.mobile-search-btn {
	display: block;
}

.boost-pfs-search-suggestion-wrapper {
	max-width: 376px !important;
}

.boost-pfs-search-suggestion-mobile-overlay {
	display: none !important;
}

.boost-pfs-search-suggestion-mobile-top-panel {
	display: none !important;
}

.boost-pfs-search-suggestion-mobile.boost-pfs-search-suggestion-wrapper {
	padding-bottom: 64px;
}

/*
.boost-pfs-search-suggestion-mobile-open
.boost-pfs-search-suggestion-mobile.boost-pfs-search-suggestion-wrapper {
	top: 126px !important;
}

.boost-pfs-search-suggestion-mobile.boost-pfs-search-suggestion-wrapper.boost-pfs-search-suggestion-product-position-none {
	top: 126px !important;
}
*/

.header__sticky
.boost-pfs-search-suggestion-mobile.boost-pfs-search-suggestion-wrapper {
	top: 95px !important;
}

.boost-pfs-search-suggestion-popover {
	display: none !important;
}

.boost-pfs-search-suggestion-group {
	.boost-pfs-search-suggestion-header {
		font-family: $font-avenir-next-condensed;
		font-size: 13px;
		line-height: 16px;
		letter-spacing: 0.08em;
		text-transform: uppercase;
	}
}

.boost-pfs-search-suggestion-mobile .boost-pfs-search-suggestion-header {
	padding: 16px 5px 16px !important;
}

.boost-pfs-search-suggestion-mobile
.boost-pfs-search-suggestion-header-view-all
> a {
	font-family: $font-avenir-next-condensed;
	font-size: 14px !important;
	font-weight: 500 !important;
	line-height: 20px !important;
	color: #000000 !important;
}

.boost-pfs-search-suggestion-item {
	.boost-pfs-search-suggestion-product-title {
		padding-bottom: 23px;
		font-family: $font-avenir-next-condensed;
		font-size: 14px;
		line-height: 20px;
	}
	.boost-pfs-search-suggestion-product-price {
		font-family: $font-avenir-next-condensed;
		font-size: 11px;
		line-height: 16px;
		letter-spacing: 0.2px;
	}
}

.no_results_block-image {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0;
}

.search_form {
	display: flex;
	width: 100%;
	input {
		height: 40px;
		padding: 0;
		border: none;
		font-size: 14px;
		line-height: 16px;
		letter-spacing: 0.2px;
		flex-grow: 1;
		text-align: center;
		color: $paragraph-secondary;
	}
}

.search-btn {
	padding: 0 0 5px;
	background: transparent;
	svg {
		width: auto;
	}
}

@include breakpoint(large) {
	.header__icon--search {
		z-index: 10;
		display: block;
		width: 100%;
		max-width: 228px;
	}
	.header__icon--search-mobile {
		display: none;
	}
	.mobile-search-btn {
		display: none;
	}
	.search_form {
		input {
			text-align: left;
		}
	}
}

@include breakpoint(medium) {
	.header__icon--search-mobile {
		padding-left: 32px;
		padding-right: 32px;
	}
}

@include breakpoint(large) {
	.header__search {
		margin: 4px 22px 0 0;
	}
}

/* Header menu drawer */

.header__icon--menu .icon {
	display: block;
	opacity: 1;
	transform: scale(1);
	transition: transform 150ms ease, opacity 150ms ease;
}

.menu-drawer-container .header__icon--menu {
	display: flex;
}

details:not([open]) > .header__icon--menu .icon-close,
details[open] > .header__icon--menu .icon-hamburger {
	display: none;
	visibility: hidden;
	opacity: 0;
	transform: scale(0.8);
}

.js details[open]:not(.menu-opening) > .header__icon--menu .icon-close {
	visibility: hidden;
}

.js details[open]:not(.menu-opening) > .header__icon--menu .icon-hamburger {
	visibility: visible;
	opacity: 1;
	transform: scale(1.07);
}

.header__inline-menu details[open] > .header__submenu {
	opacity: 1;
	transform: translateY(0);
	animation: animateMenuOpen 0.25s ease;
}

/* Header menu */

.header__inline-menu {
	display: none;
	width: 50%;
	margin-left: -1.2rem;
	grid-area: navigation;
	padding-right: 10%;
	.list-menu__item {
		font-size: 14px;
		line-height: 16px;
		letter-spacing: 0.08em;
	}
}

.header--top-center .header__inline-menu,
.header--top-center .header__heading-link {
	margin-left: 0;
}

@include breakpoint(large) {
	.header__inline-menu {
		display: block;
		margin-left: 0;
	}
	.header__icon--search {
		margin-right: 22px;
	}
	.header__icon svg {
		width: 18px;
		height: auto;
	}
	.header--top-center .header__inline-menu {
		justify-self: center;
	}
	.header--top-center .header__inline-menu > .list-menu--inline {
		justify-content: center;
	}
}

.header__menu {
	padding: 0 1rem;
}

.header__menu-item {
	margin: 32px 40px 32px 0;
	text-decoration: none;
	color: $brand-primary;
	cursor: pointer;
	&.header__menu-item--active {
		border-bottom: 1px solid $brand-primary;
	}
}

.list-menu--inline li:last-child .header__menu-item {
	margin-right: 0;
}

.header__menu-item span {
	transition: text-decoration 0.25s ease;
}

.header__submenu {
	transition: opacity 0.25s ease, transform 0.25s ease;
}

.header__submenu.list-menu {
	padding: 2rem 0;
}

.header__submenu .header__submenu {
	margin: 0.5rem 0;
	padding: 0.5rem 0;
	background-color: $background-white;
}

.header__submenu .header__menu-item::after {
	right: 2rem;
}

.header__submenu .header__menu-item {
	padding: 0.95rem 3.5rem 0.95rem 2rem;
}

.header__submenu .header__submenu .header__menu-item {
	padding-left: 3rem;
}

.header__menu-item .icon-caret {
	right: 0.8rem;
}

.header__submenu .icon-caret {
	right: 2rem;
}

details-disclosure > details {
	position: relative;
}

@keyframes animateMenuOpen {
	0% {
		opacity: 0;
		transform: translateY(-1.5rem);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

.overflow-hidden-mobile,
.overflow-hidden-tablet {
	overflow: hidden;
}

@media screen and (min-width: 750px) {
	.overflow-hidden-mobile {
		overflow: auto;
	}
}

@include breakpoint(large) {
	.overflow-hidden-tablet {
		overflow: auto;
	}
}

// Header drawer
.header__mobile-items {
	justify-self: start;
}

@media screen and (min-width: 992px) {
	.header__mobile-items {
		display: none;
	}
}

.menu-drawer-container {
	display: flex;
}

.header__mobile-items-row {
	display: flex;
	margin-left: -10px;
	margin-right: -10px;
}

.header__mobile-items-col {
	margin: 0;
	padding: 10px;
}

.list-menu {
	margin: 0;
	padding: 0;
	list-style: none;
}

.list-menu--inline {
	display: inline-flex;
	flex-wrap: wrap;
}

summary.list-menu__item {
	padding-right: 2.7rem;
}

.list-menu__item {
	display: flex;
	align-items: center;
	line-height: 1.3;
}

.list-menu__item--link {
	padding-top: 1rem;
	padding-bottom: 1rem;
	line-height: 1.8;
	text-decoration: none;
}

@media screen and (min-width: 750px) {
	.list-menu__item--link {
		padding-top: 0.5rem;
		padding-bottom: 0.5rem;
	}
}
