.featured-products__container {
	margin: 0 auto;
}

.featured-products__col {
	margin-right: 20px;
}

@include breakpoint(small) {
	.featured-products__col {
		max-width: calc((100% - 20px) / 2);

		margin-right: 20px;
	}
}

@include breakpoint(extra-large) {
	.featured-products__container {
		max-width: 950px;
	}

	.featured-products__col {
		max-width: calc((100% - 60px) / 3);

		margin-right: 30px;
	}
}
