.minicart-recommendations {
	margin-top: 20px;
}

.minicart-recommendations__title {
	margin-bottom: 4px;
	font-size: 18px;
	line-height: 1.33;
	letter-spacing: 0.07em;

	&:empty {
		display: none;
	}
}

@include breakpoint(ultra-small) {
	.minicart-recommendations {
		margin-top: 40px;
	}
}

@include breakpoint(large) {
	.minicart-recommendations {
		margin-top: 70px;
	}
}
