.hero-banner {
	padding-top: 20px;
}

.hero-banner__wrapper {
	position: relative;
	display: flex;
	min-height: 228px;
	padding: 54px 0;
}

.hero-banner__image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.hero-banner__content-wrapper {
	z-index: 1;
	align-self: var(--content-position-vertical);
	max-width: 1376px;
	width: 100%;
	margin-left: 54px;
	margin-right: auto;
	padding: 0 54px 0 0;
	text-align: var(--content-position-horizontal);
}

.hero-banner__main-content {
	max-width: var(--content-width);
	margin-left: var(--content-margin-left);
	margin-right: var(--content-margin-right);

	&.hero-banner__main-content--desktop {
		display: none;
	}

	&.hero-banner__main-content--mobile {
		display: block;
		max-width: 571px;
		margin-top: 22px;
		margin-left: auto;
		margin-right: auto;
		text-align: center;

		.hero-banner__main-content-title,
		.hero-banner__main-content-subtitle {
			color: $paragraph-primary;
		}
	}
}

.hero-banner__main-content-title,
.hero-banner__main-content-subtitle {
	color: var(--content-color);
}

.hero-banner__main-content-title {
	font-family: $font-avenir-next-condensed-medium;
	font-size: 26px;
	line-height: 1.23;
	letter-spacing: 0.06em;
}

.hero-banner__main-content-subtitle {
	margin-top: 8px;
	font-size: 14px;
	line-height: 1.43;
}

.hero-banner__buttons {
	width: 100%;
	margin-left: var(--content-margin-left);
	margin-right: var(--content-margin-right);

	&.hero-banner__buttons--desktop {
		display: none;
		margin-top: 36px;
	}

	&.hero-banner__buttons--mobile {
		display: block;
		margin-top: 22px;
	}
}

.hero-banner__buttons-row {
	display: flex;
	flex-wrap: wrap;
	margin-left: -14px;
	margin-right: -14px;
}

.hero-banner__button {
	min-width: calc(100% - 28px);
	margin-left: 14px;
	margin-right: 14px;
	box-shadow: none;
	border: 1px solid $brand-primary;

	+ .hero-banner__button {
		margin-top: 16px;
	}

	@include hover {
		background-color: $brand-primary;
		color: $white;
	}
}

.hero-banner__secondary-content {
	max-width: 571px;
	margin-top: 22px;
	margin-left: auto;
	margin-right: auto;
	text-align: center;
}

.hero-banner__secondary-content-subtitle {
	max-width: 515px;
	margin-top: 8px;
	margin-left: auto;
	margin-right: auto;
	font-size: 14px;
	line-height: 1.43;
	color: $paragraph-secondary;
}

@include breakpoint(small) {
	.hero-banner__wrapper {
		min-height: 470px;
	}
}

@include breakpoint(medium) {
	.hero-banner {
		padding-top: 0;
	}

	.hero-banner__main-content {
		&.hero-banner__main-content--desktop {
			display: block;
		}

		&.hero-banner__main-content--mobile {
			display: none;
		}
	}

	.hero-banner__main-content-title {
		font-size: 36px;
		line-height: 1.1;
		letter-spacing: 0.04em;
	}

	.hero-banner__main-content-subtitle,
	.hero-banner__secondary-content-subtitle {
		margin-top: 12px;
	}

	.hero-banner__buttons {
		width: max-content;

		&.hero-banner__buttons--desktop {
			display: block;
		}

		&.hero-banner__buttons--mobile {
			display: none;
		}
	}

	.hero-banner__button {
		width: unset;
		min-width: 284px;
		border: 1px solid transparent;
		box-shadow: 0 4px 14px rgba(0, 0, 0, 0.07);

		+ .hero-banner__button {
			margin-top: 0;
		}

		@include hover {
			background-color: $white;
			box-shadow: 0 4px 14px rgba(0, 0, 0, 0.2);
			color: $brand-primary;
		}
	}

	.hero-banner__secondary-content {
		margin-top: 40px;
	}

	.hero-banner__secondary-content,
	.hero-banner__secondary-content-subtitle {
		max-width: 100%;
	}
}

@include breakpoint(extra-large) {
	.hero-banner__wrapper {
		min-height: 591px;
	}

	.hero-banner__content-wrapper {
		padding: 0 74px 0 0;
	}

	.hero-banner__secondary-content-subtitle {
		max-width: 515px;
	}
}

@include breakpoint(x-large) {
	.hero-banner__content-wrapper {
		margin: 0 auto;
	}
}

@include breakpoint(xx-large) {
	.hero-banner__content-wrapper {
	    margin-left: 200px;
	}
}



@include breakpoint(wide) {
	.hero-banner__wrapper {
		min-height: 1000px;
	}

	.hero-banner__secondary-content {
		margin-top: 70px;
	}
}
