.denim-subcategory {
	display: flex;
	width: 100%;
}

.denim-subcategory-item {
	position: relative;
	width: 49%;
	text-align: center;
}

.denim-subcategory-item__overlay {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
	width: 100%;
	height: 100%;
}

.denim-subcategory-item__title {
	font-family: $font-avenir-next-condensed;
	font-size: 18px;
	font-weight: bold;
	line-height: 24px;
	font-style: normal;
	letter-spacing: 0.07em;
	text-transform: uppercase;
	color: $black;
}

.denim-subcategory-item__text {
	margin-bottom: 20px;
	min-height: 60px;
}

.denim-subcategory-item__text p {
	font-family: $font-avenir-next-cyr;
	font-size: 14px;
	font-weight: normal;
	line-height: 20px;
	font-style: normal;
	color: #808080;
}

.denim-subcategory-item__link {
	z-index: 5;
}

.denim-subcategory-item__popup {
	display: none;
}

.denim-gallery__container {
	position: relative;
	padding: 0;
}

.how-to-cuff-arrow {
	top: 36% !important;
	margin: 0 20px;
}

.denim-gallery__slide {
	width: 50%;
	margin-right: 2px;
}

.denim-gallery__arrow {
	margin: 0;
}

.denim-gallery-item__image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.denim-gallery-item__image-wrapper {
	position: relative;
	overflow: hidden;
	margin-bottom: 8px;
	padding-top: 127%;
}

.denim-gallery-item__title {
	font-family: $font-avenir-next-condensed;
	font-size: 18px;
	font-style: normal;
	font-weight: bold;
	line-height: 24px;
	letter-spacing: 0.07em;
	text-align: center;
	text-transform: uppercase;
	color: $black;
}

.denim-gallery-item p {
	font-family: $font-avenir-next-cyr;
	font-size: 14px;
	font-style: normal;
	font-weight: normal;
	line-height: 20px;
	text-align: center;
	color: #808080;
}

.popup-gallery__slider-slide {
	width: 100%;
	margin-right: 0;
}

@include breakpoint(medium) {
	.denim-subcategory-item {
		width: 33%;
	}

	.denim-gallery__slide {
		width: 33%;
	}
}

@include breakpoint(large) {
	.denim-subcategory-item {
		width: 17.5%;
	}

	.denim-gallery__slide {
		width: 17%;
	}

	.denim-gallery-item__image-wrapper {
		padding-top: 137%;
	}
}
