.collection-filters {
	display: none;

	.boost-pfs-filter-option {
		padding: 14px 0 !important;

		&.boost-pfs-filter-option-collapsed {
			.boost-pfs-filter-option-title {
				&::after {
					background-size: 100% 1px, 1px 100%;
				}
			}
		}

		&.boost-pfs-filter-option-sustainable {
			.boost-pfs-filter-option-item-list-swatch {
				.boost-pfs-filter-option-item {
					position: relative;
					align-items: flex-start;
					padding-left: 30px;

					.boost-pfs-check-box {
						position: absolute;
						top: 0;
						left: 0;
						display: inline-block;
						width: 20px;
						height: 20px;
						border-radius: 0;
						border: 1px solid $border-primary;
						background-color: $white;

						&::before {
							content: none;
							position: absolute;
							top: 4px;
							left: 4px;
							width: 10px;
							height: 6px;
							border: 2px solid $white;
							border-top: none;
							border-right: none;
							transform: rotate(-50deg);
						}
					}

					.boost-pfs-filter-option-swatch-image {
						border: 0 !important;

						&::before {
							content: none;
						}
					}

					&.selected {
						.boost-pfs-check-box {
							border-color: $brand-primary;
							background-color: $brand-primary;

							&::before {
								content: "";
							}
						}

						.boost-pfs-filter-option-swatch-image {
							border: 0 !important;
						}
					}
				}
			}
		}
	}

	.boost-pfs-filter-option-title {
		position: relative;
		z-index: 3;
		display: block !important;
		line-height: normal;

		.boost-pfs-filter-clear {
			display: none !important;
		}

		&::after {
			content: "";
			position: absolute;
			top: 50%;
			right: 0;
			z-index: -1;
			display: block;
			width: 14px;
			height: 14px;
			background-image: linear-gradient($grey-middle, $grey-middle),
				linear-gradient($grey-middle, $grey-middle);
			background-repeat: no-repeat;
			background-size: 100% 1px, 0;
			background-position: 50% 50%;
			transform: translateY(-50%);
		}

		.boost-pfs-filter-option-tooltip {
			&::before {
				width: 11px;
				height: 11px;
				margin-left: 2px;
				vertical-align: top;
				font-size: 8px;
				line-height: 11px;
				color: $paragraph-secondary;
			}
		}

		.boost-pfs-filter-tooltip-wrapper {
			width: 148px !important;

			.boost-pfs-filter-qtip-content {
				padding: 3px 10px;
				border: 0 !important;
				border-radius: 1px;
				background-color: #f0f1f2;
				box-shadow: 0 3px 6px rgba(0, 0, 0, 0.03);
				font-family: $font-avenir-next-condensed-light;
				font-size: 7px;
				font-weight: 500;
				line-height: 1.14;
				letter-spacing: 0.06em;
				text-align: center !important;
				text-transform: uppercase;
				color: $paragraph-primary;
			}
		}

		.boost-pfs-filter-tooltip-arrow {
			top: -16px;
			border: 0 !important;
			background-color: #f0f1f2;
		}
	}

	.boost-pfs-filter-option-title-heading {
		display: block;
		width: 100%;
		padding-bottom: 0;
		text-transform: none;
		color: $paragraph-primary;
	}

	.boost-pfs-filter-option-title-text {
		color: $paragraph-primary;

		&::before {
			content: none;
		}
	}

	.boost-pfs-filter-option-title-count {
		display: inline-block;
		margin-left: 15px;

		&::before {
			content: "(";
		}

		&::after {
			content: ")";
		}

		&:empty {
			display: none;
		}
	}

	.boost-pfs-filter-option-content {
		padding-top: 18px;

		.boost-pfs-filter-option-range-amount-min,
		.boost-pfs-filter-option-range-amount-max {
			height: 40px;
			margin-bottom: 0;
			border: 1px solid $border-primary;
			font-size: 14px;
			font-weight: 400;
			line-height: normal;
			color: $paragraph-secondary;
		}

		.boost-pfs-filter-option-range-slider {
			height: 1px;
			background-color: $border-primary;
		}

		.noUi-pips-horizontal {
			font-size: 12px;
			color: $paragraph-secondary;
		}
	}

	.boost-pfs-filter-option-content-inner {
		max-height: 195px !important;

		&::-webkit-scrollbar {
			width: 4px;
		}

		&::-webkit-scrollbar-track {
			border-radius: 2.5px;
			background-color: $grey-light;
		}

		&::-webkit-scrollbar-thumb {
			border-radius: 2.5px;
			background-color: $grey-middle;
		}
	}

	.boost-pfs-filter-option-value {
		margin-left: 10px;
	}

	.boost-pfs-filter-option-amount {
		display: none;
	}

	.boost-pfs-filter-button {
		z-index: 2;
		display: flex;
		align-items: center;
		font-size: 14px;
		font-weight: 500;
		line-height: 1.43;
		color: $paragraph-secondary;
		transition: all 0.25s ease-out;
	}

	.boost-pfs-filter-apply-button {
		display: none !important;
	}

	.boost-pfs-filter-option-item {
		&.selected {
			.boost-pfs-filter-button {
				font-weight: 500;
				color: $paragraph-primary;
			}
		}
	}

	.boost-pfs-filter-option-item-list {
		margin: 0;

		.boost-pfs-filter-option-item {
			margin: 0;
			padding: 0;
			line-height: normal;

			+ .boost-pfs-filter-option-item {
				margin-top: 12px;
			}
		}

		&.boost-pfs-filter-option-item-list-single-list {
			.boost-pfs-filter-option-item {
				padding-left: 20px;
			}
		}

		&.boost-pfs-filter-option-item-list-multiple-list {
			.boost-pfs-filter-option-item {
				&.selected {
					.boost-pfs-check-box {
						border-color: $brand-primary;
						background-color: $brand-primary;

						&::before {
							content: "";
						}
					}
				}

				.boost-pfs-check-box {
					position: relative;
					top: 0;
					left: 0;
					display: inline-block;
					width: 20px;
					height: 20px;
					border-radius: 0;
					border: 1px solid $border-primary;
					background-color: $white;

					&::before {
						content: none;
						position: absolute;
						top: 4px;
						left: 4px;
						width: 10px;
						height: 6px;
						border: 2px solid $white;
						border-top: none;
						border-right: none;
					}
				}
			}
		}

		&.boost-pfs-filter-option-item-list-swatch {
			.boost-pfs-filter-option-item {
				display: flex;
				align-items: center;

				&.selected {
					.boost-pfs-filter-option-swatch-image {
						border: 1px solid $brand-primary !important;
					}
				}

				&.has-border {
					.boost-pfs-filter-option-swatch-image {
						border: 0 !important;
					}
				}

				.boost-pfs-filter-option-swatch-image {
					display: block;
					flex: 0 0 20px;
					width: 20px;
					height: 20px;
					border-radius: 0;
					border: 0 !important;
					box-shadow: none !important;

					&::before {
						top: 0;
						bottom: 0;
						left: 0;
						right: 0;
						width: 100%;
						height: 100%;
						border-radius: 0;
						border: 0 !important;
						box-shadow: inset 0 0 0 2px $white,
							inset 0 0 0 3px $border-primary;
					}

					+ .boost-pfs-filter-button {
						display: block;
					}
				}

				.boost-pfs-filter-button {
					padding: 0;
				}
			}
		}
	}
}

@include breakpoint(large) {
	.collection-filters {
		display: block;
	}
}

@media screen and (max-width: 991px) {
	.collection-filters {
		&.boost-pfs-filter-tree-mobile-sticky.boost-pfs-filter-tree-mobile-open {
			position: fixed;
			top: 0;
			bottom: 0;
			left: 0;
			right: auto;
			z-index: 999999999;
			display: block !important;
			width: calc(100% - 40px) !important;
			height: 100% !important;
			background: $white;

			.boost-pfs-filter-mobile-toolbar {
				position: relative;

				.boost-pfs-filter-mobile-toolbar-top {
					left: auto;
					right: 0;
					width: 40px !important;

					.boost-pfs-filter-close {
						background-color: rgba($black, 0.5);

						span {
							margin: 0;
							background: none;
						}
					}
				}

				.boost-pfs-filter-mobile-toolbar-header {
					display: block;
					height: auto;
					padding: 12px 20px;
					border-bottom: 1px solid $paragraph-primary;
					background: $white;
					font-family: $font-avenir-next-condensed;
					font-size: 24px;
					font-weight: 500;
					line-height: 1.25;
					letter-spacing: 0.06em;
					text-align: left;
					text-transform: uppercase;
					color: $headline-primary;
				}

				.boost-pfs-filter-mobile-toolbar-bottom {
					position: absolute;
					top: 50%;
					right: 0;
					float: none;
					margin: 0 20px;

					.boost-pfs-filter-clear-all {
						display: inline-block;
						font-family: $font-avenir-next-condensed-light;
						font-size: 12px;
						font-weight: 500;
						line-height: 1.67;
						letter-spacing: 0.07em;
						text-transform: uppercase;
						text-decoration: none;
						color: $brand-primary;
						transform: translateY(-50%);
					}
				}
			}

			.boost-pfs-filter-options-wrapper,
			.boost-pfs-filter-mobile-footer {
				left: 0;
				right: auto;
				width: calc(100% - 40px) !important;
			}

			.boost-pfs-filter-options-wrapper {
				bottom: 92px;
				padding-left: 20px;
				padding-right: 20px;
			}

			.boost-pfs-filter-option {
				border-top: 1px solid $border-primary;

				&:first-child {
					border-top: 0;
				}

				&:last-child {
					border-bottom: 1px solid $border-primary;
				}

				.boost-pfs-filter-option-title-heading {
					&::after {
						content: none;
					}
				}

				.boost-pfs-filter-selected-items-mobile {
					display: none;
				}

				.boost-pfs-filter-option-content-inner {
					max-height: 100% !important;
				}
			}

			.boost-pfs-filter-mobile-footer {
				display: flex;
				justify-content: center;
				padding: 20px;

				.boost-pfs-filter-show-result,
				.boost-pfs-filter-apply-all-button {
					display: inline-block;
					width: 50%;
					height: auto;
					padding: 17px 16px;
					border: 1px solid $brand-primary;
					background: $brand-primary;
					font-family: $font-avenir-next-condensed-light;
					font-size: 14px;
					font-weight: 400;
					line-height: 1.15;
					letter-spacing: 0.06em;
					text-align: center;
					text-transform: uppercase;
					text-decoration: none;
					color: $white;
				}

				.boost-pfs-filter-apply-all-button {
					margin-left: 10px;
				}
			}
		}
	}
}

@media screen and (max-width: 575px) {
	.collection-filters {
		&.boost-pfs-filter-tree-mobile-sticky.boost-pfs-filter-tree-mobile-open {
			.boost-pfs-filter-mobile-footer {
				.boost-pfs-filter-show-result,
				.boost-pfs-filter-apply-all-button {
					width: 100%;
					max-width: 142px;
				}
			}
		}
	}
}

@media screen and (max-width: 340px) {
	.collection-filters {
		&.boost-pfs-filter-tree-mobile-sticky.boost-pfs-filter-tree-mobile-open {
			.boost-pfs-filter-mobile-footer {
				.boost-pfs-filter-show-result,
				.boost-pfs-filter-apply-all-button {
					padding-left: 10px;
					padding-right: 10px;
				}
			}
		}
	}
}
