.footer-menu {
	margin-top: 42px;

	&.footer-menu--mobile {
		border-top: 1px solid $border-primary;
	}

	&.footer-menu--desktop {
		display: none;
	}
}

.footer-menu__row {
	display: flex;
	flex-wrap: wrap;

	margin: 0 -5px;
}

.footer-menu__column {
	flex: 0 0 100%;
	max-width: 100%;

	padding: 0 5px;
}

.footer-menu__title {
	margin-bottom: 20px;

	font-size: 13px;
	line-height: 1.25;
	text-transform: uppercase;
}

.footer-menu__content {
	display: flex;
	flex-direction: column;
	margin-top: -12px;

	padding-bottom: 20px;
}

.footer-menu__link {
	margin-top: 12px;
	text-decoration: none;
}

.accordion.footer-menu__accordion {
	.footer-menu__accordion-item-toggler {
		padding-top: 24px;
		padding-bottom: 23px;
		font-family: $font-avenir-next-condensed;
		font-size: 12px;
		line-height: 1;
	}
}

.footer-menu__accordion-item {
	border-bottom: 1px solid $border-primary;
}

@include breakpoint(large) {
	.footer-menu {
		margin-top: 42px;

		&.footer-menu--mobile {
			display: none;
		}

		&.footer-menu--desktop {
			display: block;
		}
	}

	.footer-menu__row {
		display: flex;
		flex-wrap: wrap;

		margin: 0 -5px;
	}

	.footer-menu__column {
		flex: 0 0 25%;
		max-width: 25%;

		padding: 0 5px;
	}

	.footer-menu__title {
		margin-bottom: 20px;

		font-size: 14px;
		line-height: 1.25;
		text-transform: uppercase;
	}

	.footer-menu__content {
		display: flex;
		flex-direction: column;
		margin-top: -12px;

		padding-bottom: 0;
	}

	.footer-menu__link {
		margin-top: 12px;
		text-decoration: none;
	}
}
