.all-stores-list {
	padding-bottom: 60px;
}

.all-stores-list__container {
	padding-left: 40px;
	padding-right: 40px;
}

.all-stores-list__heading {
	margin-bottom: 16px;
	font-size: 26px;
	line-height: 1.23;
	letter-spacing: 0.06em;
	text-align: center;
	text-transform: uppercase;
}

.all-stores-list__subtext {
	margin-bottom: 32px;
	margin-left: auto;
	margin-right: auto;
	font-size: 14px;
	font-weight: 500;
	line-height: 1.43;
	text-align: center;
	color: $paragraph-secondary;
	font-style: normal;
	max-width: 335px;
}

.all-stores-list__slider-slide {
	width: 68%;
	margin-right: -1px;
	padding-top: 31px;
	padding-bottom: 52px;
	padding-left: 20px;
	padding-right: 20px;
	border: 1px solid $border-primary;
}

@include breakpoint(small) {
	.all-stores-list__slider-slide {
		width: 50%;
	}
}

@include breakpoint(medium) {
	.all-stores-list__slider {
		padding-left: 8px;
		padding-right: 8px;
	}

	.all-stores-list__slider-slide {
		width: 33.33%;
		margin-right: 0;
		padding-top: 0;
		padding-bottom: 0;
		padding-left: 0;
		padding-right: 0;
		border: none;
	}

	.all-stores-list {
		padding-bottom: 91px;
	}

	.all-stores-list__heading {
		font-size: 30px;
		line-height: 1.17;
		letter-spacing: 0.05em;
		text-transform: uppercase;
	}

	.all-stores-list__subtext {
		max-width: 560px;
	}
}

.all-stores-list__slider-wrapper {
	margin-left: -8px;
	margin-right: -8px;
}

@include breakpoint(large) {
	.all-stores-list__slider-slide {
		width: 24.05%;
		padding-left: 8px;
		padding-right: 8px;
	}
}
