.product-meta {
	margin-top: 28px;
	padding-left: 20px;
	padding-right: 20px;
}

.product-meta__rating {
	margin-bottom: 16px;
}

.product-meta__vendor {
	margin-bottom: 7px;
	font-size: 14px;
	font-weight: 400;
	line-height: 1.43;
}

.product-meta__vendor-button {
	position: relative;
	display: inline-block;
	margin-left: 11px;
	padding: 0;
	background: none;
	vertical-align: middle;
	font-size: 11px;
	font-weight: 500;
	line-height: 1.5;
	letter-spacing: 0.2px;
	text-decoration: none;
	color: $brand-primary;
	transition-duration: 0.25s;
	transition-property: visibility, opacity;
	transition-timing-function: ease-out;

	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		display: block;
		width: 100%;
		height: 1px;
		background-color: $brand-primary;
		transition: width 0.25s ease;
	}

	@include hover {
		&::after {
			width: 0;
		}
	}
}

.product-meta__vendor-button--hidden {
	visibility: hidden;
	opacity: 0;
}

.product-meta__header {
	padding-top: 16px;
	padding-bottom: 28px;
}

.product-meta__header-row {
	display: flex;
	align-items: flex-start;
	margin-left: -5px;
	margin-right: -5px;

	+ .product-meta__header-row {
		margin-top: 7px;
	}
}

.product-meta__header-col {
	padding-left: 5px;
	padding-right: 5px;
}

.product-meta__header-col--title {
	flex: 0 0 70%;
	max-width: 70%;
}

.product-meta__header-col--price {
	flex: 0 0 30%;
	max-width: 30%;
}

.product-meta__vendor-value {
	font-size: 20px;
	font-weight: 500;
}

.product-meta__title,
.product-meta__subtitle {
	font-family: $font-primary;
	letter-spacing: normal;
	text-transform: none;
}

.product-meta__title {
	font-size: 16px;
	font-weight: 400;
	line-height: 1.5;
}

.product-meta__subtitle {
	font-size: 14px;
	font-weight: 400;
	line-height: 1.5;
}

.product-meta__price {
	text-align: right;
}

.product-meta__price-value {
	font-size: 16px;
	font-weight: 500;
	line-height: 1.5;
	color: $paragraph-primary;
}

.product-meta__price-value--compare-price {
	margin-bottom: 3px;
	font-size: 11px;
	line-height: 1.45;
	letter-spacing: 0.2px;
	text-decoration: line-through;
}

.product-meta__price-value--sale {
	color: $product-sale;
}

.product-meta__price-value--unit {
	margin-top: 3px;
	font-size: 11px;
	line-height: 1.45;
	letter-spacing: 0.2px;
	color: $paragraph-secondary;
}

.product-meta__price-note {
	margin-top: 3px;
	font-size: 10px;
	letter-spacing: 0.2px;
	text-align: right;
	color: $paragraph-secondary;
}

.product-meta__delivery {
	margin-top: 8px;

	&[data-notice-more-then-two] {
		p {
			color: $state-success;
		}
	}

	&[data-notice-less-then-two],
	&[data-notice-out-of-stock] {
		p {
			color: $state-error;
		}
	}
}

.product-meta__delivery-notice {
	font-size: 11px;
	line-height: 1.45;
	letter-spacing: 0.2px;

	&[data-delivery-status="immediate-delivery"] {
		color: $state-success;
	}

	&[data-delivery-status="delayed-delivery"] {
		color: $state-error;
	}
}

.product-meta__services {
	margin-top: 16px;
}

.product-meta__services-button {
	display: block;
	padding: 22px;
	border: 1px solid $border-primary;
	background-color: $background-light-grey;

	@include hover {
		.product-meta__services-button-text::after {
			width: 0;
		}
	}
}

.product-meta__services-button-text {
	font-family: $font-primary;
	font-size: 11px;
	line-height: 1.45;
	letter-spacing: 0.2px;
	text-transform: none;
	text-decoration: none;
}

.product-meta__description {
	padding-top: 26px;
	padding-bottom: 28px;
}

.product-meta__footer {
	padding-top: 28px;
	border-top: 1px solid $border-primary;
}

.product-meta__footer-contact {
	font-size: 11px;
	line-height: 1.45;
	letter-spacing: 0.2px;
	color: $paragraph-primary;

	p,
	a {
		font-size: inherit;
		line-height: inherit;
		letter-spacing: inherit;
	}

	p + p {
		margin-top: 7px;
		color: $paragraph-primary;
	}

	a {
		position: relative;
		text-decoration: none;
		color: $paragraph-primary;

		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			display: block;
			width: 100%;
			height: 1px;
			background-color: $paragraph-primary;
			transition: width 0.25s ease;
		}

		@include hover {
			&::after {
				width: 0;
			}
		}
	}
}

.product-meta__footer-share {
	margin-top: 40px;
}

/* stylelint-disable */
.product-meta__availability {
	margin-top: 16px;
	background-color: #fafafa;
	padding: 23px;
	border: 1px solid $border-primary;
	position: relative;
}

.product-meta__availability-opener {
	cursor: pointer;

	&::after {
		content: "";
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: 25px;
		height: 12px;
		width: 6px;
		background-image: url("data:image/svg+xml,%3Csvg fill='none' xmlns='http://www.w3.org/2000/svg' class='icon icon-chevron' viewBox='0 0 6 13'%0A%3E%3Cpath d='M.137 11.824a.594.594 0 00.012.773.45.45 0 00.689-.014l5.025-5.83a.595.595 0 000-.76L.838.167a.451.451 0 00-.69-.013.594.594 0 00-.011.772l4.698 5.447-4.698 5.45z' fill='%231A1A1A' /%3E%3C/svg%3E%0A");
		background-repeat: no-repeat;
		transition: transform 0.25s ease-out;
	}

	&:hover {
		&::after {
		}
	}
}

.product-meta__availability-opener-title {
	font-size: 12px;
	font-weight: 600;
	line-height: 1.5;
	letter-spacing: 0.05em;
	text-transform: uppercase;
	font-family: $font-avenir-next-condensed;
}

.product-meta__availability-opener-subtitle {
	margin-top: 0;
	font-size: 11px;
	line-height: 16px;
	font-weight: 400;
	letter-spacing: 0.2px;
}

.product-meta__video-wrapper {
	margin-top: 40px;
	position: relative;
	overflow: hidden;
	padding-top: 55%;
}

.product-meta__video {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-position: 50% 50%;
}
/* stylelint-enable */

@include breakpoint(medium) {
	.product-meta {
		margin-top: 0;
		padding-left: 32px;
		padding-right: 0;
	}

	.product-meta__rating {
		margin-bottom: 25px;
	}

	.product-meta__header {
		padding-top: 0;
		padding-bottom: 22px;
	}

	.product-meta__vendor-value {
		font-size: 22px;
		line-height: 1.36;
	}

	.product-meta__subtitle {
		font-size: 11px;
		line-height: 1.36;
	}

	.product-meta__price-value {
		line-height: 1.36;
	}

	.product-meta__price-value--compare-price {
		margin-bottom: 8px;
		line-height: 1.45;
	}

	.product-meta__price-value--unit {
		margin-top: 8px;
	}

	.product-meta__price-note {
		margin-top: 2px;
		font-size: 8px;
	}

	.product-meta__description {
		padding-top: 43px;
	}
}

@include breakpoint(ultra-large) {
	.product-meta {
		padding-left: 102px;
		padding-right: 58px;
	}
}

@include breakpoint(extra-wide) {
	.product-meta {
		padding-left: 220px;
		padding-right: 103px;
	}
}
