.account-add-addresses {
	.account-content__header-wrapper {
		margin-bottom: 17px;
	}

	.account-content__header-icon {
		width: 36px;
		height: 36px;

		svg {
			width: 100%;
			height: 100%;
		}
	}

	.input {
		height: 50px;
	}
}

.account-add-addresses__form-checkbox-container {
	margin-top: 38px;
	margin-bottom: 22px;
}

.account-add-addresses__submit-button-container {
	display: flex;
	flex-direction: column;
	margin-top: 20px;
}

.account-add-addresses__submit-button {
	max-width: 335px;
	height: 52px !important;
}

.account-add-addresses__form-row {
	display: flex;
	flex-direction: column;
}

.account-add-addresses__custom-select-label {
	top: 2px !important;
	left: 13px !important;
}

.account-add-addresses__form-col {
	+ .account-add-addresses__form-col {
		margin-top: 14px;
	}
}

.account-add-addresses__arrow {
	position: absolute;
	top: 50%;
	right: 14px;
	display: block;
	width: 16px;
	height: 8px;
	background-image: url("data:image/svg+xml,%3Csvg width='11' height='5' viewBox='0 0 11 5' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.736026 0.109303C0.563525 -0.0403333 0.288591 -0.0360708 0.121945 0.118822C-0.0447016 0.273715 -0.0399551 0.520584 0.132546 0.67022L4.76694 4.69031C4.9353 4.83635 5.20227 4.8363 5.37056 4.69019L10.0009 0.670102C10.1734 0.520399 10.178 0.273528 10.0113 0.1187C9.84454 -0.036128 9.5696 -0.0402827 9.39717 0.10942L5.06855 3.86753L0.736026 0.109303Z' fill='black'/%3E%3C/svg%3E%0A");
	background-repeat: no-repeat;
	background-size: 14px 8px;
	transform: translateY(-50%);
	transition: transform 0.25s ease-out;
}

.account-add-addresses__arrow--active {
	transform: translateY(-50%) rotate(180deg);
}

@include breakpoint(small) {
	.account-add-addresses__form-row {
		flex-direction: row;
	}

	.account-add-addresses__form-col {
		+ .account-add-addresses__form-col {
			margin-top: 0;
		}
	}
}

@include breakpoint(medium) {
	.account-add-addresses {
		.account-content__back-link-icon {
			padding-right: 10px;
		}

		.input {
			height: 50px;
		}

		.account-content__header-wrapper {
			margin-bottom: 28px;
		}

		.account-content__header-icon {
			width: 48px;
			height: 48px;
		}
	}

	.account-add-addresses__form-checkbox-container {
		margin-top: 27px;
	}

	.account-add-addresses__submit-button {
		max-width: 274px;
		padding-top: 8px;
	}
}
