.product-recommendations {
	padding-top: 50px;
	padding-bottom: 25px;
}

.product-recommendations__title {
	margin-bottom: 22px;

	&:empty {
		display: none;
	}
}

.product-recommendations__slider-slide {
	width: 46%;
	margin-right: 20px;
}

.product-recommendations__slider-container {
	.slider__pagination.swiper-pagination-bullets {
		display: none;
	}
}

@include breakpoint(extra-small) {
	.product-recommendations__slider-slide {
		width: 47%;
	}
}

@include breakpoint(medium) {
	.product-recommendations {
		padding-top: 60px;
		padding-bottom: 30px;
	}

	.product-recommendations__title {
		margin-bottom: 24px;
		font-size: 30px;
		line-height: 1.167;
		letter-spacing: 0.05em;
	}

	.product-recommendations__slider-slide {
		width: 48%;
		margin-right: 24px;
	}
}

@include breakpoint(large) {
	.product-recommendations__slider-slide {
		width: 21%;
	}
}

@include breakpoint(extra-large) {
	.product-recommendations__slider-slide {
		width: 17%;
	}

	.product-recommendations {
		padding-top: 72px;
		padding-bottom: 43px;
	}
}

@include breakpoint(ultra-large) {
	.product-recommendations__slider-slide {
		max-width: initial;
		width: 17.5%;
	}
}

@include breakpoint(wide) {
	.product-recommendations__slider-slide {
		width: 18.5%;
	}
}

@include breakpoint(extra-wide) {
	.product-recommendations {
		padding-bottom: 50px;
	}

	.product-recommendations__title {
		margin-bottom: 40px;
	}

	.product-recommendations__slider-slide {
		width: 19.5%;
		margin-right: 40px;
	}
}
