.share-button {
	display: inline-flex;
	align-items: center;
	text-decoration: none;

	@include hover {
		.share-button__text {
			&::after {
				width: 0;
			}
		}
	}
}

.share-button__icon {
	margin-right: 17px;
	color: $paragraph-primary;

	svg {
		display: block;
		width: 29px;

		path {
			fill: currentColor;
		}
	}
}
