.all-stores-banners {
	padding-bottom: 28px;
}

.all-stores-banners__row {
	display: flex;
	flex-direction: column;
}

.all-stores-banners__column {
	display: flex;
	width: 100%;
	padding-bottom: 32px;
	flex-direction: column;
}

.all-stores-banner__title {
	margin-bottom: 6px;
	font-size: 24px;
	line-height: 1.25;
	letter-spacing: 0.06em;
}

.all-stores-banner__subtitle {
	display: flex;
}

.all-stores-banner__subtitle-link {
	font-weight: normal;
	line-height: 1.43;
	text-transform: capitalize;
	text-decoration: none;
	font-style: normal;
}

.all-stores-banner__image-wrapper {
	position: relative;
	overflow: hidden;
	display: block;
	margin-bottom: 16px;
	padding-top: 76%;
}

.all-stores-banner__image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	-o-object-fit: cover;
	object-fit: cover;
	-o-object-position: 50% 50%;
	object-position: 50% 50%;
}

@include breakpoint(small) {
	.all-stores-banners {
		padding-bottom: 20px;
	}

	.all-stores-banners__column {
		width: 50%;
		padding-bottom: 40px;
		padding-left: 18px;
		padding-right: 18px;
	}

	.all-stores-banners__row {
		flex-direction: row;
		margin-left: -18px;
		margin-right: -18px;
	}
}

@include breakpoint(medium) {
	.all-stores-banners {
		padding-bottom: 8px;
	}

	.all-stores-banners__column {
		padding-bottom: 71px;
		padding-left: 26px;
		padding-right: 26px;
	}

	.all-stores-banners__row {
		margin-left: -26px;
		margin-right: -26px;
		padding-left: 8px;
		padding-right: 8px;
	}

	.all-stores-banner__title {
		letter-spacing: 0.08em;
	}

	.all-stores-banner__subtitle-link {
		letter-spacing: 0.1em;
	}

	.all-stores-banner__image-wrapper {
		margin-bottom: 18px;
		padding-top: 76.2%;
	}
}
