.account-content__back-link-container {
	display: none;
}

.account-content__header-wrapper {
	display: flex;
	align-items: center;
	margin-bottom: 29px;
}

.account-content__header-title {
	margin-right: 16px;
	font-size: 20px;
	font-weight: bold;
	line-height: 1.1;
	letter-spacing: 0.07em;
	color: $paragraph-primary;
	font-style: normal;
}

.account-content__header {
	svg {
		width: 19px;
		height: 19px;
	}

	.input {
		height: 50px;
	}
}

.account-content__header-subtitle {
	margin-top: 22px;
	margin-bottom: 40px;
	color: $paragraph-secondary;
}

.account-content__title {
	font-size: 16px;
	line-height: 1.375;
	letter-spacing: 0.08em;
	color: $paragraph-primary;
}

.account-content__table-heading {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

@include breakpoint(medium) {
	.account-content-addresses__form-wrapper {
		max-width: 526px;
	}

	.account-content__back-link-container {
		display: block;
		margin-bottom: 4px;
	}

	.account-content__back-link-icon {
		padding-right: 12px;
	}

	.account-content__back-link {
		display: flex;
		width: max-content;
		padding-top: 2px;
		font-family: $font-avenir-next-condensed;
		font-size: 12px;
		line-height: 1.67;
		letter-spacing: 0.04em;
		text-transform: uppercase;
		text-decoration: none;
		flex-direction: row;
	}

	.account-content__header-title {
		font-size: 24px;
		line-height: 1.17;
		letter-spacing: 0.08em;
	}
}

@include breakpoint(large) {
	.account-content__container {
		max-width: 1030px;
		//padding-left: 63px;
	}

	.account-content__header {
		margin-bottom: 40px;
	}

	.account-content__header-subtitle {
		margin-top: 7px;
	}
}

@include breakpoint(ultra-large) {
	.account-content__container {
		//padding-left: 126px;
	}
}
