.tag {
	display: inline-block;
	padding: 2.5px 8px;
	border-radius: 2px;
	border: 1px solid transparent;
	background-color: rgba($brand-primary, 0.5);
	font-size: 12px;
	font-weight: 400;
	line-height: 1.5;
	text-align: center;
	color: $white;
	cursor: default;

	&.tag--outline {
		border-color: $brand-primary;
		background-color: transparent;
		color: $brand-primary;
	}

	&.tag--link {
		border-color: transparent;
		background-color: transparent;
		color: $brand-primary;
	}
}
