.stores-and-services {
	padding-top: 52px;
	padding-bottom: 48px;
}

.stores-and-services__row {
	display: flex;
	flex-direction: column;
}

.stores-and-services__col {
	flex: 0 0 100%;
	max-width: 100%;

	+ .stores-and-services__col {
		margin-top: 58px;
	}
}

.stores-and-services__content {
	display: flex;
	flex-direction: column;

	&.stores-and-services__content--services {
		.stores-and-services__content-col + .stores-and-services__content-col {
			margin-top: 28px;
		}
	}
}

.stores-and-services__content-col {
	position: relative;
	flex: 0 0 100%;
	max-width: 100%;

	+ .stores-and-services__content-col {
		margin-top: 12px;
	}
}

.stores-and-services__title {
	margin-bottom: 12px;
}

.stores-and-services__subtitle {
	margin-bottom: 12px;
	font-size: 16px;
	line-height: 1.375;
	letter-spacing: 0.08em;
}

.stores-and-services__text {
	padding-right: 110px;
	font-size: 14px;
	font-weight: 400;
	line-height: 1.43;
	color: $paragraph-primary;

	p + p {
		margin-top: 20px;
	}

	&.stores-and-services__text--list {
		p {
			position: relative;
			padding-left: 12px;

			&::before {
				content: "";
				position: absolute;
				top: 8px;
				left: 0;
				display: block;
				width: 5px;
				height: 5px;
				border-radius: 50%;
				background-color: currentColor;
				transform: translateY(-50%);
			}

			+ p {
				margin-top: 12px;
			}
		}
	}
}

.stores-and-services__link {
	width: fit-content;
	margin-top: 28px;
}

.stores-and-services__image-wrapper {
	position: relative;
	overflow: hidden;
	padding-top: 111.64%;
}

.stores-and-services__image {
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.stores-and-services__link-wrapper {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.stores-and-services__mascot-svg-wrapper {
	position: absolute;
	width: 100%;

	&.stores-and-services__mascot-svg-wrapper--stores {
		bottom: 30px;
		right: -10px;
		max-width: 131px;
	}

	&.stores-and-services__mascot-svg-wrapper--services {
		bottom: -20px;
		right: 22px;
		max-width: 112px;
	}
}

.stores-and-services__mascot-wrapper {
	position: absolute;
	width: 100%;

	&.stores-and-services__mascot-wrapper--stores {
		bottom: 30px;
		right: -10px;
		max-width: 131px;
	}

	&.stores-and-services__mascot-wrapper--services {
		bottom: -20px;
		right: 22px;
		max-width: 112px;
	}
}

.stores-and-services__mascot {
	display: block;
	width: 100%;
}

@include breakpoint(medium) {
	.stores-and-services {
		padding-top: 35px;
		padding-bottom: 30px;
	}

	.stores-and-services__col {
		display: flex;
		flex-direction: column;

		+ .stores-and-services__col {
			margin-top: 70px;
		}
	}

	.stores-and-services__content {
		height: 100%;
		margin-left: -16px;
		margin-right: -16px;
		flex-direction: row;

		&.stores-and-services__content--services {
			.stores-and-services__content-col
			+ .stores-and-services__content-col {
				margin-top: 0;
			}
		}
	}

	.stores-and-services__content-col {
		display: flex;
		padding-left: 16px;
		padding-right: 16px;
		flex-direction: column;

		&.stores-and-services__content-col--image {
			flex: 0 0 58%;
			max-width: 58%;
		}

		&.stores-and-services__content-col--text {
			flex: 0 0 42%;
			max-width: 42%;
		}

		+ .stores-and-services__content-col {
			margin-top: 0;
		}
	}

	.stores-and-services__title {
		margin-bottom: 22px;
	}

	.stores-and-services__subtitle {
		margin-top: 21px;
	}

	.stores-and-services__text {
		padding-right: 0;
	}

	.stores-and-services__image-wrapper {
		padding-top: 130.45%;
	}

	.stores-and-services__mascot-svg-wrapper {
		position: static;
		margin-top: auto;

		&.stores-and-services__mascot-svg-wrapper--stores {
			bottom: auto;
			right: auto;
			max-width: unset;
		}

		&.stores-and-services__mascot-svg-wrapper--services {
			bottom: auto;
			right: auto;
			max-width: unset;
		}
	}

	.stores-and-services__mascot-wrapper {
		&.stores-and-services__mascot-wrapper--stores {
			bottom: -19px;
			left: 15px;
			right: unset;
			max-width: 207px;
		}

		&.stores-and-services__mascot-wrapper--services {
			bottom: 5px;
			left: 15px;
			right: unset;
			max-width: 165px;
		}
	}
}

@include breakpoint(extra-large) {
	.stores-and-services {
		padding-top: 51px;
	}

	.stores-and-services__row {
		flex-direction: row;
	}

	.stores-and-services__col {
		flex: 0 0 50%;
		max-width: 50%;

		&.stores-and-services__col--stores {
			padding-right: 75px;
		}

		&.stores-and-services__col--services {
			padding-left: 75px;
		}

		+ .stores-and-services__col {
			margin-top: 0;
		}
	}

	.stores-and-services__content {
		&.stores-and-services__content--stores {
			.stores-and-services__content-col.stores-and-services__content-col--text {
				padding-bottom: 258px;
			}

			.stores-and-services__content-col.stores-and-services__content-col--no-padding {
				padding-bottom: 0;
			}
		}

		&.stores-and-services__content--services {
			.stores-and-services__content-col.stores-and-services__content-col--text {
				padding-bottom: 317px;
			}

			.stores-and-services__content-col.stores-and-services__content-col--no-padding {
				padding-bottom: 0;
			}
		}
	}
}

@include breakpoint(extra-wide) {
	.stores-and-services {
		padding-top: 60px;
	}

	.stores-and-services__col {
		&.stores-and-services__col--stores {
			padding-right: 20px;
		}

		&.stores-and-services__col--services {
			padding-left: 20px;
		}

		+ .stores-and-services__col {
			margin-top: 0;
		}
	}

	.stores-and-services__content-col {
		&.stores-and-services__content-col--image {
			flex: 0 0 54%;
			max-width: 54%;
		}

		&.stores-and-services__content-col--text {
			flex: 0 0 46%;
			max-width: 46%;
		}
	}

	.stores-and-services__subtitle {
		margin-top: 39px;
	}

	.stores-and-services__image-wrapper {
		padding-top: 130.62%;
	}

	.stores-and-services__content {
		&.stores-and-services__content--stores {
			.stores-and-services__content-col.stores-and-services__content-col--text {
				padding-bottom: 420px;
			}

			.stores-and-services__content-col.stores-and-services__content-col--no-padding {
				padding-bottom: 0;
			}
		}

		&.stores-and-services__content--services {
			.stores-and-services__content-col.stores-and-services__content-col--text {
				padding-bottom: 410px;
			}

			.stores-and-services__content-col.stores-and-services__content-col--no-padding {
				padding-bottom: 0;
			}
		}
	}

	.stores-and-services__mascot-wrapper {
		&.stores-and-services__mascot-wrapper--stores {
			bottom: 50px;
			left: 20px;
			max-width: 310px;
		}

		&.stores-and-services__mascot-wrapper--services {
			bottom: 55px;
			left: 35px;
			max-width: 202px;
		}
	}
}
