.store-overview-list__container {
	p {
		+ p {
			margin-top: 0;
		}
	}
}

.store-overview-list__subtitle {
	margin-bottom: 12px;
	font-size: 16px;
	line-height: 1.37;
	letter-spacing: 0.08em;
	text-transform: uppercase;
}

.store-overview-list__row {
	display: flex;
	flex-direction: column;
	padding-bottom: 40px;
}

.store-overview-list__title {
	width: 100%;
	margin-bottom: 16px;
	font-size: 28px;
	line-height: 1.29;
	letter-spacing: 0.09em;
	text-transform: uppercase;
	border-bottom: 1px solid $border-primary;
}

.store-overview-list__subtitle--big {
	margin-top: 7px;
	margin-bottom: 14px;
	font-size: 20px;
	line-height: 1.1;
	letter-spacing: 0.07em;
}

.store-overview-list__left-col {
	padding-bottom: 17px;
}

.store-overview-list__image-wrapper {
	position: relative;
	overflow: hidden;
	width: 100%;
	margin-top: auto;
	padding-top: 77.8%;
}

.store-overview-list__col-image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.store-overview-list__col {
	+ .store-overview-list__col {
		margin-top: 26px;
	}
}

.store-overview-list__content {
	column-count: 2;
	width: 96.8%;
	column-gap: 0;
}

.store-overview-list__content--full {
	column-count: 2;
	column-gap: 0;
	width: 96.8%;
}

.store-overview-list__item {
	max-width: 147px;
	font-size: 14px;
	font-weight: normal;
	line-height: 1.43;
	color: $brand-secondary;
	font-style: normal;
}

@include breakpoint(medium) {
	.store-overview-list__container {
		padding-left: 40px;
		padding-right: 40px;
	}

	.store-overview-list__content-wrapper {
		width: 100%;
		padding-left: 40px;
	}

	.store-overview-list__row {
		padding-bottom: 60px;
		flex-direction: row;
	}

	.store-overview-list__title {
		margin-top: -3px;
		margin-bottom: 27px;
	}

	.store-overview-list__left-col {
		width: 41.6%;
		padding-bottom: 0;
	}

	.store-overview-list__col {
		width: 50%;
		padding-bottom: 24px;
		padding-right: 11px;

		+ .store-overview-list__col {
			margin-top: 0;
			margin-left: 24px;
		}
	}

	.store-overview-list__col--wide {
		width: 90%;
	}

	.store-overview-list__content {
		column-count: 2;
		column-gap: 37px;
		width: 100%;
	}

	.store-overview-list__content--full {
		column-count: 4;
		column-gap: 24px;
		width: 100%;
	}

	.store-overview-list__content-container {
		display: flex;
		flex-direction: row;
		margin-top: -1px;
	}

	.store-overview-list__image-wrapper {
		padding-top: 87.66%;
	}

	.store-overview-list__content-wrapper--two-col {
		.store-overview-list__col {
			&:first-child {
				border-right: 1px solid $border-primary;
			}
		}
	}

	.store-overview-list__subtitle {
		margin-bottom: 14px;
		font-size: 16px;
		line-height: 1.37;
		letter-spacing: 0.08em;
		text-transform: uppercase;
	}

	.store-overview-list__item {
		max-width: 136px;
	}
}

@include breakpoint(large) {
	.store-overview-list__content-wrapper {
		padding-left: 127px;
	}

	.store-overview-list__col {
		width: 37.75%;
	}

	.store-overview-list__col--wide {
		width: 79.65%;
	}
}
