.card {
	position: relative;

	@include hover {
		.card__buttons-wrapper {
			transform: translateY(0);
		}
	}

	@include isHoverDisabled {
		.card__buttons-wrapper {
			&.card__buttons-wrapper--desktop {
				display: none;
			}

			&.card__buttons-wrapper--mobile {
				display: block;
			}
		}
	}
}

.card__image-wrapper {
	position: relative;
	overflow: hidden;
	padding-top: 114.58%;
}

.card__image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.card__buttons-wrapper {
	margin-top: 16px;

	&.card__buttons-wrapper--desktop {
		position: absolute;
		overflow-x: auto;
		bottom: 0;
		left: 0;
		z-index: 2;
		display: none;
		width: 100%;
		margin-top: 0;
		padding: 14px 0;
		background-color: $white;
		white-space: nowrap;
		transform: translateY(100%);
		transition: transform 0.25s ease-out;

		-webkit-overflow-scrolling: touch;

		&::-webkit-scrollbar {
			height: 4px;
		}

		&::-webkit-scrollbar-track {
			background-color: $text-field-disabled;
		}

		&::-webkit-scrollbar-thumb {
			background-color: $text-field-border;
		}
	}
}

.card__button {
	margin-right: 24px;
}

.card__link-wrapper {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
	display: block;
	width: 100%;
	height: 100%;
}

.card__title {
	display: inline-block;
	margin-top: 16px;
	font-family: $font-avenir-next-condensed;
	font-size: 16px;
	font-weight: 500;
	line-height: 1.25;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	text-decoration: none;
	color: $paragraph-primary;
	transition: color 0.25s ease-out;

	&:link {
		@include hover {
			color: $paragraph-secondary;
		}
	}
}

.card__subtitle {
	margin-top: 8px;
	font-size: 14px;
	line-height: 1.43;
	color: $paragraph-secondary;
}

@include breakpoint(medium) {
	.card__image-wrapper {
		padding-top: 133.44%;
	}
}

@include breakpoint(extra-large) {
	.card__buttons-wrapper {
		&.card__buttons-wrapper--desktop {
			display: block;
		}

		&.card__buttons-wrapper--mobile {
			display: none;
		}
	}
}

@include breakpoint(extra-wide) {
	@include breakpoint(medium) {
		.card__image-wrapper {
			padding-top: 133.47%;
		}
	}
}
