.event-booking {
	padding-top: 26px;
	padding-bottom: 16px;
}

.event-booking__container {
	max-width: 2247px;
	margin-left: auto;
}

.event-booking__title {
	margin-bottom: 11px;
}

.event-booking__pagination {
	margin-top: 4px;
}

@include breakpoint(medium) {
	.event-booking {
		padding-top: 56px;
		padding-bottom: 42px;
	}

	.event-booking__title {
		margin-bottom: 22px;
	}
}

@include breakpoint(extra-large) {
	.event-booking {
		padding-bottom: 23px;
	}
}

@include breakpoint(extra-wide) {
	.event-booking {
		padding-top: 126px;
		padding-bottom: 25px;
	}
}

@media screen and (min-width: 2561px) {
	.event-booking__container {
		margin-right: auto;
	}
}
