.customer-mobile-popup {
	position: fixed;
	top: 105px;
	left: -100vw;
	z-index: 10;
	display: block;
	width: 100%;
	height: 100vh;
	padding: 31px 20px;
	background-color: $white;
	transition: 0.3s all ease;

	&.popup-open {
		left: 0;
	}
}

.customer-mobile-popup__link-list--no-user-link,
.customer-mobile-popup__link-list-item-link {
	display: block;
	margin-bottom: 32px;
	font-family: $font-avenir-next-condensed;
	font-size: 16px;
	text-transform: uppercase;
	text-decoration: none !important;
}

.customer-mobile-popup__customer-name {
	margin-bottom: 22px;
}

@include breakpoint(large) {
	.customer-mobile-popup {
		display: none;
	}
}
