.featured-banner__title {
	margin-bottom: 13px;

	font-size: 26px;
}

.featured-banner__image-wrapper {
	position: relative;
	overflow: hidden;
	display: block;

	margin-bottom: 10px;

	padding-top: 111.4%;
}

.featured-banner__image {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

	width: 100%;
	height: 100%;

	object-fit: cover;
}

.featured-banner__subtitle {
	margin-bottom: 2px;

	font-size: 19px;
}

.featured-banner__subtitle-link {
	font-size: inherit;
	text-decoration: none;
}

.featured-banner__description {
	font-family: $font-primary;
	font-size: 12px;
	line-height: 1.5;

	p,
	a {
		font-family: inherit;
		font-size: inherit;
		font-weight: 400;
		line-height: inherit;
		color: $paragraph-secondary;
	}

	a {
		text-underline-position: under;
	}
}

.featured-banner__button {
	margin-top: 15px;

	&.button--text {
		border-bottom: 1px solid $brand-primary;
	}
}

@include breakpoint(medium) {
	.featured-banner__title {
		margin-bottom: 19px;

		font-size: 31px;
	}

	.featured-banner__image-wrapper {
		position: relative;
		overflow: hidden;
		display: block;

		margin-bottom: 11px;

		padding-top: 100.4%;
	}

	.featured-banner__image {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;

		width: 100%;
		height: 100%;

		object-fit: cover;
	}

	.featured-banner__subtitle {
		margin-bottom: 2px;

		font-size: 24px;
	}

	.featured-banner__subtitle-link {
		font-size: inherit;
		text-decoration: none;
	}

	.featured-banner__description {
		font-size: 12px;
		line-height: 1.5;

		p {
			font-size: inherit;
			line-height: inherit;
			color: $paragraph-secondary;
		}
	}

	.featured-banner__button {
		&.button--text {
			border-bottom: 1px solid $brand-primary;
		}
	}
}
