.featured-banners {
	padding-top: 40px;
	padding-bottom: 40px;
}

.featured-banners__row {
	margin: 0 -13px;
	margin-top: -46px;
}

.featured-banners__column {
	flex: 0 0 100%;
	max-width: 100%;

	margin-top: 46px;
	padding: 0 13px;
}

@include breakpoint(medium) {
	.featured-banners__column {
		flex: 0 0 50%;
		max-width: 50%;
	}
}
