.account-orders {
	margin-top: 6px;

	.account-content__header {
		margin-bottom: 16px;
	}

	.account-content__header-title {
		display: flex;
		flex-direction: row;
		align-items: center;
	}

	.account-content__header-icon {
		width: 43px;
		height: 48px;
		margin-left: 6px;

		svg {
			width: 100%;
			height: 100%;
		}
	}
}

.account-orders__pagination-wrapper {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
}

.account-orders__content-container {
	.orders {
		padding-bottom: 12px;
		overflow-y: scroll;

		&::-webkit-scrollbar {
			width: 0;
			height: 2px;
		}

		&::-webkit-scrollbar-track {
			background-color: $text-field-border;
		}

		&::-webkit-scrollbar-thumb {
			background-color: black;
		}

		+ .account-orders__pagination-wrapper {
			margin-top: 24px;
		}
	}
}

.account-orders__title {
	margin-bottom: 16px;
	font-size: 16px;
	font-weight: bold;
	line-height: 1.37;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	font-style: normal;
}

@include breakpoint(medium) {
	.account-orders {
		margin-top: 0;

		.account-content__header {
			margin-bottom: 25px;
		}
	}

	.account-orders__pagination-wrapper {
		justify-content: flex-end;
	}
}
