/* -- quantity box -- */

.quantity {
	position: relative;
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 40px;
	border: 1px solid $text-field-border;
	vertical-align: middle;
	text-align: left;
	white-space: nowrap;
	box-sizing: border-box;
	max-width: 115px;
	border-radius: 2px;

	input[type="number"]::-webkit-inner-spin-button,
	input[type="number"]::-webkit-outer-spin-button {
		margin: 0;
		-webkit-appearance: none;
	}

	input[type="number"] {
		width: auto;
		height: 100%;
		padding: 0 5px;
		border: none;
		background-color: transparent;
		text-align: center;
		-moz-appearance: textfield;
		min-width: 25px;
		flex-grow: 1;
	}

	.minus,
	.plus {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100%;
		padding: 0 2px 0 9px;
		border: none;
		background-color: $white;
		cursor: pointer;
		&:focus,
		&:hover {
			outline: none;
			background-color: $white;
		}

		.icon {
			height: 12px;
			max-width: 12px;
		}
	}

	.plus {
		padding: 0 9px 0 2px;
	}

	&.quantity--arrows {
		max-width: 63px;

		.quantity-buttons {
			max-width: 28px;
			display: flex;
			flex-direction: column;
			height: 100%;

			.plus,
			.minus {
				width: 100%;
				padding: 0 12px 0 10px;

				.icon {
					height: auto;
					max-width: 6px;
				}
			}
		}
	}
}
