.form__row {
	display: flex;
	flex-wrap: wrap;
	margin-left: -7px;
	margin-right: -7px;

	+ .form__row {
		margin-top: 14px;
	}
}

.form__col {
	flex: 1 0 50%;
	padding-left: 7px;
	padding-right: 7px;
}

.form__input-group {
	display: block;
}

.form__input,
.form__label,
.form__textarea {
	display: block;
	width: 100%;
}
