/* stylelint-disable */
.orders-table {
}

.orders {
	padding-bottom: 7px;
	overflow-y: scroll;

	&::-webkit-scrollbar {
		width: 0;
		height: 3px;
	}

	&::-webkit-scrollbar-track {
		background-color: $text-field-border;
	}

	&::-webkit-scrollbar-thumb {
		background-color: black;
	}
}

.orders-table__table {
	width: max-content;
	min-width: 100%;
	border: 1px solid $border-primary;
}

.orders-table__no-result {
}

.orders-table__no-result-text {
	color: $paragraph-secondary;
}

.orders-table__wrapper {
}

.orders-table__row {
	+ .orders-table__row {
		border-top: 1px solid $border-primary;
	}
}

.orders-table__row--head {
	border-bottom: 1px solid $border-primary;
}

.orders-table__cell {
	padding: 15px 12px 17px;
	font-size: 12px;
	line-height: 1.5;
	color: $paragraph-secondary;
	vertical-align: middle;
}

.orders-table__cell-data {
	padding-top: 8px;
	padding-bottom: 8px;
}

.orders-table__cell--head {
	font-family: $font-avenir-next-condensed;
	font-weight: 500;
	letter-spacing: 0.04em;
	text-align: left;
	text-transform: uppercase;
	color: $paragraph-primary;
}

.orders-table__cell--date {
	width: 17.3%;
}

.orders-table__cell--number {
	width: 18.9%;
}

.orders-table__cell--total {
	width: 12.8%;
}

.orders-table__cell--address {
	width: 38%;
	p {
		color: $brand-secondary;
		font-size: 12px;
		line-height: 1.5;
		font-style: normal;
		font-weight: normal;
	}
}

.orders-table__cell--status {
}

.orders-table__status {
}

.orders-table__status {
	display: inline-block;
	padding: 2px 8px;
	border-radius: 2px;
	color: $paragraph-primary;
}

.orders-table__status--unfulfilled {
	background-color: rgba($status-unfulfilled, 0.4);
}

.orders-table__status--fulfilled {
	background-color: rgba($status-fulfilled, 0.4);
}

.orders-table__status--partially-fulfilled {
	background-color: rgba($status-partially-fulfilled, 0.4);
}

.orders-table__status--scheduled {
	background-color: rgba($status-scheduled, 0.4);
}

.orders-table__status--on-hold {
	background-color: rgba($status-on-hold, 0.4);
}

@include breakpoint(large) {
	.orders-table__cell {
		padding-left: 16px;
		padding-right: 16px;
	}
}

/* stylelint-enable */
