.customer-page__form-inner {
	display: flex;
	padding: 39px 0;
	flex-wrap: wrap;
	.customer-page__form-button {
		margin-top: 16px;
		margin-bottom: 0;
	}
	.customer-page__create-account-link {
		width: 100%;
		font-size: 16px;
	}
	p {
		margin: 16px 0;
		font-size: 12px;
		text-align: center;
		color: $paragraph-secondary;
	}
	iframe {
		width: 100%;
		height: 55px !important;
		padding: 0 !important;
	}
}

.customer-page__form-button {
	width: 100%;
}

.customer-page__form-message {
	margin-top: 16px;
	font-size: 14px;
}

.customer-page__title {
	text-align: center;
}

.customer-page__text--has-account {
	margin: 32px 0 0;
	text-align: center;
}

.customer-page__content-wrapper {
	display: none;
}

.customer-page__image-wrapper {
	position: relative;
	overflow: hidden;
	flex-basis: 100%;
	max-width: 100%;
	max-height: 640px;
}

.customer-page__image {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.customer-page__form-section {
	flex-basis: 100%;
	max-width: 100% !important;
	padding: 0;
	.customer-page__error-message {
		margin-top: -10px;
		margin-bottom: 16px;
	}
}

.customer-page__form {
	margin-top: 0 !important;
}

.customer__page-form__message {
	margin-top: 16px;
	font-size: 14px;
}

.customer-page__field {
	margin: 14px 0;
}

.customer-page__field-input {
	width: 100%;
	height: 50px;
	&::placeholder {
		font-size: 16px;
	}
}

.customer-page__social-buttons {
	display: flex;
	justify-content: space-between;
	.icon {
		width: 15px;
	}
}

.customer-page__social-button {
	flex-basis: 49%;
	max-width: 49%;
	@include hover() {
		@media (-moz-touch-enabled: 0), (hover: hover) {
			&:hover {
				.icon {
					path {
						fill: $white;
					}
				}
			}
		}
	}
}

.customer-page__social-button-inner {
	position: relative;
	display: block;
	width: max-content;
	height: 100%;
	margin: 0 auto;
	padding-left: 25px;
	.icon {
		position: absolute;
		top: 50%;
		left: 0;
		width: 20px;
		transform: translateY(-50%);
		&.icon-facebook {
			width: 10px;
		}
	}
}

.customer-page__gift-block {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin-top: 28px;
	margin-bottom: 38px;
	.customer-page__gift-block-text {
		padding-left: 78px;
		text-align: left;
	}
}

.customer-page__gift-block-image {
	position: absolute;
	top: 50%;
	left: 0;
	transform: translateY(-50%);
}

.customer-page__gift-block-image--svg {
	width: 61px;
}

.customer-page__select {
	padding: 14px 12px;
	border: 1px solid $text-field-border;
	background-color: $white;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	color: $text-field-placeholder;
}

.customer-page__date-picker {
	color: $text-field-placeholder;
}

.customer-page__marketing_button {
	padding: 30px 0;
	border-bottom: 1px solid $text-field-border;
}

.customer-page__login-link {
	display: block;
	font-family: $font-primary;
	font-size: 12px;
	font-weight: 400;
	text-align: center;
	text-transform: uppercase;
	text-underline-position: under;
}

.customer-page__registration-form-section {
	.customer-page__gift-block-text {
		text-align: left;
	}
}

.login_page {
	.customer-page__image-wrapper {
		max-height: 690px;
	}
}

.customer-page__error-block {
	margin-top: 16px;
}

.customer-page__registration-form-error {
	margin-top: -10px;
}

.customer-page__recover-wrapper {
	.customer-page__form-inner {
		display: block;
		text-align: center;
	}
	.customer-page__content-wrapper {
		display: block;
		max-width: unset;
	}
	.customer-page__image-wrapper {
		width: 153px;
		max-width: 153px;
		margin: 0 auto;
		padding-top: 0;
	}
	.customer-page__image-wrapper--svg {
		width: 153px;
	}
	.customer-page__image {
		position: relative;
	}
	.customer-page__recover-form-section {
		max-width: 330px !important;
		margin: 0 auto;
		padding: 23px 0 0 0;
	}
	.customer-page__form-button {
		display: block;
		margin-bottom: 30px;
	}
}

//Reset success page
.customer-page {
	&.reset-password-page {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
}

.reset-password-page__subtitle {
	margin-top: 16px;
}

.reset-password-page {
	.customer-page__form-button {
		margin-bottom: 30px;
	}
}

.reset-password-page__title {
	text-align: center;
}

.reset-password-page__inner {
	max-width: 331px;
	padding: 60px 0 80px;
	text-align: center;
}

@include breakpoint(large) {
	.customer-page__registration-form-section {
		padding-top: 28px;
	}
	.customer-page__form-section {
		flex-basis: 30%;
		max-width: 30% !important;
		padding: 47px 0 40px 0;
	}
	.customer-page__content-wrapper {
		display: flex;
		flex-wrap: unset;
		flex-basis: 66.666%;
		max-width: 66.666%;
	}
	.customer-page__image-wrapper {
		margin-right: 60px;
		padding-top: 80%;
	}
	.customer-page__title {
		text-align: left;
	}
	.customer-page__form-inner p {
		text-align: left;
	}
}

@include breakpoint(extra-large) {
	.customer-page {
		.customer-page__text--has-account {
			margin: 32px 0 0;
			text-align: left;
		}
	}
	.customer-page__registration-image-wrapper {
		padding-top: 75%;
	}
	.customer-page__image {
		object-position: center;
	}
	.customer-page__form-section {
		padding: 47px 28px 40px 55px;
	}
	.customer-page__registration-form-section {
		padding-top: 0;
	}
	.customer-page__recover-wrapper {
		.customer-page__form-section {
			margin: 0 auto;
		}
	}
}
