.blog__container {
	max-width: 100%;
	margin: 0 auto;
	padding: 0 15px;
}

.blog__row {
	display: flex;
	justify-content: stretch;
	flex-wrap: wrap;

	margin: 0 -7.5px;
}

.blog__column {
	flex: 0 0 100%;
	max-width: 100%;

	padding: 0 7.5px;
}

@include breakpoint(large) {
	.blog__container {
		max-width: 1440px;
		margin: 0 auto;
		padding: 0 98px;
	}

	.blog__row {
		display: flex;
		justify-content: stretch;
		flex-wrap: wrap;

		margin: 0 -30px;
	}

	.blog__column {
		flex: 0 0 33.33%;
		max-width: 33.33%;

		padding: 0 30px;
	}
}
