.help-center-content__columns-container {
	display: flex;
	padding-bottom: 40px;
	flex-direction: column;
}

.help-center-content__column {
	display: flex;
	width: 100%;
	max-width: 274px;
	flex-direction: column;

	&:first-child {
		padding-right: 12px;
	}

	b,
	strong {
		color: $black;
	}
}

.help-center-content__block {
	display: flex;
	padding-bottom: 24px;
	flex-direction: column;
}

.help-center-content__block--mini-padding {
	padding-bottom: 12px;
}

.help-center-content__block-title {
	padding-bottom: 5px;
	padding-right: 10px;
	font-size: 14px;
	font-weight: 500;
	line-height: 1.43;
	text-transform: inherit;
	font-style: normal;
}

@include breakpoint(large) {
	.help-center-content__columns-container {
		flex-direction: row;
	}

	.help-center-content__block {
		flex-direction: row;
		flex-wrap: wrap;
	}
}
