.minicart {
	position: relative;
	z-index: 99999;
}

.minicart__container {
	position: fixed;
	top: 0;
	right: 0;
	width: 100%;
	max-width: 428px;
	height: 100%;
	background-color: $background-white;
	transform: translateX(100%);
	transition-timing-function: ease-out;
	transition-property: transform;
	transition-duration: 0.25s;
}

.minicart__container--open {
	transform: translateX(0);
}

.minicart__overlay {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: -1;
	visibility: hidden;
	opacity: 0;
	background-color: $background-overlay;
	transition-property: visibility, opacity;
	transition-duration: 0.3s;
	transition-timing-function: ease-out;
}

.minicart__overlay--shown {
	visibility: visible;
	opacity: 1;
}

.minicart__wrapper {
	position: relative;
	display: flex;
	flex-direction: column;
	height: 100%;
	background-color: $background-white;
}

.minicart__section {
	padding-left: 20px;
	padding-right: 20px;
	-webkit-transform: translate3d(0, 0, 0);
}

.minicart__section--header {
	padding-top: 22px;
	padding-bottom: 24px;
}

.minicart__section--footer {
	padding-top: 20px;
	padding-bottom: 20px;
}

.minicart__section--items {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	overflow-y: auto;

	&::-webkit-scrollbar {
		width: 5px;
	}

	&::-webkit-scrollbar-track {
		border-radius: 2.5px;
		background-color: $grey-light;
	}

	&::-webkit-scrollbar-thumb {
		border-radius: 2.5px;
		background-color: $grey-middle;
	}
}

.minicart__empty-wrapper {
	padding-top: 16px;
}

.minicart__empty-title {
	text-align: center;
}

.minicart__empty-button {
	margin-top: 30px;
}

.minicart__header {
	position: relative;
}

.minicart__header-title,
.minicart__header-subtitle {
	text-align: center;
}

.minicart__header-subtitle {
	margin-top: 5px;
	font-size: 14px;
	font-weight: 500;
	line-height: 1.5;
}

.minicart__header-close {
	position: absolute;
	top: -3px;
	right: -6px;
	padding: 0;
	background: none;
	color: $brand-primary;
	transition: 0.25s color ease-out;

	svg {
		display: block;

		path {
			fill: currentColor;
		}
	}

	@include hover {
		color: $brand-secondary;
	}
}

.minicart__free-shipping {
	margin-top: 11px;
}

.minicart__body {
	position: relative;
	overflow: hidden;
	flex: 1 0 auto;
}

.minicart__body--empty {
	display: flex;
	flex-direction: column;
}

.minicart__products {
	display: grid;
	grid-auto-rows: 1fr;
}

.minicart__products-item {
	+ .minicart__products-item {
		margin-top: 10px;
	}
}

.minicart__footer-row {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-left: -6px;
	margin-right: -6px;

	+ .minicart__footer-row {
		margin-top: 8px;
	}
}

.minicart__footer-col {
	padding-left: 6px;
	padding-right: 6px;
}

.minicart__footer-text {
	font-size: 14px;
	font-weight: 500;
	line-height: 1.43;
	color: $paragraph-primary;
}

.minicart__footer-text--secondary {
	font-weight: 400;
	color: $paragraph-secondary;
}

.minicart__footer-submit {
	margin-top: 20px;
}

.minicart__footer-submit-button {
	padding-top: 18px;
	padding-bottom: 16px;

	&::before {
		content: "";
		display: inline-block;
		width: 13px;
		height: 16px;
		margin-top: -4px;
		margin-right: 12px;
		background-image: url("data:image/svg+xml,%3Csvg fill='none' xmlns='http://www.w3.org/2000/svg' class='icon icon-lock' viewBox='0 0 14 17'%0A%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.936 10.434v-.01a1.968 1.968 0 00-1.138-1.75 1.974 1.974 0 00-2.075.255 1.053 1.053 0 00-.264.273c-.653.825-.511 2.059.337 2.736.01.009.022.036.022.065v1.308c0 .59.48 1.069 1.071 1.069h.21a1.07 1.07 0 001.071-1.07v-1.307c0-.026.014-.054.037-.073a1.91 1.91 0 00.73-1.495zm-2.632-.728c.329-.264.737-.314 1.084-.153.345.16.569.503.577.883a.945.945 0 01-.363.736 1.068 1.068 0 00-.403.828v1.31a.099.099 0 01-.1.1h-.21a.099.099 0 01-.099-.1v-1.307c0-.312-.137-.61-.374-.812a1.006 1.006 0 01-.112-1.485z' fill='%23fff' /%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M11.66 5.88h.963c.76 0 1.377.615 1.377 1.374v8.372c0 .759-.616 1.374-1.377 1.374H1.377C.617 17 0 16.385 0 15.626V7.254c0-.76.616-1.375 1.377-1.375h.962V4.47A4.588 4.588 0 016.991.001a4.596 4.596 0 014.67 4.471v1.407zm-1.133 0V4.488a3.465 3.465 0 00-3.536-3.356c-1.9-.03-3.469 1.471-3.518 3.352v1.395h7.054zM1.133 7.253c0-.134.11-.243.243-.243h11.247c.134 0 .243.109.243.243v8.372a.243.243 0 01-.243.242H1.376a.243.243 0 01-.243-.242V7.254z' fill='%23fff' /%3E%3C/svg%3E%0A");
		background-repeat: no-repeat;
		background-position: 50% 50%;
		vertical-align: middle;
	}
}

.minicart__footer-submit-button--cart-page {
	margin-bottom: 16px;
}

@media screen and (max-height: 575px) {
	.minicart__section-header,
	.minicart__section--footer {
		padding-top: 12px;
		padding-bottom: 12px;
	}

	.minicart__free-shipping {
		margin-top: 2px;
	}

	.minicart__products-item {
		+ .minicart__products-item {
			margin-top: 6px;
		}
	}

	.minicart__footer-text {
		font-size: 12px;
	}

	.minicart__footer-submit {
		margin-top: 12px;
	}
}

@media screen and (orientation: landscape) and (max-height: 575px) {
	.minicart__container {
		overflow-y: auto;
	}

	.minicart__body {
		overflow: unset;
	}

	.minicart__section--items {
		position: relative;
	}
}
