body {
	font-family: $font-primary;
	font-size: 14px;
	font-weight: 400;
	line-height: 1.43;
	color: $paragraph-primary;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
	font-family: $font-avenir-next-condensed;
	font-weight: 500;
	text-transform: uppercase;
	color: $headline-primary;
}

h1,
.h1 {
	font-size: 28px;
	line-height: 1.23;
	letter-spacing: 0.06em;
}

h2,
.h2 {
	font-size: 26px;
	line-height: 1.25;
	letter-spacing: 0.06em;
}

h3,
.h3 {
	font-size: 20px;
	line-height: 1.1;
	letter-spacing: 0.07em;
}

h4,
.h4 {
	font-size: 18px;
	line-height: 1.33;
	letter-spacing: 0.07em;
}

h5,
.h5 {
	font-size: 15px;
	font-weight: 500;
	line-height: 1.37;
	letter-spacing: 0.08em;
}

h6,
.h6 {
	font-size: 12px;
	font-weight: 500;
	line-height: 1.5;
	letter-spacing: 0.06em;
}

p,
.paragraph {
	font-size: 14px;
	font-weight: 400;
	line-height: 1.43;
	color: $paragraph-primary;

	+ p,
	+ .paragraph {
		margin-top: 10px;
	}
}

small,
.small {
	font-size: 12px;
	font-weight: 400;
	line-height: 18px;
	color: $paragraph-secondary;
}

a {
	font-size: 14px;
	font-weight: 500;
	line-height: 1.43;
	text-decoration: underline;
	color: $paragraph-primary;

	@include hover {
		text-decoration: none;
	}
}

.section-title {
	font-size: 26px;
	line-height: 1.23;
	letter-spacing: 0.06em;
	text-transform: uppercase;
	color: $paragraph-primary;
}

.section-title--small {
	font-size: 20px;
	line-height: 1.1;
	letter-spacing: 0.07em;
}

@include breakpoint(medium) {
	h1,
	.h1 {
		font-size: 30px;
		line-height: 1.17;
		letter-spacing: 0.05em;
	}

	h2,
	.h2 {
		font-size: 28px;
		line-height: 1.29;
		letter-spacing: 0.07em;
	}

	h3,
	.h3 {
		font-size: 24px;
		line-height: 1.17;
		letter-spacing: 0.08em;
	}

	h4,
	.h4 {
		font-size: 20px;
		line-height: 1.3;
	}

	.section-title {
		font-size: 30px;
		line-height: 1.167;
		letter-spacing: 0.05em;
	}

	.section-title--small {
		font-size: 24px;
		line-height: 1.167;
		letter-spacing: 0.08em;
	}
}
