.seo-text {
	padding-top: 27px;
	padding-bottom: 32px;
}

.seo-text__title {
	font-family: $font-primary;
	font-size: 14px;
	font-weight: 500;
	line-height: 1.43;
	text-transform: none;
	color: $paragraph-primary;
}

.seo-text__text {
	margin-top: 10px;
	font-size: 12px;
	font-weight: 400;
	line-height: 1.5;
	color: $paragraph-secondary;

	> * {
		color: $paragraph-secondary;
	}
}

@include breakpoint(medium) {
	.seo-text__title {
		line-height: 1.29;
		letter-spacing: 0.4px;
	}
}

@include breakpoint(large) {
	.seo-text__container {
		padding: 0 80px;
	}
}

@include breakpoint(wide) {
	.seo-text__container {
		max-width: 2030px;
	}
}
