.error-message {
	margin-top: 2px;
	font-size: 12px;
	line-height: 1.17;
	color: $state-error;
}

.input {
	display: block;
	height: 48px;
	padding: 14px 12px;
	border: 1px solid $text-field-border;
	background-color: $white;
	font-size: 16px;
	font-weight: 400;
	line-height: 1.125;
	color: $text-field;
	transition: all 0.25s ease-out;

	-webkit-appearance: none;
	-moz-appearance: none;

	~ .error-message {
		display: block;
	}

	&.error {
		border: 2px solid $state-error;

		&:focus {
			border-color: $state-error;
		}
	}

	&.success {
		background-image: url("data:image/svg+xml,%3Csvg width='20' height='14' viewBox='0 0 20 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.7741 0.215183C18.0694 -0.0752108 18.5443 -0.0711761 18.8347 0.224195C19.1251 0.519566 19.1211 0.994423 18.8257 1.28482L6.53525 13.3682C6.2435 13.655 5.7757 13.6551 5.48384 13.3683L0.224271 8.20122C-0.0712078 7.91093 -0.0754179 7.43608 0.214867 7.1406C0.505152 6.84512 0.980007 6.84091 1.27549 7.1312L6.00925 11.7818L17.7741 0.215183Z' fill='%2300BC37'/%3E%3C/svg%3E%0A");
		background-repeat: no-repeat;
		background-position: calc(100% - 11px) 50%;
	}

	&:focus {
		border: 2px solid $text-field;
	}

	&.disabled,
	&[disabled] {
		border-color: $text-field-border;
		background-color: $text-field-disabled;
		cursor: default;
		pointer-events: none;
	}

	@include placeholder {
		color: $text-field-placeholder;
	}
}

input {
	@extend .input;

	&[type="number"] {
		-moz-appearance: textfield;
	}

	&::-webkit-outer-spin-button,
	&::-webkit-inner-spin-button {
		-webkit-appearance: none;
		margin: 0;
	}

	&:-webkit-autofill,
	&:-webkit-autofill:hover,
	&:-webkit-autofill:focus {
		box-shadow: 0 0 0 1000px #fff inset;
	}
}
