.all-stores-info {
	display: flex;
	justify-content: center;
	padding-top: 6px;
	padding-bottom: 28px;
	flex-direction: column;
}

.all-stores-info__title {
	padding-top: 23px;
	padding-bottom: 16px;
	font-size: 26px;
	line-height: 1.23;
	letter-spacing: 0.06em;
	text-align: center;
	text-transform: uppercase;
}

.all-stores-info__appeal-text {
	margin-left: auto;
	margin-right: auto;
	padding-bottom: 20px;
	font-size: 14px;
	font-weight: 500;
	line-height: 1.43;
	text-align: center;
	color: $paragraph-secondary;
	max-width: 335px;
	font-style: normal;
}

.all-stores-info__slogan-container {
	display: flex;
	justify-content: center;
	margin-left: -10px;
	margin-right: -10px;
	padding-bottom: 20px;
	text-align: center;
	flex-direction: row;
}

.all-stores-info__slogan-item {
	position: relative;
	margin-top: 0;
	padding-left: 10px;
	padding-right: 10px;
	color: $brand-secondary;

	&::before {
		content: "";
		position: absolute;
		top: 4px;
		left: 0;
		width: 1px;
		height: 10px;
		background-color: $paragraph-secondary;
		color: $paragraph-secondary;
	}

	&:first-child {
		&::before {
			content: unset;
		}
	}
}

.all-stores-info__contact_info {
	display: flex;
	justify-content: center;

	p {
		font-size: 14px;
		font-weight: normal;
		line-height: 1.43;
		text-align: center;
		font-style: normal;
	}
}

@include breakpoint(medium) {
	.all-stores-info {
		padding-top: 4px;
		padding-bottom: 40px;
	}

	.all-stores-info__container {
		padding-left: 8px;
		padding-right: 8px;
	}

	.all-stores-info__appeal-text--width {
		max-width: 565px;
	}

	.all-stores-info__title {
		padding-top: 12px;
		padding-bottom: 11px;
		font-size: 30px;
		line-height: 1.17;
		letter-spacing: 0.05em;
	}
}
