.checkbox,
input[type="checkbox"] {
	display: none;

	&:checked {
		+ label {
			&::before {
				border-color: $brand-primary;
				background-color: $brand-primary;
			}

			&::after {
				display: block;
			}
		}

		&:disabled,
		&.disabled {
			+ label {
				&::before {
					border-color: $text-field-border;
					background-color: $text-field-disabled;
				}

				&::after {
					background-image: url("data:image/svg+xml,%3Csvg width='12' height='9' viewBox='0 0 12 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10.9954 0.174259C11.228 -0.0609069 11.6019 -0.0576395 11.8306 0.181557C12.0592 0.420753 12.056 0.8053 11.8235 1.04047L4.14597 8.82574C3.91624 9.05803 3.54789 9.05809 3.31808 8.8259L0.176687 5.64148C-0.055973 5.4064 -0.059288 5.02185 0.169283 4.78257C0.397854 4.54329 0.771755 4.53988 1.00442 4.77496L3.73179 7.54106L10.9954 0.174259Z' fill='%23D3D6D9'/%3E%3C/svg%3E%0A");
				}
			}
		}
	}

	+ label {
		position: relative;
		display: inline-block;
		padding-left: 30px;
		font-size: 14px;
		font-weight: 400;
		line-height: 1.43;
		color: $brand-secondary;
		cursor: pointer;

		&::before,
		&::after {
			content: "";
			position: absolute;
		}

		&::before {
			top: 50%;
			left: 0;
			width: 20px;
			height: 20px;
			border-radius: 1px;
			border: 1px solid $text-field-border;
			background-color: $white;
			transform: translateY(-50%);
		}

		&::after {
			top: 5px;
			left: 5px;
			width: 12px;
			height: 9px;
			background-image: url("data:image/svg+xml,%3Csvg width='12' height='9' viewBox='0 0 12 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11.8061 0.207839C11.5429 -0.0659569 11.1114 -0.0697579 10.8436 0.199509L3.76898 7.33566L1.15622 4.70019C0.888367 4.43102 0.456803 4.43499 0.193743 4.70888C-0.0678307 4.98123 -0.0640931 5.41787 0.202241 5.68551L3.29214 8.80072C3.55668 9.06656 3.98195 9.06638 4.24639 8.80044L11.7979 1.18447C12.0642 0.916729 12.0678 0.480088 11.8061 0.207839Z' fill='white'/%3E%3C/svg%3E%0A");
			background-repeat: no-repeat;
			background-size: 12px 9px;
		}
	}
}
