.product-availability {
	position: relative;

	.product-availability__close-button {
		position: absolute;
		top: 7px;
		left: 9px;
		display: block;
		width: 14px;
		height: 14px;
		padding: 0;
		background-color: transparent;
		color: $paragraph-primary;
		transition: color 0.25s ease-out;

		svg {
			display: block;
			width: 14px;

			path {
				fill: currentColor;
			}
		}

		@include hover {
			color: $paragraph-secondary;
		}
	}

	.product-availability__overlay {
		cursor: pointer;
	}

	.product-availability__header {
		text-align: center;
	}

	.product-availability__wrapper {
		max-width: 355px;
		position: fixed;
		top: 0;
		left: auto;
		right: 0;
		transform: translateX(100%);

		&.is-opened {
			transform: translateX(0);
		}

		@include breakpoint(medium) {
			max-width: 398px;
		}
	}

	.product-availability__scroll-wrapper {
		padding: 0;

		&::-webkit-scrollbar {
			width: 4px;
		}

		&::-webkit-scrollbar-track {
			border-radius: 2.5px;
			background-color: $grey-light;
		}

		&::-webkit-scrollbar-thumb {
			border-radius: 2.5px;
			background-color: $grey-middle;
		}
	}
}

.product-availability__content {
	padding: 39px 0;
}

.product-availability__header,
.product-availability__list-item {
	padding-left: 21px;
	padding-right: 21px;
}

.product-availability__header {
	padding-bottom: 22px;
	border-bottom: 1px solid $border-primary;
}

.product-availability__title {
	font-size: 24px;
	font-weight: bold;
	font-style: normal;
	line-height: 1.25;
	letter-spacing: 0.06em;
	text-transform: uppercase;
}

.product-availability__key {
	display: flex;
	justify-content: space-evenly;
	margin-top: 28px;
}

.product-availability__key-title {
	display: flex;
	align-items: center;
	font-family: $font-secondary;
	font-size: 14px;
	font-weight: 500;
	line-height: 20px;
	color: #4d4d4f;
}

.product-availability__key-item {
	display: flex;
	align-items: center;
	margin-left: 19px;
}

.product-availability__key-item-title {
	font-family: $font-secondary;
	font-size: 12px;
	line-height: 18px;
	color: #4d4d4f;
}

.product-availability__key-circle {
	display: block;
	width: 10px;
	height: 10px;
	border-radius: 50%;
	margin-right: 7px;

	.product-availability__key--stock & {
		background: #81cb9f;
	}

	.product-availability__key--low & {
		background: #f9ac52;
	}

	.product-availability__key--out & {
		width: 14px;
		height: 3px;
		background: #ff5942;
		border-radius: 0;
	}
}

@include breakpoint(medium) {
	.product-availability__wrapper {
		max-width: 521px;
	}
}

.product-availability__list-content {
	padding: 33px 25px;
}

.product-availability__list-item {
	position: relative;
	border-bottom: 1px solid $border-primary;

	p {
		font-size: 11px;
		line-height: 1.45;
		letter-spacing: 0.2px;
		color: #808080;
	}
}

.product-availability__list-item-title {
	margin-bottom: 12px;
	font-size: 12px;
	font-weight: 500;
	font-style: normal;
	line-height: 1.5;
	letter-spacing: 0.06em;
	text-transform: uppercase;
	color: #000000;
}

.product-availability__list-item-time {
	padding-top: 16px;
}

.product-availability__mark {
	position: absolute;
	top: 50%;
	right: 47px;
	transform: translateY(-50%);
}

.product-availability__mark-circle {
	display: block;
	width: 21px;
	height: 21px;
	border-radius: 50%;

	.product-availability__mark--stock & {
		background: #81cb9f;
	}

	.product-availability__mark--low & {
		background: #f9ac52;
	}

	.product-availability__mark--out & {
		width: 14px;
		height: 3px;
		background: #ff5942;
		border-radius: 0;
	}
}

.product-availability__info {
	padding: 25px 46px;
}

.product-availability__info-content {
	display: flex;
	align-items: center;
	padding: 15px 12px 16px 29px;
	border: 1px solid #000000;
}

.product-availability__info-image {
	width: 100%;
}

.product-availability__info-img {
	width: 100%;
	max-width: 81px;
}

.product-availability__info-text {
	font-size: 11px;
	line-height: 1.45;
	letter-spacing: 0.2px;
	text-align: center;
}

@include breakpoint(medium) {
	.product-availability__content {
		padding-left: 21px;
		padding-right: 21px;
	}

	.product-availability__header,
	.product-availability__list-item {
		padding-left: 0;
		padding-right: 0;
	}

	.product-availability__info {
		padding-left: 25px;
		padding-right: 25px;
	}
}
