.accordion {
	.accordion__item {
		&.active {
			.accordion__item-toggler {
				&::before {
					transform: translateY(-50%);
				}
			}
		}
	}

	.accordion__item-toggler {
		position: relative;
		display: block;
		width: 100%;
		padding-top: 15px;
		padding-bottom: 15px;
		padding-left: 0;
		padding-right: 20px;
		border: 0;
		background: transparent;
		font-size: 12px;
		font-weight: 600;
		line-height: 1.5;
		letter-spacing: 0.06em;
		text-align: left;
		text-transform: uppercase;
		color: $paragraph-primary;

		&::before,
		&::after {
			content: "";
			position: absolute;
			top: 50%;
			right: 0;
			width: 10.5px;
			height: 1px;
			border-radius: 8px;
			background-color: $paragraph-primary;
			transition-property: transform;
			transition-duration: 0.25s;
			transition-timing-function: ease-out;
		}

		&::before {
			transform: translateY(-50%) rotate(-90deg);
		}

		&::after {
			transform: translateY(-50%);
		}
	}

	.accordion__item-content {
		display: none;
		font-size: 12px;
		line-height: 1.45;
		letter-spacing: 0.2px;
		color: $paragraph-primary;
	}
}
