.outfit-one-person-list__container {
	padding-bottom: 20px;
}

.outfit-one-person-list__breadcrumbs-container {
	margin-bottom: 20px;
}

.outfit-one-person-list__product-count {
	margin-bottom: 12px;
	font-size: 14px;
	font-weight: normal;
	line-height: 1.43;
	color: $brand-secondary;
	font-style: normal;
}

.outfit-one-person-list__top-row {
	position: relative;
	display: flex;
	justify-content: space-between;
	width: 100%;
	margin-bottom: 57px;
	flex-direction: column;
}

.outfit-one-person-list__back-link-container {
	display: flex;
	max-width: 175px;
	flex-direction: row;
	order: 1;
	margin-top: 12px;
}

.outfit-one-person-list__back-link-icon {
	padding-right: 11px;
}

.outfit-one-person-list__back-link {
	font-family: $font-avenir-next-condensed;
	font-size: 12px;
	font-weight: normal;
	line-height: 1.67;
	letter-spacing: 0.04em;
	text-transform: uppercase;
	text-decoration: none;
	font-style: normal;
}

.outfit-one-person-list__heading-container {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	flex-direction: row;
}

.outfit-one-person-list__heading {
	font-size: 26px;
	line-height: 1.23;
	letter-spacing: 0.06em;
	text-align: center;
	text-transform: uppercase;
}

.outfit-one-person-list__row {
	display: flex;
	flex-direction: column;
}

.outfit-one-person-list__row--hidden {
	display: none;
}

.outfit-one-person-list__load-button-wrapper {
	display: flex;
	flex-direction: row;
	justify-content: center;
}

.outfit-one-person-list__button--hidden {
	display: none;
}

.outfit-one-person-list__load-button {
	max-width: 335px;
	margin-left: auto;
	margin-right: auto;
}

.outfit-one-person-list__col--left {
	margin-bottom: 40px;
}

.outfit-one-person-list__col--wide {
	display: flex;
	height: max-content;
	margin-left: -8px;
	margin-right: -8px;
	flex-direction: row;
	flex-wrap: wrap;
}

.outfit-one-person-list__product--empty {
	display: none;
}

.outfit-one-person-list__product {
	width: 50%;
	height: max-content;
	margin-bottom: 40px;
	padding-left: 8px;
	padding-right: 8px;

	.product-card__options {
		display: none;
	}
}

.outfit-one-person-list__description {
	margin-top: 6px;
}

@include breakpoint(small) {
	.outfit-one-person-list__product {
		width: 33.33%;
	}

	.outfit-one-person-list__col--left {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}

@include breakpoint(medium) {
	.outfit-one-person-list__breadcrumbs-container {
		margin-bottom: 15px;
	}

	.outfit-one-person-list__container {
		padding-bottom: 40px;
		padding-left: 40px;
		padding-right: 40px;
	}

	.outfit-one-person-list__heading {
		font-size: 30px;
		line-height: 1.17;
		letter-spacing: 0.05em;
	}

	.outfit-one-person-list__heading-container {
		width: calc(100% - 150px);
		padding-right: 150px;
	}

	.outfit-one-person-list__top-row {
		flex-direction: row;
		align-items: center;
	}

	.outfit-one-person-list__back-link-container {
		width: 150px;
		margin-top: 0;
		order: 0;
		padding-top: 4px;
	}

	.outfit-one-person-list__load-button {
		max-width: 274px;
	}

	.outfit-one-person-list__row {
		flex-direction: row;
		margin-bottom: 60px;
	}

	.outfit-one-person-list__col--left {
		width: 400px;
		margin-right: 50px;
	}

	.outfit-one-person-list__col--wide {
		display: flex;
		width: calc(100% - 400px);
		margin-left: -20px;
		margin-right: -20px;
		flex-direction: row;
		flex-wrap: wrap;
	}

	.outfit-one-person-list__product {
		width: 33.33%;
		padding-left: 20px;
		padding-right: 20px;
	}
}

@include breakpoint(large) {
	.outfit-one-person-list__col--wide {
		width: calc(100% - 500px);
	}

	.outfit-one-person-list__col--left {
		display: block;
		width: 500px;
	}
}

@include breakpoint(extra-large) {
	.outfit-one-person-list__col--wide {
		width: calc(100% - 677px);
	}

	.outfit-one-person-list__col--left {
		width: 677px;
		margin-right: 0;
	}
}
