.account-overview {
	margin-top: 19px;

	.account-content__header-wrapper {
		margin-bottom: 22px;
	}

	.account-content__header-icon {
		width: 25px;
		height: 32px;

		svg {
			width: 100%;
			height: 100%;
		}
	}
}

.account-overview__orders {
	margin-bottom: 30px;
}

.account-overview__orders-title {
	margin-bottom: 16px;
}

.account-overview__details-row {
	display: flex;
	justify-content: space-between;
	flex-direction: column;
}

.account-overview__details-col:not(:first-of-type) {
	margin-top: 40px;
}

.account-overview__details-title {
	margin-bottom: 12px;
	font-size: 16px;
	font-weight: bold;
	line-height: 1.37;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	font-style: normal;
}

.account-info__link {
	padding-top: 10px;
}

.account-info__field {
	font-size: 14px;
	font-weight: normal;
	line-height: 1.43;
	color: $brand-secondary;
	font-style: normal;
	max-width: 217px;
}

.account-info__field-address {
	p {
		font-size: 14px;
		font-weight: normal;
		line-height: 1.43;
		color: $brand-secondary;
		font-style: normal;
	}
}

.account-menu__logout--mobile {
	display: flex;
	justify-content: center;
	padding-top: 45px;
	padding-bottom: 10px;
	flex-direction: row;

	.account-menu__logout-button {
		width: 100%;
		max-width: 335px;
	}
}

@include breakpoint(medium) {
	.account-overview {
		margin-top: 0;
		.account-content__header {
			margin-bottom: 0;
		}

		.account-content__header-icon {
			width: 26px;
			height: 38px;
		}

		.account-content__header-wrapper {
			margin-bottom: 0;
		}
	}

	.account-menu__logout--mobile {
		display: none;
	}

	.account-overview__orders {
		margin-bottom: 28px;
	}

	.account-overview__details-row {
		flex-direction: row;
		justify-content: unset;
	}

	.account-overview__details-col {
		+ .account-overview__details-col {
			margin-left: 83px;
		}

		&.account-overview__details-col--width {
			width: 300px;
		}
	}

	.account-overview__details-col:not(:first-of-type) {
		margin-top: 0;
	}
}
