.about-us-banner {
	padding-top: 22px;
}

.about-us-banner__wrapper {
	position: relative;
	display: flex;
	min-height: 248px;
}

.about-us-banner__image {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.about-us-banner__head {
	z-index: 1;
	max-width: 1376px;
	align-self: center;
	width: 100%;
	margin: 0 auto;
	text-align: center;
}

.about-us-banner__title {
	color: $white;
}

.about-us-banner__breadcrumbs {
	position: absolute;
	top: -20px;
	z-index: 2;

	span:not(.breadcrumbs__separator) {
		color: $black;
	}
}

@include breakpoint(large) {
	.about-us-banner__wrapper {
		min-height: 618px;
	}

	.about-us-banner__title {
		font-size: 36px;
		line-height: 40px;
	}
}

@include breakpoint(extra-large) {
	.about-us-banner {
		padding-top: 0;
	}

	.about-us-banner__breadcrumbs {
		top: 3px;

		span:not(.breadcrumbs__separator) {
			color: $white;
		}
	}
}

@include breakpoint(wide) {
	.about-us-banner__wrapper {
		min-height: 1000px;
	}
}
