.instagram-collection {
	margin-top: 78px;

	.fs-has-links {
		display: none;
	}

	.fs-timeline {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		column-gap: 15px;
	}

	.fs-wrapper {
		div {
			&.fs-entry-container {
				width: calc(100% / 2 - 8px);
				padding-top: 40%;
			}

			&.fs-text-container {
				position: absolute;
				top: 100%;
				bottom: unset;
				display: block;
				margin-top: 20px;
				padding: 0;
			}

			&.fs-text-container * {
				color: $black;
			}
		}
	}

	.fs-service-icon {
		display: none;
	}

	.fs-entry-container {
		margin-bottom: 100px;
	}

	.fs-text-container {
		opacity: 1;
		background-color: unset;
	}

	.fs-timeline-entry {
		overflow: visible;
		margin-top: 0;
		margin-bottom: 0;
		margin-left: 0;
		margin-right: 0;

		&:hover {
			.fs-text-container {
				display: flex;
				align-items: flex-start;
				flex-direction: column;
			}
		}
	}

	.fs-timeline-text {
		display: block !important;
		margin-top: 0;
		margin-bottom: 0;
		padding-top: 0;
	}

	.fs-entry-title {
		overflow: hidden;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		text-overflow: ellipsis;
		min-height: 40px;
		max-height: 40px;
		font-family: $font-primary !important;
		text-align: left;
	}

	.fs-entry-date {
		margin-top: 7px;
		font-size: 12px !important;
		color: $paragraph-secondary !important;
	}

	div {
		&.fs-entry-date {
			display: block !important;
			margin-top: 13px;
			font-family: $font-primary !important;
			text-align: left;
		}
	}
}

.instagram-collection__title {
	margin-bottom: 11px;
}

.instagram-collection__title-url {
	text-decoration: none;
}

@include breakpoint(medium) {
	.instagram-collection {
		.fs-timeline {
			column-gap: 25px;
		}

		.fs-wrapper {
			div {
				&.fs-entry-container {
					width: calc(100% / 3 - 17px);
					padding-top: 30%;
				}
			}
		}
	}
}

@include breakpoint(large) {
	.instagram-collection {
		.fs-entry-container {
			margin-bottom: 80px;
		}

		.fs-entry-title {
			overflow: hidden;
			display: -webkit-box;
			-webkit-line-clamp: 1;
			-webkit-box-orient: vertical;
			text-overflow: ellipsis;
			min-height: 20px;
			max-height: 20px;
		}

		.fs-entry-date {
			font-size: 14px !important;
		}
	}
}
