.page-custom-container {
	display: block;
	max-width: 1440px;
	margin: 0 auto;
	padding: 0 20px;
}

.denim-content {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	flex-direction: column;
	max-width: 100%;
	width: 100%;
	padding: 52px 0 50px;
}

.denim-content-wrap {
	margin: 0 auto;
}

.denim-content-row {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	flex-direction: column;
	width: 100%;
}

.denim-content-row:not(:last-of-type) {
	margin-bottom: 70px;
}

.denim-content-column {
	width: 100%;
}

.denim-content-column.denim-content-textarea {
	/*max-width: 805px;*/
	width: 100%;
}

.denim-content-title__field {
	margin-bottom: 12px;
	font-family: $font-avenir-next-condensed;
	font-size: 24px;
	font-style: normal;
	font-weight: bold;
	line-height: 30px;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	color: $black;
}

.denim-content-textarea {
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
}

.denim-content-textarea__field {
	/*max-width: 805px;*/
	width: 100%;
}

.denim-content-textarea__field p {
	font-family: $font-avenir-next;
	font-size: 20px;
	font-style: normal;
	font-weight: 500;
	line-height: 34px;
	color: #4f4f4f;
}

.denim-content-textarea__field img {
	margin: 30px 0;
}

.denim-content-textarea__field img:last-child {
	margin: 30px 0 0;
}

.denim-popup-gallery__item.popup-gallery-item {
	width: 100%;
}

.popup__icon {
	position: absolute;
	z-index: 2;
}

.popup__icon--desktop {
	position: absolute;
	top: 29px;
	right: 29px;
	display: none;
}

.popup__icon--mobile {
	position: absolute;
	top: -40px;
	right: 0;
}

.denim-slider__pagination {
	margin-top: 20px;
}

@include breakpoint(medium) {
	.popup__icon--mobile {
		display: none;
	}

	.denim-popup .popup-gallery {
		user-select: none;
		pointer-events: none;
	}

	.popup-gallery__slider-slide:not(:last-of-type) {
		margin-right: 20px;
	}
}

@include breakpoint(medium) {
	.page-custom-container {
		padding: 0 32px;
	}
}

@include breakpoint(large) {
	.denim-content {
		margin: 0 auto;
		padding: 123px 0 70px;
	}

	.denim-content-row {
		flex-direction: row;
	}

	.denim-content-column {
		width: 45%;
	}

	.denim-content-title__field {
		max-width: 185px;
		margin-bottom: unset;
		font-size: 16px;
		line-height: 22px;
	}

	.denim-content-textarea__field p {
		font-size: 33px;
		line-height: 55px;
	}

	.popup__icon--desktop {
		display: block;
	}
}

@include breakpoint(wide) {
	.page-custom-container,
	.denim-content-wrap {
		max-width: 1944px;
	}
}
