.page-404 {
	padding-top: 20px;
	padding-bottom: 35px;
}

.page-404__content {
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
}

.page-404__image {
	display: block;
	max-width: 292px;
	margin-bottom: 17px;
	margin-left: auto;
	margin-right: auto;
}

.page-404__image--svg {
	margin-bottom: 0;
}

.page-404__image--svg svg {
	display: block;
	max-width: 292px;
	width: 100%;
}

.page-404__title {
	max-width: 285px;
	margin-left: auto;
	margin-right: auto;
	font-size: 30px;
	line-height: 1.17;
}

.page-404__text {
	max-width: 285px;
	margin-top: 15px;
	margin-left: auto;
	margin-right: auto;
	font-size: 14px;
	line-height: 1.48;
	color: $paragraph-secondary;
}

.page-404__button {
	width: 100%;
	max-width: 335px;
	margin-top: 15px;
	border: 1px solid $brand-primary;
	background-color: $background-white;
}

@include breakpoint(large) {
	.page-404__title {
		max-width: 100%;
	}
	.page-404__text {
		margin-top: 10px;
	}
	.page-404__button {
		margin-top: 29px;
	}
}
