.custom-select {
	position: relative;

	&.custom-select--floating-label {
		.custom-select__label {
			position: absolute;
			top: 6px;
			left: 12px;
			margin-bottom: 0;
			font-size: 12px;
			color: $text-field-placeholder;
		}

		.select2-container {
			.select2-selection.select2-selection--single {
				padding-top: 22px;
				padding-bottom: 6px;
			}
		}
	}

	.custom-select__select {
		display: none;
	}

	.custom-select__label {
		margin-bottom: 6px;
		font-size: 14px;
		font-weight: 400;
		color: $grey-middle;
	}

	.custom-select__dropdown {
		width: 100%;
		background-color: $white;
	}
}
