.product-brand {
	border-top: 1px solid $border-primary;
	border-bottom: 1px solid $border-primary;

	.product-brand__title {
		width: auto;
		margin: 0 auto;
		padding: 18px 50px 18px 0;
		font-family: $font-avenir-next-condensed;
		font-size: 18px;
		font-weight: bold;
		line-height: 24px;
		letter-spacing: 0.07em;
		text-align: left;
		text-transform: uppercase;

		&::before,
		&::after {
			width: 15.75px;
		}

		&.active {
			&::before,
			&::after {
				transform: translate(7px, -50%);
			}
		}
	}

	.product-brand__description {
		max-width: 683px;
		margin: 0 auto;
		padding: 0 20px 38px;
		font-size: 14px;
		line-height: 20px;
		text-align: center;
		color: #808080;

		a {
			text-underline-position: under;
		}
	}

	@include breakpoint(large) {
		.product-brand__title {
			font-size: 20px;
			line-height: 26px;
		}
	}
}
