.account-newsletter .account-content__header {
	display: flex;
	align-items: center;
	margin-bottom: 30px;
}

.account-newsletter .account-content__header-icon {
	width: 35px;
	height: 33px;
}

.account-newsletter .account-content__header-icon svg {
	width: 100%;
	height: 100%;
}

.account-newsletter__form input {
	margin-bottom: 15px;
}

.account-newsletter__form .button {
	width: 100%;
}

.account-newsletter .klaviyo_header,
.account-newsletter .klaviyo_subheader {
	font-size: 16px;
	font-weight: bold;
	line-height: 1.375;
	letter-spacing: 0.08em;
	text-transform: uppercase;
}

.account-newsletter__form {
	a {
		font-size: 14px;
		font-weight: 400;
		line-height: 1.42;
		text-decoration: none;
		color: $brand-secondary;
	}

	p {
		font-size: 14px;
		font-weight: 400;
		line-height: 1.42;
		text-decoration: none;
		color: $brand-secondary;
	}
}

.account-newsletter__unsubscribe-link {
	b,
	strong {
		text-decoration: underline;
	}
}

@include breakpoint(medium) {
	.account-newsletter .account-content__header {
		margin-bottom: 35px;
	}
	.account-newsletter__form {
		max-width: 332px;
	}

	.account-newsletter__unsubscribe-link {
		p {
			margin-right: -10px;
		}
	}
}
