.denim-nav {
	display: flex;
	justify-content: space-between;
	align-items: stretch;
	margin-bottom: 28px;
}

.denim-nav-container {
	width: 100%;
	margin: 0 auto;
}

.denim-nav-item {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	width: calc(100% / 3);
	padding: 15px 0 17px;
	border-top: none;
	border: 1px solid #d3d6d9;
	cursor: pointer;

	&::before {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		display: block;
		width: 100%;
		height: 0;
		background-color: $black;
	}

	&::after {
		content: "";
		position: absolute;
		top: 100%;
		display: flex;
		opacity: 0;
		width: 0;
		height: 0;
		border-left: 10px solid transparent;
		border-right: 10px solid transparent;
		border-top: 6px solid $black;
	}
}

.denim-nav-item.active {
	&::before {
		height: 2px;
	}

	&::after {
		opacity: 1;
	}
}

.denim-nav-item__img {
	user-select: none;
}

.denim-nav-content-wrapper {
	padding: 70px 0;
}

.denim-nav-content {
	display: flex;
	align-items: flex-start;
	flex-wrap: wrap;
}

.denim-nav-content__title {
	max-width: 431px;
	width: 100%;
}

.denim-nav-item__img-wrapper {
	margin: auto;
}

.denim-nav-item__title {
	margin-top: 15px;
	padding: 0 25px;
	font-family: $font-avenir-next-condensed;
	font-size: 12px;
	font-style: normal;
	font-weight: bold;
	line-height: 18px;
	letter-spacing: 0.07em;
	text-align: center;
	text-transform: uppercase;
	color: $black;
}

.denim-item-content {
	display: none;
}

.js-arrow-hidden {
	opacity: 0;
}

.denim-item-content.active {
	display: block;
}

.denim-nav-content__title span {
	max-width: 185px;
	display: inline-flex;
	align-items: center;
	width: auto;
	font-family: $font-avenir-next-condensed;
	font-size: 16px;
	font-style: normal;
	font-weight: bold;
	line-height: 22px;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	color: $black;
}

.denim-nav-content__textarea {
	max-width: 805px;
	width: 100%;
}

/* Steps */
.denim-steps {
	max-width: 994px;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	margin: 0 auto;
	padding: 0;
}

.denim-steps-item-wrapper {
	width: 100%;
	max-width: max-content;
	margin-bottom: 30px;
}

.denim-steps-item {
	max-width: 400px;
}

.denim-steps-item__title {
	font-family: $font-avenir-next-condensed;
	font-size: 18px;
	font-style: normal;
	font-weight: bold;
	line-height: 24px;
	letter-spacing: 0.07em;
	text-transform: uppercase;
	color: $black;
}

.denim-steps-item p {
	font-family: $font-avenir-next;
	font-size: 12px;
	font-style: normal;
	font-weight: normal;
	line-height: 18px;
	color: #808080;
}

/* End steps */

.denim-popup .popup__content {
	padding: 0;
}

.denim-popup .popup-content {
	display: flex;
	flex-direction: column-reverse;
}

.denim-popup .popup__title {
	display: none;
}

.denim-popup .popup__title.popup__title--mobile {
	display: block;
	margin-bottom: 26px;
	font-family: $font-avenir-next-condensed;
	font-size: 28px;
	font-style: normal;
	font-weight: bold;
	line-height: 36px;
	letter-spacing: 0.07em;
	text-align: left;
	text-transform: uppercase;
	color: $black;
}

.denim-popup .popup-gallery-item__img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.denim-popup .popup-textarea {
	padding: 18px 22px 30px;
	font-family: $font-avenir-next;
	font-size: 14px;
	font-style: normal;
	font-weight: normal;
	line-height: 20px;
	text-align: left;
	color: $black;
}

.denim-popup .popup-properties {
	margin: 20px 0;
}

.denim-popup .popup-gallery-item {
	position: relative;
	flex: none;
	width: 100%;
	padding-top: 125.5%;
}

.denim-popup .popup-properties-item:not(:last-of-type) {
	margin-bottom: 12px;
}

@include breakpoint(medium) {
	.denim-steps {
		padding: 0 20px;
	}

	.denim-steps-item-wrapper {
		max-width: 50%;
	}

	.denim-steps-item {
		max-width: 320px;
	}
}

@include breakpoint(large) {
	.denim-steps {
		justify-content: space-between;
		padding: 87px 32px 70px;
	}

	.denim-nav-item {
		padding: 12px 0 27px;
	}

	.denim-nav-item__title {
		font-size: 20px;
		line-height: 26px;
	}

	.denim-steps-item-wrapper {
		width: 100%;
		max-width: 390px;
		margin-bottom: 0;
	}

	.denim-steps-item {
		max-width: 100%;
	}

	.denim-steps-item-wrapper:not(:first-of-type) {
		margin-top: 0;
	}

	.denim-steps-item-wrapper:nth-child(n + 3) {
		width: 50%;
		margin-top: 70px;
	}

	.popup__wrapper.custom-popup__wrapper {
		max-width: 1280px;
	}

	.denim-popup-content {
		display: flex;
	}

	.denim-popup-info,
	.denim-popup-gallery {
		width: 50%;
	}

	.denim-popup-gallery {
		display: flex;
	}

	.denim-popup .popup__content {
		padding: 48px 80px;
	}

	.denim-popup .popup-content {
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-items: flex-start;
	}

	.denim-popup .popup__title {
		display: block;
		margin-bottom: 26px;
		font-family: $font-avenir-next-condensed;
		font-size: 28px;
		font-style: normal;
		font-weight: bold;
		line-height: 36px;
		letter-spacing: 0.07em;
		text-align: center;
		text-transform: uppercase;
		color: $black;
	}

	.denim-popup .popup__title.popup__title--mobile {
		display: none;
	}

	.denim-popup .popup-textarea {
		flex: none;
		margin-right: auto;
		padding: 0;
		max-width: 360px;
		font-family: $font-avenir-next;
		font-size: 14px;
		font-style: normal;
		font-weight: normal;
		line-height: 20px;
		text-align: left;
		color: $black;
	}

	.denim-popup .popup-properties {
		margin: 32px 0;
	}

	.denim-popup .popup-properties-item {
		display: flex;
	}

	.denim-popup .popup-properties-item__title {
		flex: 1 1 20%;
	}

	.denim-popup .popup-properties-item__value {
		flex: 1 1 75%;
	}

	.denim-popup .popup-gallery {
		max-width: 50%;
		width: 100%;
	}

	.denim-popup .popup-gallery-item {
		width: 360px;
		margin-right: 20px;
	}
}

@include breakpoint(ultra-large) {
	.denim-popup .popup-gallery {
		max-width: 740px;
		width: 100%;
	}

	.denim-popup .denim-popup-gallery .swiper-slide {
		width: 50%;
	}
}

@include breakpoint(wide) {
	.denim-nav-container {
		max-width: 1944px;
	}
}
