.side-menu {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 100;
	transition-property: visibility;
	transition-duration: 0.3s;
	visibility: hidden;

	&.is-opened {
		visibility: visible;

		.side-menu__wrapper {
			transform: translateZ(0);
		}

		.side-menu__overlay {
			visibility: visible;
			opacity: 1;
		}
	}

	.side-menu__close-button {
		display: none;
	}
}

.side-menu__wrapper {
	display: flex;
	flex-direction: column;
	max-width: 376px;
	width: 100%;
	height: 100%;
	background-color: #fff;
	transform: translateX(-100%);
	transition-property: transform;
	transition-duration: 0.3s;
}

.side-menu__close-button {
	position: absolute;
	top: 54px;
	right: -54px;
	display: none;
	width: 28px;
	height: 28px;
	cursor: pointer;
}

.icon.icon-close-side-menu {
	width: auto;
}

.side-menu__content {
	display: flex;
	flex-direction: column;
	height: 100%;
}

.side-menu__scroll-wrapper {
	max-height: 100%;
	padding: 42px 20px 0;
	overflow-y: auto;
	-webkit-overflow-scrolling: touch;
}

.side-menu__overlay {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	visibility: hidden;
	opacity: 0;
	background-color: rgba(52, 59, 65, 0.5);
	transition-property: opacity, visibility;
	transition-duration: 0.3s;
}

.side-menu__tab-switchers {
	padding: 0 32px;
}

.side-menu__tab-switcher {
	position: relative;
	display: inline-block;
	margin: 30px 16px 21px 0;
	padding: 0;
	background: transparent;
	font-family: $font-avenir-next-condensed;
	font-size: 13px;
	font-weight: 500;
	line-height: 1.67;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	text-decoration: none;
	color: $brand-primary;

	&:last-child {
		margin-right: 0;
	}

	&.is-active {
		border-bottom: 1px solid $brand-primary;
	}
}

.side-menu__link-item {
	padding-bottom: 23px;

	&:last-child {
		padding-bottom: 0;
	}
}

.side-menu__link-item.active {
	.side-menu__link--button::after {
		transform: rotate(180deg);
	}
}

.side-menu__link.side-menu__link--button {
	padding-right: 14px;

	&::after {
		content: "";
		position: absolute;
		top: 8px;
		right: 0;
		display: flex;
		align-items: center;
		width: 0;
		height: 0;
		border-left: 4px solid transparent;
		border-right: 4px solid transparent;
		border-top: 4px solid currentColor;
		transition-property: transform;
		transition-duration: 0.25s;
		transition-timing-function: ease-out;
	}
}

.side-menu__links {
	padding-left: 20px;
}

.side-menu__link {
	position: relative;
	padding: 0;
	background: transparent;
	font-family: $font-avenir-next-condensed;
	font-size: 12px;
	font-weight: 500;
	line-height: 18px;
	letter-spacing: 0.06em;
	text-transform: uppercase;
	text-decoration: none;
	color: var(--link-color, #000);

	@include hover {
		color: $brand-secondary;
	}
}

.side-menu__child-links-content--accordion {
	display: none;
}

.side-menu__child-links-content {
	padding-top: 16px;
	max-width: 230px;
}

.side-menu__child-links--columns {
	display: flex;
	flex-wrap: wrap;

	.side-menu__child-link-item {
		width: 16.6666666%;
		margin-bottom: 16px;
	}
}

.side-menu__child-links {
	padding-left: 20px;
}

.side-menu__child-link-item {
	margin-bottom: 12px;
}

.side-menu__child-link {
	position: relative;
	padding: 0;
	background: transparent;
	font-family: $font-avenir-next-condensed;
	font-size: 12px;
	font-weight: 400;
	line-height: 28px;
	letter-spacing: 0.06em;
	text-decoration: none;
	text-underline-position: under;
	color: var(--link-color, $brand-secondary);

	@include hover {
		text-decoration: underline;
		color: var(--link-color, $brand-primary);
	}
}

// Mobile side menu

.side-menu__mobile-top {
	display: flex;
	padding: 24px 21px 23px;
}

.js-side-menu-close {
	svg.icon {
		width: auto;
	}
}

.side-menu-desktop-header {
	display: none;
}

.side-menu__mobile-toggle-content {
	display: none;
	padding-top: 23px;
}

.side-menu__top-level-link {
	position: relative;
	padding: 0;
	background: transparent;
	font-family: $font-avenir-next-condensed;
	font-size: 15px;
	font-weight: 500;
	line-height: 1.67;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	text-decoration: none;
	color: var(--link-color, $brand-primary);
}

.side-menu__tab-content {
	margin-bottom: 32px;
}

.active {
	.accordion__item-toggler {
		&::before {
			transform: translateY(-50%);
		}
	}
}

.accordion__item-toggler {
	position: relative;
	display: block;
	width: 100%;
	padding-left: 0;
	padding-right: 20px;
	border: 0;
	background: transparent;
	text-align: left;
	text-transform: uppercase;
	color: $paragraph-primary;

	&::before,
	&::after {
		content: "";
		position: absolute;
		top: 50%;
		right: 0;
		width: 14px;
		height: 1px;
		border-radius: 8px;
		background-color: $paragraph-primary;
		transition-property: transform;
		transition-duration: 0.25s;
		transition-timing-function: ease-out;
	}

	&::before {
		transform: translateY(-50%) rotate(-90deg);
	}

	&::after {
		transform: translateY(-50%);
	}
}

.side-menu__mobile-links {
	display: flex;
	flex-direction: column;

	.side-menu__top-level-link {
		margin-bottom: 32px;
	}
}

.header__additional-links-mobile {
	border-bottom: 1px solid $text-field-border;
}

.header__store-links-mobile {
	margin-top: 32px;

	.side-menu__top-level-link {
		color: $brand-secondary;
	}
}


.side-menu__child-link-item.active .side-menu__child-link--button:after {
    transform: rotate(180deg)
}

.side-menu__child-link.side-menu__child-link--button {
    padding-right: 14px
}

.side-menu__child-link.side-menu__child-link--button:after {
    content: "";
    position: absolute;
    top: 13px;
    right: 0;
    display: flex;
    align-items: center;
    width: 0;
    height: 0;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    border-top: 4px solid;
    transition-property: transform;
    transition-duration: .25s;
    transition-timing-function: ease-out
}

.side-menu__childchild-links {
	padding-left: 40px;
	padding-top: 0;
}

.side-menu__childchild-link-item {
    margin-bottom: 10px
}

.side-menu__childchild-link {
    position: relative;
    padding: 0;
    background: transparent;
    font-family: AvenirNext LT Pro Cn,sans-serif;
    font-size: 12px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: .06em;
    text-decoration: none;
    text-underline-position: under;
    color: var(--link-color,grey)
}

.side-menu__childchild-link:hover {
	text-decoration: underline;
	color: var(--link-color,#000);
}

@include breakpoint(extra-small) {
	.side-menu__scroll-wrapper {
		padding: 24px 32px 32px;
	}

	.side-menu__link-item {
		padding-bottom: 21px;
	}

	.side-menu__links {
		padding-left: 0;
	}

	.side-menu__link {
		font-size: 13px;
		line-height: 1.67;
		letter-spacing: 0.08em;
	}

	.side-menu__child-links {
		padding-left: 0;
	}

	.side-menu__child-link {
		font-size: 14px;
		line-height: 20px;
		letter-spacing: 0.08em;
	}

	.side-menu__childchild-links {
        padding-left: 20px;
        padding-top: 15px
    }

	.side-menu__childchild-link {
        font-size: 14px;
        line-height: 20px;
        letter-spacing: .08em
    }

	.side-menu__mobile-header {
		display: none;
	}

	.side-menu-desktop-header {
		display: block;
	}

	.side-menu .side-menu__close-button {
		display: block;
	}

	.side-menu__item-toggle {
		display: none;
	}

	.side-menu__mobile-toggle-content {
		display: block;
		padding-top: 0;
	}


	.side-menu__tab-content {
		display: none;

		&.is-active {
			display: block;
		}
	}

	.side-menu__child-links-wrapper,
	.side-menu__grandchild-links-wrapper {
        display: none
    }

	.side-menu__child-link {
		&.side-menu__child-link--button:after {
			top: 10px
		}
    }
}

.side-menu__content .header__icon--search-mobile {
	.mobile-search-btn {
		display: none;
	}

	.search_form input {
		text-align: left;
	}
}
