.featured-categories {
	padding-top: 34px;
}

.featured-categories__container {
	max-width: 2247px;
	margin-left: auto;
}

@include breakpoint(medium) {
	.featured-categories {
		padding-top: 40px;
		padding-bottom: 35px;
	}
}

@include breakpoint(extra-large) {
	.featured-categories {
		padding-bottom: 51px;
	}
}

@include breakpoint(extra-wide) {
	.featured-categories {
		padding-top: 70px;
		padding-bottom: 60px;
	}
}

@media screen and (min-width: 2561px) {
	.featured-categories__container {
		margin-right: auto;
	}
}
