}

.account__container {
	max-width: 1310px;
}

.account__row {
	display: flex;
	flex-wrap: wrap;
}

.account__col {
	flex: 0 0 100%;
	max-width: 100%;
}

.account__col--menu {
	flex: 0 0 100%;
	max-width: 100%;
}

.account__col--content {
	flex: 0 0 100%;
	max-width: 100%;
}

@media (min-width: 768px) {
	.account__col--menu {
		flex: 0 0 30%;
