.help-center-content-click-and-meet {
	h1 {
		padding-bottom: 37px;
	}

	h2 {
		font-size: 16px;
		line-height: 1.37;
		letter-spacing: 0.08em;
		text-transform: uppercase;
	}

	ul {
		padding-inline-start: 24px;
		list-style: disc;
	}

	li {
		font-size: 14px;
		font-weight: normal;
		line-height: 1.43;
		letter-spacing: 0;
		color: #808080;
		font-style: normal;
	}

	a {
		text-transform: initial;
		text-decoration: underline;
		color: $black;
	}

	b,
	strong {
		font-weight: 500;
		color: $black;
	}
}

.help-center-content-click-and-meet__bottom-images {
	display: flex;
	flex-direction: column;
}

.help-center-content-click-and-meet-banners__col {
	width: 100%;
}

.help-center__gallery-container {
	.help-center-contact-info {
		display: flex;
		flex-direction: row;
		justify-content: center;
	}
}

.help-center-content-click-and-meet__image-wrapper {
	position: relative;
	overflow: hidden;
	display: block;
	width: 100%;
	margin-bottom: 16px;
	padding-top: 61%;
}

.help-center-content-click-and-meet__image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

@include breakpoint(medium) {
	.help-center-content-click-and-meet-banners__col {
		padding-left: 12px;
		padding-right: 12px;
	}

	.help-center-content-click-and-meet__bottom-images {
		margin-left: -12px;
		margin-right: -12px;
		flex-direction: row;
	}
}

@include breakpoint(large) {
	.help-center-content-click-and-meet {
		h1 {
			padding-bottom: 33px;
		}

		h4 {
			+ p {
				margin-top: 24px;
			}
		}

		h2 {
			+ p {
				margin-top: 4px;
			}
		}
	}

	.help-center__gallery-container {
		.help-center-contact-info {
			display: none;
		}

		.help-center-content {
			padding-bottom: 0;
		}
	}
}
