.brands {
	padding-top: 20px;
	padding-bottom: 40px;
}

.brands__row {
	display: flex;
	flex-direction: column;
}

.brands__col {
	width: 100%;

	&.brands__col--button {
		margin-top: 35px;
	}
}

.brands__logos {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	margin-left: -10px;
	margin-right: -10px;
}

.brands__logos-item {
	width: calc(33.33% - 20px);
	margin-left: 10px;
	margin-right: 10px;
}

.brands__image-wrapper {
	position: relative;
	overflow: hidden;
}

.brands__image {
	display: block;
	width: 100%;
	max-width: 106px;
	margin: auto;
}

.brands__link-wrapper {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.brands__button {
	display: block;
	width: 100%;
	margin: 0 auto;
}

@include breakpoint(medium) {
	.brands {
		padding-top: 36px;
		padding-bottom: 43px;
	}

	.brands__col {
		&.brands__col--button {
			margin-top: 20px;
		}
	}

	.brands__logos {
		margin-left: -16px;
		margin-right: -16px;
	}

	.brands__logos-item {
		width: calc(20% - 32px);
		margin-left: 16px;
		margin-right: 16px;

		&:nth-child(n + 6) {
			display: none;
		}
	}

	.brands__button {
		max-width: 253px;
	}
}

@include breakpoint(extra-large) {
	.brands {
		padding-top: 58px;
		padding-bottom: 92px;
	}
}

@include breakpoint(ultra-large) {
	.brands__row {
		align-items: center;
		flex-direction: row;
		margin-left: -30px;
		margin-right: -30px;
	}

	.brands__col {
		padding-left: 30px;
		padding-right: 30px;

		&.brands__col--button {
			width: 27%;
			margin-top: 0;
		}
	}

	.brands__logos-item {
		width: calc(12.5% - 32px);

		&:nth-child(-n + 8) {
			display: block;
		}
	}

	.brands__button {
		margin-right: 0;
	}
}

@include breakpoint(wide) {
	.brands__container {
		max-width: 2030px;
		padding: 0 80px;
	}

	.brands__logos-item {
		width: calc(9.09% - 32px);

		&:nth-child(-n + 11) {
			display: block;
		}
	}
}

@include breakpoint(extra-wide) {
	.brands {
		padding-top: 92px;
		padding-bottom: 120px;
	}
}
