.product-option {
	+ .product-option {
		margin-top: 22px;
	}
}

.product-option__label {
	display: inline-block;
	margin-bottom: 12px;
	font-family: $font-avenir-next-condensed;
	font-size: 12px;
	line-height: 1.5;
	text-transform: uppercase;
	color: $paragraph-primary;
}

.product-option__label--error {
	color: $state-error;
}

.product-option__select {
	.choices__list {
		padding-left: 0;
		padding-right: 0;
		color: $paragraph-primary;
	}

	.choices__list--single {
		.choices__item {
			padding-left: 0;
			padding-right: 0;
			border-bottom: 0;

			@include hover {
				background-color: transparent;
			}
		}
	}

	.choices__item {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		padding-left: 12px;
		padding-right: 12px;
		border-bottom: 1px solid #f0f1f2;
		transition-property: background-color, color;
		transition-duration: 0.25s;
		transition-timing-function: ease-out;

		&:last-child {
			border-bottom: 0;
		}

		&.disabled {
			color: $paragraph-secondary;

			&::after {
				content: "Out of Stock";
				opacity: 1;
				margin-left: auto;
				padding-left: 24px;
				background-image: url("data:image/svg+xml,%3Csvg width='14' height='11' viewBox='0 0 14 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M13.3879 0.815455H0.612058C0.60879 0.815421 0.605522 0.815421 0.602256 0.815455H0.589714C0.331981 0.815455 0.123047 1.02439 0.123047 1.28212V9.74366C0.123047 10.0014 0.331981 10.2103 0.589714 10.2103H13.4102C13.668 10.2103 13.8769 10.0014 13.8769 9.74366V1.28212C13.8769 1.02439 13.668 0.815455 13.4102 0.815455H13.3977C13.3944 0.815421 13.3911 0.815421 13.3879 0.815455ZM12.0051 1.74879L6.99997 5.5431L1.99487 1.74879H12.0051ZM12.9436 2.20854L7.28189 6.50059C7.11519 6.62696 6.88475 6.62696 6.71805 6.50059L1.05638 2.20854V9.27699H12.9436V2.20854Z' fill='%23CB4867'/%3E%3C/svg%3E%0A");
				background-repeat: no-repeat;
				background-position: 0 50%;
				font-size: 12px;
				text-align: right;
				color: $product-badge-sale;
				white-space: nowrap;
			}
			&:not(.is-highlighted) {
				display: none;
			}
		}

		.custom-select__label-region {
			margin-left: 8px;
			color: #808080;
			cursor: default;
			pointer-events: none;
		}
	}

	.choices__item--selectable {
		&.is-highlighted {
			background-color: #fafafa;
		}

		@include hover {
			background-color: #fafafa;
		}
	}
}

.product-option__swatches {
	display: grid;
	grid-template-columns: repeat(4, minmax(0, 1fr));
	grid-auto-rows: minmax(0, 1fr);
	grid-gap: 18px;

	&[data-swatches-displayed="4"] {
		grid-template-columns: repeat(4, minmax(0, 1fr));

		.product-option__swatches-item {
			&:nth-child(n + 4) {
				display: none;
			}

			&:nth-child(4n)::after {
				content: none;
			}

			&.product-option__swatches-item--load-more {
				display: block;
			}
		}
	}

	&[data-swatches-displayed="5"] {
		grid-template-columns: repeat(5, minmax(0, 1fr));

		.product-option__swatches-item {
			&:nth-child(n + 5) {
				display: none;
			}

			&:nth-child(5n)::after {
				content: none;
			}

			&.product-option__swatches-item--load-more {
				display: block;
			}
		}
	}

	&[data-swatches-displayed="6"] {
		grid-template-columns: repeat(6, minmax(0, 1fr));

		.product-option__swatches-item {
			&:nth-child(n + 6) {
				display: none;
			}

			&:nth-child(6n)::after {
				content: none;
			}

			&.product-option__swatches-item--load-more {
				display: block;
			}
		}
	}
}

.product-option__swatches-item {
	&::after {
		content: "";
		position: absolute;
		top: 0;
		right: -9px;
		display: block;
		width: 1px;
		height: 100%;
		background-color: $background-light-grey;
	}

	&:nth-child(1)::after {
		display: none;
	}

	&:nth-child(2)::before {
		content: "";
		position: absolute;
		top: 0;
		left: -9px;
		display: block;
		width: 1px;
		height: 100%;
		background-color: $background-light-grey;
	}

	&:nth-child(4n)::after {
		display: none;
	}

	&:nth-last-child(-n + 2)::after {
		display: none;
	}
}

.product-option__swatches-item--load-more {
	display: none;

	&::after {
		content: none;
	}

	.product-swatch__value {
		white-space: normal;
	}
}

.product-option__row {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-left: -5px;
	margin-right: -5px;
}

.product-option__col {
	padding-left: 5px;
	padding-right: 5px;
}

.product-option__col--link {
	flex-shrink: 0;
}

.product-option__text,
.product-option__link {
	display: inline-block;
	font-family: $font-primary;
	font-size: 11px;
	line-height: 1.45;
	color: $paragraph-primary;
}

.product-option__link {
	position: relative;
	padding: 0;
	background: none;
	text-decoration: none;

	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		display: block;
		width: 100%;
		height: 1px;
		background-color: $paragraph-primary;
		transition: width 0.25s ease;
	}

	@include hover {
		&::after {
			width: 0;
		}
	}
}

@include breakpoint(medium) {
	.product-option {
		+ .product-option {
			margin-top: 36px;
		}
	}
}

@include breakpoint(large) {
	.product-option {
		+ .product-option {
			margin-top: 22px;
		}
	}
}

@include breakpoint(extra-wide) {
	.product-option {
		+ .product-option {
			margin-top: 30px;
		}
	}
}
