.services-booking {
	padding-top: 30px;
	padding-bottom: 70px;
}

.services-booking__content {
	text-align: center;
}

.services-booking__text {
	font-size: 14px;
	line-height: 1.43;
	color: $paragraph-secondary;
}

.services-booking__button {
	width: 100%;
	max-width: 268px;
	margin-top: 30px;
	padding: 18px 16px;
	border-radius: 0;
	border: 1px solid $brand-primary;
	color: $brand-primary !important;

	@include hover {
		background-color: $brand-primary;
		color: $white !important;
	}
}

@include breakpoint(medium) {
	.services-booking {
		padding-top: 45px;
		padding-bottom: 110px;
	}

	.services-booking__text {
		max-width: 582px;
		margin-left: auto;
		margin-right: auto;
	}
}
