.about-us-brands {
	margin-top: 32px;
	margin-bottom: 22px;
}

.about-us-brands__wrapper {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	margin: 24px 0 20px;
}

.about-us-brands__title {
	text-align: center;
}

.about-us-brands__item {
	width: 58px;
}

.about-us-brands__button {
	display: block;
	width: 100%;
	margin: 0 auto;
	padding: 19px 16px 17px;
	border: 1px solid $black;
	font-size: 14px;
	font-weight: 500;
	line-height: 16px;
	text-align: center;
	text-transform: uppercase;
	text-decoration: none;
	color: $black;
}

@include breakpoint(large) {
	.about-us-brands__item {
		width: 106px;
	}
}

@include breakpoint(medium) {
	.about-us-brands__button {
		width: 263px;
		margin-bottom: 66px;
	}

	.about-us-brands__wrapper {
		margin: 33px 0;
	}

	.about-us-brands {
		margin-top: 72px;
		margin-bottom: 97px;
	}
}
