.footer-newsletter {
	margin-bottom: 42px;
	text-align: center;
}

.footer-newsletter__title {
	margin-bottom: 9px;

	font-size: 26px;
	line-height: 1.25;
}

.footer-newsletter__content {
	margin-bottom: 17px;

	font-size: 14px;
	line-height: 1.45;
}

.footer-newsletter__input {
	display: flex;
	align-items: center;

	border-bottom: 1px solid $black;

	.input {
		flex-grow: 1;
		height: 38px;

		padding-top: 10px;
		padding-bottom: 10px;
		padding-left: 0;

		border: none;
		font-size: 12px;
		color: $brand-secondary;
	}
}

.footer-newsletter__submit {
	margin-top: 8px;
	padding: 0;
	background-color: transparent;
	font-size: 12px;
	font-weight: 400;
	text-transform: uppercase;
	color: $black;
}

.footer-newsletter__error {
	@extend .error-message;
	text-align: left;
}

@include breakpoint(medium) {
	.footer-newsletter__content {
		margin-bottom: 30px;
	}

	.footer-newsletter__form {
		max-width: 302px;
		margin: 0 auto;
	}
}

@include breakpoint(large) {
	.footer-newsletter {
		margin-bottom: 44px;

		text-align: left;
	}

	.footer-newsletter__title {
		margin-bottom: 12px;

		font-size: 22px;
		line-height: 1.15;
	}

	.footer-newsletter__content {
		margin-bottom: 17px;
	}

	.footer-newsletter__form {
		max-width: unset;
	}

	.footer-newsletter__input {
		display: flex;
		align-items: center;

		border-bottom: 1px solid $black;

		.input {
			flex-grow: 1;
			height: 38px;

			padding-top: 10px;
			padding-bottom: 10px;
			padding-left: 0;

			border: none;
			font-size: 12px;
			color: $brand-secondary;
		}
	}

	.footer-newsletter__submit {
		margin-top: 8px;

		padding: 0;
		background-color: transparent;

		font-size: 12px;
		text-transform: uppercase;
	}

	.footer-newsletter__error {
		@extend .error-message;
	}
}

@include breakpoint(ultra-large) {
	.test {
		font-size: inherit;
	}
}
