.input-group {
	display: inline-block;

	&.input-group--floating-label {
		position: relative;

		.input-group__label {
			position: absolute;
			top: 50%;
			left: 12px;
			opacity: 0;
			margin-bottom: 0;
			font-size: 16px;
			color: $text-field-placeholder;
			transform: translateY(-50%);
			transition-property: opacity, font-size, transform;
			transition-duration: 0.25s;
			transition-timing-function: ease-out;
		}

		.input-group__input {
			&:not(:placeholder-shown) {
				padding-top: 22px;
				padding-bottom: 6px;

				~ .input-group__label {
					opacity: 1;
					font-size: 12px;
					transform: translateY(calc(-50% - 10px));
				}
			}
		}
	}

	.input-group__label {
		margin-bottom: 6px;
		font-size: 14px;
		font-weight: 400;
		color: $grey-middle;
	}

	+ .input-group {
		margin-top: 20px;
	}
}
