.slider {
	&.slider--arrows-inverse {
		.slider__arrow {
			background-color: $brand-primary;
			color: $white;
		}
	}
}

.slider__container {
	position: relative;
	padding-left: 20px;
	padding-right: 20px;

	&:not(.initialized) {
		.slider__wrapper {
			display: flex;
		}

		.slider__arrow {
			visibility: hidden;
			opacity: 0;
		}
	}
}

.slider__slide {
	width: 71.95%;
	margin-right: 16px;

	&:last-child {
		margin-right: 0;
	}
}

.slider__arrow {
	position: absolute;
	top: 50%;
	z-index: 1;
	display: none;
	opacity: 0.8;
	width: 44px;
	height: 44px;
	margin: 0 32px;
	padding: 0;
	background-color: $white;
	text-align: center;
	color: $brand-primary;
	transform: translateY(-50%);
	transition: all 0.25s ease-out;

	&.slider__arrow--prev {
		left: 0;

		svg {
			transform: rotate(180deg);
		}
	}

	&.slider__arrow--next {
		right: 0;
	}

	&:disabled {
		visibility: hidden;
		opacity: 0;
	}

	svg {
		display: inline-block;
		vertical-align: middle;
		max-width: 6px;

		path {
			fill: currentColor;
		}
	}

	@include hover {
		opacity: 1;
	}
}

.slider__pagination {
	position: static;
	margin-top: 29px;
	font-size: 0;
	text-align: center;

	.swiper-pagination-bullet {
		opacity: 1;
		width: 30px;
		height: 3px;
		margin: 4px !important;
		border-radius: 0;
		background-color: $border-primary;
		transition: background-color 0.25s ease-out;

		&.swiper-pagination-bullet-active {
			background-color: $brand-primary;
		}
	}
}

@include breakpoint(medium) {
	.slider__container {
		padding-left: 32px;
		padding-right: 32px;
	}

	.slider__slide {
		width: 29.7%;
	}
}

@include breakpoint(extra-large) {
	.slider__slide {
		width: 22.61%;
	}

	.slider__arrow {
		display: block;
	}

	.slider__pagination {
		display: none;
	}
}

@include breakpoint(extra-wide) {
	.slider__slide {
		width: 21.5%;
		margin-right: 20px;

		&:last-child {
			margin-right: 0;
		}
	}
}
