.product {
	padding-bottom: 64px;
}

.product__container {
	padding-left: 0;
	padding-right: 0;
}

.product__breadcrumbs {
	padding: 20px 32px;
}

.product__row {
	display: flex;
	flex-wrap: wrap;
}

.product__col {
	flex: 0 0 100%;
	max-width: 100%;
}

@include breakpoint(medium) {
	.product {
		padding-bottom: 64px;
	}

	.product__container {
		padding-left: 32px;
		padding-right: 32px;
	}

	.product__col--media {
		flex: 0 0 55%;
		max-width: 55%;
	}

	.product__col--meta {
		flex: 0 0 45%;
		max-width: 45%;
	}

	.product__breadcrumbs {
		padding-left: 0;
		padding-right: 0;
	}
}

@include breakpoint(large) {
	.product__col--media {
		flex: 0 0 63.37%;
		max-width: 63.37%;
	}

	.product__col--meta {
		flex: 0 0 36.63%;
		max-width: 36.63%;
	}
}

@include breakpoint(extra-large) {
	.product {
		padding-bottom: 103px;
	}

	.product__breadcrumbs {
		padding-top: 4px;
		padding-bottom: 4px;
	}
}

@include breakpoint(extra-wide) {
	.product {
		padding-bottom: 200px;
	}

	.product__col--media {
		flex: 0 0 62.34%;
		max-width: 62.34%;
	}

	.product__col--meta {
		flex: 0 0 37.66%;
		max-width: 37.66%;
	}
}
