.product-description__accordion-item {
	&:last-child {
		.product-description__accordion-item-content {
			padding-bottom: 0;
		}
	}
}

.product-description__accordion-item-toggler {
	font-family: $font-avenir-next-condensed;
}

.product-description__accordion-item-content {
	padding-top: 10px;
	padding-bottom: 10px;
}

.product-description__list {
	list-style-type: none;
	color: $paragraph-primary;
}

.product-description__list--bullets {
	font-size: 14px;
	line-height: 1.43;
	color: $paragraph-primary;
	list-style-type: disc;
}

.product-description__list--bullets .product-description__list-item {
	position: relative;
	padding-left: 1em;
	font-size: 12px;
	line-height: inherit;
	color: inherit;
	p {
		font-size: 12px;
	}
}
.product-description__list--bullets .product-description__list-item::before {
	content: "•";
	position: absolute;
	top: 1px;
	left: 0;
	font-size: 12px;
}

.product-description__list--material {
	color: $paragraph-secondary;
}

.product-description__list-item {
	display: flex;
	align-items: center;

	+ .product-description__list-item {
		margin-top: 10px;
	}
}

.product-description__list-item-title {
	font-size: 12px;
	line-height: 1.45;
	letter-spacing: 0.2px;
	color: inherit;
}

.product-description__list-item-icon {
	margin-right: 17px;

	svg {
		display: block;
		width: 20px;

		path {
			fill: currentColor;
		}
	}
}

.product-description__list-item-tooltip {
	position: relative;
	width: 10px;
	height: 10px;
}

.product-description__list-item-tooltip-icon {
	position: absolute;
	top: -5px;

	svg {
		display: block;
		width: 10px;
	}

	@include hover {
		~ .product-description__list-item-tooltip-content {
			visibility: visible;
		}
	}
}

.product-description__list-item-tooltip-content {
	position: absolute;
	top: -15px;
	left: 100%;
	display: inline-block;
	visibility: hidden;
	margin-left: 2px;
	padding: 3px 10px;
	border: 0;
	border-radius: 1px;
	background-color: #f0f1f2;
	box-shadow: 0 3px 6px rgba(0, 0, 0, 0.03);
	font-family: $font-avenir-next-condensed-light;
	font-size: 7px;
	font-weight: 500;
	line-height: 1.14;
	letter-spacing: 0.06em;
	text-align: center;
	text-transform: uppercase;
	color: $paragraph-primary;
}

.product-description__material {
	font-size: 12px;
	line-height: 1.5;
	color: $paragraph-primary;

	+ .product-description__list {
		margin-top: 12px;
	}
}
