.collection__banner {
	margin-bottom: 60px;

	.boost-pfs-action-list-wrapper {
		display: none;
	}
}

.collection__banner--with-under-content {
	.collection-banner {
		display: block;
		height: auto;
	}

	.collection-banner__media {
		position: relative;
	}

	&[data-banner-width="2"] {
		.collection-banner__media-item {
			padding-top: 57%;
		}
	}

	&[data-banner-width="3"] {
		.collection-banner__media-item {
			padding-top: 39%;
		}
	}
}

.collection-banner {
	position: relative;
	display: flex;
	width: 100%;
	height: 100%;
}

.collection-banner__media {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.collection-banner__media-item {
	position: relative;
	overflow: hidden;
	height: 100%;
	min-height: 350px;
}

.collection-banner__image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: center;
}

/*---- Video player----*/

.collection-banner__video {
	position: relative;
	width: 100%;
	height: 100%;

	@include hover {
		.collection-banner__video-button--paused {
			opacity: 1;
		}
	}
}

.collection-banner__video-player {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: $black;
}

.collection-banner__video-controls {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 5;
	opacity: 1;
	width: 100%;
	height: 100%;
}

.collection-banner__video-button {
	position: absolute;
	top: 50%;
	left: 50%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	opacity: 0.7;
	width: 60px;
	height: 60px;
	background-color: $white;
	transform: translate(-50%, -50%);

	.icon-pause {
		display: none;
	}
}

.collection-banner__video-button--paused {
	opacity: 0;
	transition: 0.3s all ease;

	.icon-pause {
		display: block;
	}

	.icon-play-filled {
		display: none;
	}
}

.collection-banner__content {
	display: flex;
	flex-direction: column;
	width: 100%;
	height: 100%;
	padding: 10px;

	&[data-position="under-left"] {
		justify-content: flex-start;
		align-items: flex-start;
	}

	&[data-position="under-center"] {
		justify-content: center;
		align-items: center;
	}

	&[data-position="under-right"] {
		justify-content: flex-end;
		align-items: flex-end;
	}

	&[data-position="over-left-middle"] {
		justify-content: center;
		align-items: flex-start;
	}

	&[data-position="over-center-middle"] {
		justify-content: center;
		align-items: center;
	}

	&[data-position="over-right-middle"] {
		justify-content: center;
		align-items: flex-end;
	}

	&[data-position="over-left-bottom"] {
		justify-content: flex-end;
		align-items: flex-start;
	}

	&[data-position="over-center-bottom"] {
		justify-content: flex-end;
		align-items: center;
	}

	&[data-position="over-right-bottom"] {
		justify-content: flex-end;
		align-items: flex-end;
	}

	&[data-position="over-left-top"] {
		justify-content: flex-start;
		align-items: flex-start;
	}

	&[data-position="over-center-top"] {
		justify-content: flex-start;
		align-items: center;
	}

	&[data-position="over-right-top"] {
		justify-content: flex-start;
		align-items: flex-end;
	}
}

.collection-banner__content-inner {
	position: relative;
	z-index: 5;
}

.collection-banner__link {
	text-decoration: none;
}

.collection-banner__title {
	color: $black;
}
