.account-addresses {
	.account-content__header {
		margin-top: 12px;
	}

	.account-content__header-wrapper {
		margin-bottom: 44px;
	}
}

.account-addresses__header-icon {
	width: 36px;
	height: 36px;

	.icon {
		width: 100%;
		height: 100%;
	}
}

.account-addresses__row {
	margin-bottom: 40px;
}

.account-addresses__row-title {
	margin-bottom: 12px;
	font-size: 16px;
	font-weight: bold;
	line-height: 1.37;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	font-style: normal;
}

.account-addresses__field {
	font-size: 14px;
	font-weight: normal;
	line-height: 1.43;
	color: $brand-secondary;
	font-style: normal;

	p {
		font-size: 14px;
		font-weight: normal;
		line-height: 1.43;
		color: $brand-secondary;
		font-style: normal;
	}
}

.account-addresses__field--empty {
	max-width: 217px;
}

.account-addresses__col {
	+ .account-addresses__col {
		margin-top: 32px;
	}
}

.account-addresses__links-container {
	display: flex;
	flex-direction: row;
}

.account-addresses__link {
	background-color: transparent;
}

.account-addresses__link--padding {
	+ .account-addresses__link--padding {
		margin-left: 20px;
	}
}

.account-addresses__add-button-container {
	max-width: 217px;
}

.account-addresses__button {
	font-weight: 500;
}

.account-addresses__button--small {
	width: 100%;
}

@include breakpoint(small) {
	.account-content__content-container {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
	}
}

@include breakpoint(medium) {
	.account-addresses {
		.account-content__header {
			margin-top: 0;
			margin-bottom: 0;
		}

		.account-content__header-wrapper {
			margin-bottom: 25px;
		}
	}

	.account-addresses__header-icon {
		width: 48px;
		height: 48px;
	}

	.account-addresses__row {
		margin-bottom: 40px;
	}

	.account-addresses__button--wide {
		max-width: 216px;
	}

	.account-addresses__content {
		display: flex;
		flex-direction: row;
		flex-basis: 60%;
		flex-wrap: wrap;
	}

	.account-content__content-container {
		display: block;
	}

	.account-addresses__button--small {
		max-width: 217px;
		padding-top: 10px;
		padding-bottom: 8px;
	}
}

@include breakpoint(large) {
	.account-addresses__col {
		+ .account-addresses__col {
			margin-top: 20px;
		}

		&:nth-child(-n + 2) {
			margin-top: 0;
		}

		&:nth-child(even) {
			margin-left: 50px;
		}
	}
}
