.complementary-products {
	padding-bottom: 50px;
}

.complementary-products__row {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
}

.complementary-products__col {
	flex: 0 0 100%;
	max-width: 100%;
}

.complementary-products__advisor-image-wrapper {
	position: relative;
	overflow: hidden;
	width: 100%;
	height: 100%;
	padding-top: 117.61%;
}

.complementary-products__advisor-image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.complementary-products__advisor-role {
	margin-top: 13px;
	font-family: $font-avenir-next-condensed;
	font-size: 12px;
	line-height: 1.5;
	text-transform: uppercase;
}

.complementary-products__advisor-name {
	margin-top: 6px;
	font-family: $font-avenir-next-condensed;
	font-size: 20px;
	font-weight: 700;
	line-height: 1.1;
	letter-spacing: 0.07em;
	text-transform: uppercase;
}

.complementary-products__advisor-quote {
	margin-top: 0;
	font-family: $font-avenir-next-cyr;
	font-size: 12px;
	font-weight: 400;
	color: rgba($paragraph-primary, 0.5);
}

.complementary-products__help {
	display: flex;
	align-items: center;
	margin-top: 21px;
}

.complementary-products__help-label {
	margin-right: 10px;
	font-family: $font-avenir-next-condensed;
	font-size: 16px;
	font-weight: 700;
	text-transform: uppercase;
}

@include breakpoint(ultra-small) {
	.complementary-products__advisor {
		padding-left: 32px;
		padding-right: 32px;
	}
}

@include breakpoint(large) {
	.complementary-products__col--advisor {
		flex: 0 0 32%;
		max-width: 32%;
	}

	.complementary-products__col--product {
		flex: 0 0 68%;
		max-width: 68%;
	}

	.complementary-products__advisor {
		padding-left: 0;
		padding-right: 64px;
	}

	.complementary-products__advisor-name {
		font-size: 24px;
		line-height: 1.16;
		letter-spacing: 0.08em;
	}
}

@include breakpoint(extra-large) {
	.complementary-products {
		padding-bottom: 120px;
	}
}

@include breakpoint(ultra-large) {
	.complementary-products__advisor {
		padding-right: 90px;
	}
}

@include breakpoint(wide) {
	.complementary-products__advisor {
		padding-right: 218px;
	}
}
