.page-404-featured-categories {
	padding-top: 35px;
	padding-bottom: 70px;
}

.page-404-featured-categories__container {
	max-width: 1332px;
}

.page-404-featured-categories__cards-wrapper {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin-top: -30px;
}

.page-404-featured-categories__card {
	flex: 0 0 100%;
	max-width: 100%;
	margin-top: 30px;
}

@include breakpoint(medium) {
	.page-404-featured-categories__container {
		max-width: 1356px;
	}

	.page-404-featured-categories__cards-wrapper {
		margin-left: -8px;
		margin-right: -8px;
	}

	.page-404-featured-categories__card {
		flex: 0 0 50%;
		max-width: 50%;
		padding-left: 8px;
		padding-right: 8px;
	}
}

@include breakpoint(large) {
	.page-404-featured-categories {
		padding-top: 30px;
		padding-bottom: 107px;
	}

	.page-404-featured-categories__card {
		flex: 0 0 25%;
		max-width: 25%;
	}
}
