.product-swatch {
	position: relative;
}

.product-swatch__radio {
	display: none;

	&:checked + .product-swatch__label,
	&.checked + .product-swatch__label {
		border-color: $border-primary;
	}

	&:disabled + .product-swatch__label,
	&.disabled + .product-swatch__label {
		opacity: 0.25;
		pointer-events: none;
	}
}

.product-swatch__label {
	position: relative;
	display: block;
	border: 1px solid transparent;
	font-family: $font-avenir-next-condensed-light;
	font-size: 11px;
	line-height: 1.8;
	text-align: center;
	text-transform: uppercase;
	color: $paragraph-primary;
	cursor: pointer;
	transition-property: opacity, border-color, color;
	transition-duration: 0.25s;
	transition-timing-function: ease-out;

	@include hover {
		border-color: $background-light-grey;
	}
}

.product-swatch__wrapper {
	position: relative;
	overflow: hidden;
	display: block;
	width: 100%;
	padding-top: 130%;
}

.product-swatch__image {
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.product-swatch__value {
	position: absolute;
	top: 50%;
	left: 0;
	right: 0;
	overflow: hidden;
	padding: 5px;
	text-overflow: ellipsis;
	white-space: nowrap;
	transform: translateY(-50%);
}

@include breakpoint(medium) {
	.product-swatch__wrapper {
		padding-top: 134%;
	}
}

@include breakpoint(large) {
	.product-swatch__wrapper {
		padding-top: 133.82%;
	}
}
