.cart-products {
	width: 100%;
}

.cart-products__row {
	+ .cart-products__row {
		.minicart-product {
			border-top: 0;
		}
	}
}

.cart-products__row--heading {
	display: none;
}

.cart-products__col {
	width: 100%;
}

.cart-products__col--product {
	width: 43%;
}

.cart-products__col--quantity {
	width: 16%;
}

.cart-products__col--availability {
	width: 15%;
}

.cart-products__col--subtotal {
	width: 15%;
}

.cart-products__col--actions {
	width: 1%;
}

.cart-products__col--desktop {
	display: none;
	padding-right: 20px;
	vertical-align: middle;
}

.cart-products__heading-label {
	padding: 17px 16px;
	font-family: $font-avenir-next-condensed-light;
	font-size: 12px;
	line-height: 1.5;
	letter-spacing: 0.06em;
	text-transform: uppercase;
	color: $paragraph-primary;
}

.cart-products__item {
	display: flex;
	align-items: center;
}

.cart-products__item-col--image {
	flex-shrink: 0;
	width: 100%;
	max-width: 108px;
}

.cart-products__item-col--content {
	width: 100%;
}

.cart-products__item-image-wrapper {
	position: relative;
	display: block;
	width: 100%;
	padding-top: 133.33%;
}

.cart-products__item-image {
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.cart-products__item-content {
	padding-top: 10px;
	padding-bottom: 10px;
	padding-left: 12px;
}

.cart-products__item-vendor {
	font-size: 14px;
	font-weight: 400;
	line-height: 1.43;
	color: $paragraph-primary;
}

.cart-products__item-title {
	display: inline-block;
	font-size: 12px;
	font-weight: 400;
	line-height: 1.45;
	text-decoration: none;
	color: $paragraph-secondary;
	transition-property: color;
	transition-duration: 0.25s;
	transition-timing-function: ease-out;

	@include hover {
		color: $paragraph-primary;
	}
}

.cart-products__item-options {
	margin-top: 4px;
}

.cart-products__item-option {
	font-size: 12px;
	line-height: 1.5;
	color: $paragraph-secondary;

	+ .cart-products__item-option {
		margin-top: 0;
	}
}

.cart-products__item-price {
	margin-top: 4px;
}

.cart-products__item-price-value {
	font-size: 11px;
	font-weight: 400;
	line-height: 1.45;
	letter-spacing: 0.2px;
	color: $paragraph-primary;
}

.cart-products__item-price-value--compare-price {
	margin-right: 12px;
	text-decoration: line-through;
	color: $product-old-price;
}

.cart-products__item-price-value--sale {
	color: $product-sale;
}

.cart-products__item-actions {
	margin-top: 8px;
}

.cart-products__item-actions-button {
	display: inline-block;
	margin: 0;
	padding: 0;
	background: none;
	color: $brand-primary;
	transition: 0.25s color ease-out;

	svg {
		display: block;
		width: 14px;

		path {
			fill: currentColor;
		}
	}

	@include hover {
		color: $brand-secondary;
	}
}

.cart-products__quantity-selector-button,
.cart-products__quantity-selector-input {
	padding-top: 12px;
	padding-bottom: 10px;
}

.cart-products__price-value {
	font-size: 14px;
	font-weight: 400;
	line-height: 1.43;
	color: $paragraph-primary;
}

.cart-products__price-unit-value {
	color: $paragraph-secondary;
}

.cart-products__actions {
	display: flex;
	justify-content: center;
}

.cart-products__action-button {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 24px;
	height: 24px;
	padding: 0;
	background: none;
	color: $brand-primary;
	transition: color 0.25s ease-out;

	svg {
		display: block;
		width: 12.8px;

		path {
			fill: currentColor;
		}
	}

	@include hover {
		color: $brand-secondary;
	}
}

.cart-availability__key-item {
	display: flex;
	align-items: center;
}

.cart-availability__key-circle {
	display: block;
	min-width: 10px;
	height: 10px;
	margin-right: 7px;
	border-radius: 50%;

	.cart-availability__key--stock & {
		background: #81cb9f;
	}

	.cart-availability__key--out & {
		background: #ff5942;
	}
}

.cart-availability__key-item-title {
	font-size: 14px;
	line-height: 1.43;
}

.cart__notify {
	margin: 19px 0;
}

.cart__notify-item {
	display: flex;
	align-items: center;
}

.cart__notify-circle {
	display: block;
	min-width: 20px;
	height: 20px;
	margin-right: 24px;
	border-radius: 50%;

	.cart__notify-item--green & {
		background: #81cb9f;
	}

	.cart__notify-item--red & {
		background: #ff5942;
	}

	.cart__notify-item--orange & {
		background: #fde7ae;
	}
}

.cart__notify-text {
	font-family: $font-primary;
	font-size: 11px;
	line-height: 1.45;
	letter-spacing: 0.2px;
	color: $brand-secondary;
}

@include breakpoint(large) {
	.cart-products__row {
		border: 1px solid $border-primary;
		border-bottom: 0;

		&:last-of-type {
			border-bottom: 1px solid $border-primary;
		}
	}

	.cart-products__row--heading {
		display: table-row;
	}

	.cart-products__col--desktop {
		display: table-cell;
	}

	.cart-products__col--mobile {
		display: none;
	}
}
