.denim-info-section {
	margin-bottom: 60px;
}

.denim-info {
	display: flex;
	justify-content: space-between;
	padding: 40px 20px;
	flex-direction: column;
	background: #c7d5cc;
}

.denim-info__img {
	margin-top: 0;
	padding-left: 54px;

	img {
		user-select: none;
	}
}

.denim-info-textarea {
	margin: 35px auto 0;
	max-width: 450px;
}

.denim-info-textarea__title {
	font-family: $font-avenir-next-condensed;
	font-size: 26px;
	font-style: normal;
	font-weight: bold;
	line-height: 32px;
	letter-spacing: 0.06em;
	text-transform: uppercase;
	color: $black;
}

.denim-info-list {
	margin-top: 30px;
}

.denim-info-list img {
	margin-right: 18px;
	user-select: none;
}

.denim-info-list li {
	display: flex;
	align-items: center;
}

.denim-info-list li:not(:last-of-type) {
	margin-bottom: 26px;
}

.denim-info__link {
	width: 100%;
	margin-top: 30px;
}

.denim-info__link.denim-info__link--transparent {
	background: transparent;
}

@include breakpoint(medium) {
	.denim-info-section {
		margin-bottom: 87px;
	}

	.denim-info {
		padding: 46px 60px 65px;
		flex-direction: row;
	}

	.denim-info-textarea {
		margin: unset;
	}

	.denim-info-list {
		margin-top: 52px;
	}

	.denim-info__link.denim-info__link--transparent {
		width: auto;
		min-width: 257px;
		margin-top: 55px;
	}

	.denim-info-textarea__title {
		font-size: 30px;
		line-height: 35px;
	}
}
