.customer-popup {
	position: absolute;
	top: 25px;
	right: -45px;
	z-index: 15;
	display: none;
	min-width: 390px;
	padding: 30px;
	background-color: $white;
	box-shadow: 0 4px 9px rgba(0, 0, 0, 0.25);

	&::before {
		content: "";
		position: absolute;
		top: -6px;
		right: 49px;
		width: 10px;
		height: 10px;
		background-color: $white;
		transform: rotate(45deg);
		-webkit-filter: drop-shadow(-5px -8px 7px rgba(0, 0, 0, 0.25));
		filter: drop-shadow(-5px -8px 7px rgba(0, 0, 0, 0.25));
	}

	&::after {
		content: "";
		position: absolute;
		top: -15px;
		left: 0;
		width: 100%;
		height: 15px;
	}
}

.customer-popup__link-list {
	margin: 22px 0;
}

.customer-popup__link-list-item {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 8px 0;
}

.customer-popup__link-list-item-link {
	font-family: $font-primary;
	font-size: 16px;
	text-decoration: none;
}

.customer-popup__link-list-count {
	display: block;
	width: 20px;
	height: 20px;
	background-color: $black;
	font-size: 14px;
	text-align: center;
	color: $white;
	border-radius: 50%;
}

.customer-popup__button {
	width: 100%;
}

.customer-popup__link-list--no-user {
	margin-top: 28px;
	text-align: center;
}

.customer-popup__link-list--no-user-item {
	margin-top: 20px;
	text-transform: uppercase;
}

.customer-popup__link-list--no-user-link {
	font-family: $font-avenir-next;
	font-weight: 400;
	text-underline-position: under;
}
