.minicart-product {
	border: 1px solid $border-primary;
}

.minicart-product__row {
	display: flex;
	height: 100%;
}

.minicart-product__col {
	position: relative;
}

.minicart-product__col--image {
	flex-shrink: 0;
	max-width: 108px;
	width: 100%;
}

.minicart-product__col--content {
	display: flex;
	flex-direction: column;
	justify-content: center;
	width: 100%;
}

.minicart-product__image-wrapper {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.minicart-product__image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.minicart-product__content {
	position: relative;
	padding: 12px;
}

.minicart-product__actions {
	position: absolute;
	top: 7px;
	right: 11px;
}

.minicart-product__actions-button {
	display: inline-block;
	margin: 0;
	padding: 0;
	background: none;
	color: $brand-primary;
	transition: 0.25s color ease-out;

	+ .minicart-product__actions-button {
		margin-left: 9px;
	}

	svg {
		display: block;
		width: 14px;

		path {
			fill: currentColor;
		}
	}

	@include hover {
		color: $brand-secondary;
	}
}

.minicart-product__vendor {
	font-size: 14px;
	font-weight: 400;
	line-height: 1.43;
	color: $paragraph-primary;
}

.minicart-product__title {
	display: inline-block;
	font-size: 12px;
	font-weight: 400;
	line-height: 1.45;
	text-decoration: none;
	color: $paragraph-secondary;
	transition-property: color;
	transition-duration: 0.25s;
	transition-timing-function: ease-out;

	@include hover {
		color: $paragraph-primary;
	}
}

.minicart-product__options {
	margin-top: 7px;
}

.minicart-product__option {
	font-size: 11px;
	line-height: 1.45;
	letter-spacing: 0.2px;
	color: $paragraph-secondary;

	+ .minicart-product__option {
		margin-top: 0;
	}
}

.minicart-product__footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 6px;
	margin-left: -6px;
	margin-right: -6px;
}

.minicart-product__footer-col {
	padding-left: 6px;
	padding-right: 6px;
}

.minicart-product__quantity-selector-input,
.minicart-product__quantity-selector-button {
	padding-top: 7px;
	padding-bottom: 5px;
}

.minicart-product__price {
	text-align: right;
}

.minicart-product__price-value {
	font-size: 11px;
	font-weight: 400;
	line-height: 1.45;
	letter-spacing: 0.2px;
	color: $paragraph-primary;
}

.minicart-product__price-unit-value {
	color: $paragraph-secondary;
}

.minicart-product__price-value--compare-price {
	text-decoration: line-through;
	color: $product-old-price;
}

.minicart-product__price-value--sale {
	color: $product-sale;
}

.minicart-product__form {
	margin-top: 7px;
}

.minicart-product__form-row {
	display: flex;
	align-items: flex-end;
	margin-left: -6px;
	margin-right: -6px;

	+ .minicart-product__form-row {
		margin-top: 7px;
	}
}

.minicart-product__form-col {
	padding-left: 6px;
	padding-right: 6px;
}

.minicart-product__form-col--large {
	flex: 0 0 64%;
	max-width: 64%;
}

.minicart-product__form-col--small {
	flex: 0 0 36%;
	max-width: 36%;
}

.minicart-product__form-option-values {
	.choices {
		.choices__inner {
			min-height: unset;
			padding-top: 6px;
			padding-bottom: 6px;
		}

		&[data-type*="select-one"] {
			.choices__inner {
				padding-top: 6px;
				padding-bottom: 6px;
			}
		}

		.choices__list {
			padding-left: 0;
			padding-right: 0;
			color: $paragraph-primary;
		}

		.choices__list--single {
			.choices__item {
				padding-left: 0;
				padding-right: 0;
				border-bottom: 0;

				@include hover {
					background-color: transparent;
				}
			}
		}

		.choices__item {
			padding-left: 12px;
			padding-right: 12px;
			border-bottom: 1px solid #f0f1f2;
			transition-property: background-color;
			transition-duration: 0.25s;
			transition-timing-function: ease-out;

			&:last-child {
				border-bottom: 0;
			}

			&.disabled {
				color: $paragraph-secondary;
				cursor: default;
				pointer-events: none;
			}
		}

		.choices__item--selectable {
			&.is-highlighted {
				background-color: #fafafa;
			}

			@include hover {
				background-color: #fafafa;
			}
		}
	}
}

.minicart-product__form-button {
	position: relative;
	overflow: hidden;
	display: block;
	padding-top: 9px;
	padding-bottom: 7px;
	white-space: nowrap;
	text-overflow: ellipsis;
}

.minicart-product__additional-fields {
	margin-top: 15px;
}

.cart-products__col--availability {
	display: flex;
	justify-content: space-between;
}
