.career-one-department-list {
	padding-bottom: 20px;
}

.career-one-department-list__heading {
	padding-bottom: 24px;
	font-size: 26px;
	line-height: 1.23;
	text-align: center;
}

.career-one-department-list__col {
	padding-bottom: 24px;
}

.career-one-department-list__row {
	text-decoration: none;
}

.career-one-department-list__item {
	border-bottom: 1px solid $border-primary;
	margin-bottom: 16px;
}

.career-one-department-list__item-title {
	padding-bottom: 4px;
	font-size: 18px;
	line-height: 1.34;
	letter-spacing: 0.07em;
	text-align: left;
	text-transform: uppercase;
	max-width: 220px;
}

.career-one-department-list__item-date {
	font-size: 12px;
	font-weight: normal;
	line-height: 1.5;
	text-align: right;
	color: $brand-secondary;
	font-style: normal;
}

.career-one-department-list__location {
	font-family: $font-avenir-next-condensed;
	font-size: 14px;
	font-weight: 500;
	line-height: 1.43;
	font-style: normal;
	letter-spacing: 0.09em;
}

.career-one-department-list__upper-line {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.career-one-department-list__bottom-line {
	padding-bottom: 16px;
}

.career-one-department-list__bottom-content-title {
	padding-bottom: 24px;
	font-size: 20px;
	line-height: 1.1;
	letter-spacing: 0.07em;
	text-align: center;
	text-transform: uppercase;
}

.career-one-department-list__bottom-content-text {
	p {
		font-size: 14px;
		font-weight: normal;
		line-height: 1.43;
		color: $brand-secondary;
		font-style: normal;
	}
}

@include breakpoint(medium) {
	.career-one-department-list {
		padding-bottom: 60px;
	}

	.career-one-department-list__container {
		margin-left: auto;
		margin-right: auto;
		padding-left: 0;
		padding-right: 0;
		max-width: 788px;
	}

	.career-one-department-list__heading {
		padding-bottom: 40px;
	}

	.career-one-department-list__row {
		@include hover {
			.career-one-department-list__item-title {
				color: $brand-secondary;
				transition-property: color;
				transition-duration: 0.25s;
				transition-timing-function: ease-out;
			}
		}
	}

	.career-one-department-list__col {
		padding-bottom: 44px;
	}

	.career-one-department-list__item-title {
		font-size: 20px;
		line-height: 1.3;
		max-width: unset;
	}

	.career-one-department-list__bottom-content-title {
		padding-bottom: 24px;
		font-size: 24px;
		line-height: 1.17;
		letter-spacing: 0.08em;
	}

	.career-one-department-list__bottom-content-text {
		p {
			font-weight: 500;
		}
	}
}
