.account {
	padding-top: 20px;
	padding-bottom: 60px;
}

.account__container {
	max-width: 1310px;
}

.account__row {
	display: flex;
	flex-wrap: wrap;
}

.account__col {
	flex: 0 0 100%;
	max-width: 100%;
}

.account__col--menu {
	flex: 0 0 100%;
	max-width: 100%;
}

.account__col--content {
	flex: 0 0 100%;
	max-width: 100%;
}

@include breakpoint(medium) {
	.account__col--menu {
		flex: 0 0 30%;
		max-width: 30%;
		margin-right: 5%;
	}

	.account__col--content {
		flex: 0 0 65%;
		max-width: 65%;
	}
}

@include breakpoint(large) {
	.account__row {
		flex-wrap: unset;
		flex-direction: row;
	}

	.account__col--menu {
		max-width: 215px;
	}

	.account__col--content {
		flex: 0 0 70%;
		max-width: calc(100% - 215px);
	}
}

@include breakpoint(extra-large) {
	.account__col--content {
		flex: 0 0 72.45%;
	}
}
