.product-rating {
	.ruk_rating_snippet {
		display: flex;
		align-items: center;
		line-height: 1;
		color: #414b56;

		i,
		[class^="ruk-icon-"] {
			font-size: 9px;
			line-height: 1;
			letter-spacing: 4px;
		}

		.ruk-rating-snippet-count {
			bottom: unset;
			margin-left: 5px;
			font-family: $font-primary;
			font-size: 8px;
			line-height: 1;
			letter-spacing: 0.2px;
			color: $paragraph-secondary;
		}
	}
}
