.people-behind {
	padding-top: 44px;
	padding-bottom: 61px;
}

.people-behind__items-wrapper {
	&.people-behind__items-wrapper--desktop {
		display: none;
	}
}

.people-behind__container {
	max-width: 575px;

	&.container--slider {
		padding-left: calc(calc(100% - 645px) / 2);
		padding-right: calc(calc(100% - 645px) / 2);

		max-width: unset;
	}
}

.people-behind__title {
	margin-bottom: 24px;

	font-size: 26px;
	text-align: center;
}

.people-behind__row {
	flex-wrap: wrap;
}

.people-behind__item {
	display: flex;
	flex-direction: column;

	margin-right: 25px;

	&.people-behind__item--large-bottom {
		.people-behind__item-row {
			&.people-behind__item-row--first {
				order: 2;

				.people-behind__item-block-column {
					&.people-behind__item-block-column--rectangle {
						order: 2;
					}

					&.people-behind__item-block-column--square {
						order: 1;
					}
				}
			}

			&.people-behind__item-row--second {
				order: 1;
			}
		}
	}
}

.people-behind__item-row {
	position: relative;

	padding-top: 54.15%;
}

.people-behind__item-block {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

	width: 100%;
	height: 100%;

	padding-bottom: 15px;
}

.people-behind__item-block-row {
	display: flex;

	height: 100%;

	margin: 0 -7.5px;
}

.people-behind__item-block-column {
	flex: 0 0 50%;
	max-width: 50%;

	padding: 0 7.5px;

	&.people-behind__item-block-column--large {
		flex: 0 0 100%;
		max-width: 100%;
	}

	&.people-behind__item-block-column--rectangle {
		flex: 0 0 46.3%;
		max-width: 46.3%;
	}

	&.people-behind__item-block-column--square {
		flex: 0 0 53.7%;
		max-width: 53.7%;
	}
}

.people-behind__image-wrapper {
	position: relative;
	overflow: hidden;

	width: 100%;
	height: 100%;
}

.people-behind__image {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

	width: 100%;
	height: 100%;

	object-fit: cover;
}

@include breakpoint(medium) {
	.people-behind__items-wrapper {
		&.people-behind__items-wrapper--mobile {
			display: none;
		}

		&.people-behind__items-wrapper--desktop {
			display: block;
		}
	}

	.people-behind__title {
		margin-bottom: 38px;

		font-size: 30px;
		text-align: center;
	}

	.people-behind__wrapper {
		transform: translate3d(-2680px, 0, 0);
	}

	.people-behind__item-row {
		padding-top: 55.8%;
	}

	.people-behind__item-block {
		padding-bottom: 25px;
	}

	.people-behind__item-block-row {
		margin: 0 -12.5px;
	}

	.people-behind__item-block-column {
		padding: 0 12.5px;
	}

	.people-behind__item {
		&.people-behind__item--large-bottom {
			.people-behind__item-row {
				&.people-behind__item-row--first {
					order: 1;

					.people-behind__item-block-column {
						&.people-behind__item-block-column--rectangle {
							order: 1;
						}

						&.people-behind__item-block-column--square {
							order: 2;
						}
					}
				}

				&.people-behind__item-row--second {
					order: 2;
				}
			}
		}
	}
}
