.product-card {
	position: relative;

	@include hover {
		.product-card__options {
			transform: translateY(0);
		}
	}
}

.product-card__media {
	position: relative;
	overflow: hidden;
}

.product-card__meta {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: 8px;
	text-align: center;
}

.product-card__image-wrapper {
	position: relative;
	overflow: hidden;
	display: block;
	padding-top: 133.33%;

	@include hover {
		.product-card__image {
			&.product-card__image--secondary {
				visibility: visible;
				opacity: 1;
			}
		}
	}
}

.product-card__image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;

	&.product-card__image--secondary {
		z-index: 2;
		visibility: hidden;
		opacity: 0;
		transition: all 0.25s ease-out;
	}
}

.product-card__badge {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 3;

	&.product-card__badge--sustainable {
		top: unset;
		bottom: 0;
		left: unset;
		right: 0;
	}
}

.product-card__options {
	position: absolute;
	bottom: 0;
	left: 0;
	z-index: 3;
	width: 100%;
	transform: translateY(100%);
	transition: transform 0.25s ease-out;
}

.product-card__actions {
	margin-bottom: 5px;
}

.product-card__vendor,
.product-card__title {
	font-weight: 400;
	text-decoration: none;
	transition: color 0.25s ease-out;
}

.product-card__vendor {
	font-size: 14px;
	line-height: 1.43;
	color: $paragraph-primary;
}

.product-card__title {
	font-size: 12px;
	line-height: 1.5;
	color: $paragraph-secondary;

	@include hover {
		color: $paragraph-primary;
	}
}

.product-card__price-value {
	font-size: 11px;
	line-height: 1.45;
	letter-spacing: 0.2px;
	color: $paragraph-primary;

	&.product-card__price-value--compare-price {
		margin-right: 12px;
		text-decoration: line-through;
		color: $product-old-price;
	}

	&.product-card__price-value--sale {
		color: $product-sale;
	}
}

.product-card__price-unit-value {
	color: $paragraph-secondary;
}
