.cart-empty {
	padding-top: 25px;
	padding-bottom: 2px;
}

.cart-empty__row {
	display: flex;
	flex-wrap: wrap;
}

.cart-empty__col {
	flex: 0 0 100%;
	max-width: 100%;
}

.cart-empty__text {
	text-align: center;
}

.cart-empty__subtitle {
	margin-top: 16px;
	font-size: 14px;
	line-height: 1.43;
	color: $paragraph-secondary;
}

.cart-empty__button {
	max-width: 335px;
	margin-top: 20px;
	margin-left: auto;
	margin-right: auto;
}

.cart-empty__banner {
	margin-top: 41px;
}

@include breakpoint(medium) {
	.cart-empty {
		padding-top: 40px;
	}

	.cart-empty__col--text {
		flex: 0 0 26.57%;
		max-width: 26.57%;
	}

	.cart-empty__col--banner {
		flex: 0 0 73.43%;
		max-width: 73.43%;
	}

	.cart-empty__text {
		max-width: 335px;
		text-align: left;
	}

	.cart-empty__subtitle {
		margin-top: 14px;
	}

	.cart-empty__button {
		margin-left: 0;
	}

	.cart-empty__banner {
		margin-top: 0;
		padding-left: 96px;
	}
}

@include breakpoint(extra-large) {
	.cart-empty__banner {
		padding-left: 126px;
	}
}
