.career-all-departments-info {
	padding-top: 9px;
	padding-bottom: 40px;
}

.career-all-departments-info__breadcrumbs-wrapper {
	padding-bottom: 23px;
}

.career-all-departments-info__heading {
	padding-bottom: 40px;
	font-size: 26px;
	line-height: 1.23;
	letter-spacing: 0.06em;
	text-align: center;
	text-transform: uppercase;
}

.career-all-departments-info__title {
	margin-left: auto;
	margin-right: auto;
	padding-bottom: 24px;
	font-size: 20px;
	line-height: 1.1;
	letter-spacing: 0.07em;
	text-align: center;
	text-transform: uppercase;
	max-width: 335px;
}

.career-all-departments-info__text {
	margin-left: auto;
	margin-right: auto;
	font-size: 14px;
	font-weight: normal;
	line-height: 1.43;
	color: $brand-secondary;
	font-style: normal;
	max-width: 335px;
}

.career-all-departments-info__text-link {
	text-decoration: none;
}

.career-all-departments-info__button {
	display: block;
	width: 100%;
	max-width: 268px;
	margin: 40px auto;
	padding-top: 18px;
	padding-bottom: 16px;
	padding-left: 16px;
	padding-right: 16px;
	border-radius: initial;

	&:hover {
		color: #fff !important;
	}
}

@include breakpoint(medium) {
	.career-all-departments-info {
		padding-top: 7px;
		padding-bottom: 60px;
	}

	.career-all-departments-info__breadcrumbs-wrapper {
		padding-bottom: 35px;
	}

	.career-all-departments-info__heading {
		font-size: 30px;
		line-height: 1.17;
		letter-spacing: 0.05em;
	}

	.career-all-departments-info__title {
		margin-left: unset;
		margin-right: unset;
		font-size: 24px;
		line-height: 1.17;
		letter-spacing: 0.08em;
		max-width: unset;
	}

	.career-all-departments-info__text {
		max-width: unset;
		margin-left: unset;
		margin-right: unset;
	}

	.career-all-departments-info__content-container {
		max-width: 788px;
		margin-left: auto;
		margin-right: auto;
	}
}
