.acuity-embed-button {
	padding: 0;
	box-shadow: none;
	background: none;
	color: $paragraph-primary !important;
}

.acuity-embed-modal {
	background-color: rgba($brand-primary, 0.5);

	iframe {
		width: 100% !important;
		max-width: 100% !important;
	}
}

.acuity-modal-inner {
	width: 100%;
	max-width: calc(100% - 40px);
	max-height: calc(100% - 120px);
	padding: 20px 0;
}

.acuity-modal-close {
	top: 20px;
	right: 20px;
	display: block;
	width: 14px;
	height: 14px;
	background-image: linear-gradient($grey-middle, $grey-middle),
		linear-gradient($grey-middle, $grey-middle);
	background-repeat: no-repeat;
	background-size: 100% 1px, 1px 100%;
	background-position: 50% 50%;
	line-height: 14px;
	color: $grey-middle;
	transform: rotate(45deg);

	span {
		display: none;
	}
}

.acuity-modal-content {
	padding-left: 20px;
	padding-right: 20px;
}

.acuity-modal-inner,
.acuity-modal-content {
	&::-webkit-scrollbar {
		width: 4px;
	}

	&::-webkit-scrollbar-track {
		background-color: $text-field-disabled;
	}

	&::-webkit-scrollbar-thumb {
		background-color: $text-field-border;
	}
}

@include breakpoint(large) {
	.acuity-modal-inner {
		max-width: calc(100% - 80px);
	}
}

@include breakpoint(ultra-large) {
	.acuity-modal-inner {
		max-width: 1030px;
	}
}
