.career-all-departments-banners {
	padding-bottom: 60px;
}

.career-all-departments-banners__content-container {
	padding-bottom: 20px;
}

.career-all-departments-banners__col {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	flex-direction: column;
}

.career-all-departments-banners__title {
	padding-bottom: 24px;
	font-size: 26px;
	line-height: 1.23;
	letter-spacing: 0.06em;
	text-align: center;
	text-transform: uppercase;
}

.career-all-departments-banners__item {
	position: relative;
	padding-bottom: 20px;
}

.career-all-departments-banners__item-link {
	position: absolute;
	width: 100%;
	height: 100%;
}

.career-all-departments-banners__item-title {
	position: absolute;
	bottom: 40px;
	left: 20px;
	z-index: 5;
	font-size: 16px;
	line-height: 1.37;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	color: $white;
}

.career-all-departments-banners__mobile_block {
	margin-left: auto;
	margin-right: auto;
	max-width: 335px;
}

.career-all-departments-banners__mobile-title {
	padding-bottom: 24px;
	font-size: 20px;
	line-height: 1.1;
	letter-spacing: 0.07em;
	text-align: center;
	text-transform: uppercase;
}

.career-all-departments-banners__mobile-text {
	font-size: 14px;
	font-weight: 500;
	line-height: 1.43;
	color: $brand-secondary;
	font-style: normal;
}

@include breakpoint(medium) {
	.career-all-departments-banners {
		padding-bottom: 100px;
	}

	.career-all-departments-banners__content-container {
		margin-left: auto;
		margin-right: auto;
		padding-bottom: 0;
		max-width: 788px;
	}

	.career-all-departments-banners__col {
		justify-content: flex-start;
		margin-left: -10px;
		margin-right: -10px;
		flex-direction: row;
		flex-wrap: wrap;
	}

	.career-all-departments-banners__title {
		padding-bottom: 40px;
		font-size: 24px;
		line-height: 1.17;
		letter-spacing: 0.08em;
	}

	.career-all-departments-banners__item {
		width: 50%;
		padding-left: 10px;
		padding-right: 10px;
	}

	.career-all-departments-banners__image-wrapper {
		padding-top: 69.75%;
	}

	.career-all-departments-banners__mobile_block {
		display: none;
	}
}
