.career-one-department-info {
	padding-top: 6px;
	padding-bottom: 40px;
}

.career-one-department-info__breadcrumbs-wrapper {
	padding-bottom: 32px;
}

.career-one-department-info__top-row {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	padding-top: 55px;
	padding-bottom: 26px;
	flex-direction: row;
}

.career-one-department-info__back-link {
	position: absolute;
	top: 0;
	left: 0;
	font-family: $font-avenir-next-condensed;
	text-decoration: none;
}

.career-one-department-info__back-link-text {
	padding-left: 9px;
	font-size: 12px;
	line-height: 1.67;
	letter-spacing: 0.04em;
	text-align: center;
	text-transform: uppercase;
}

.career-one-department-info__heading {
	font-size: 26px;
	line-height: 1.23;
	letter-spacing: 0.06em;
	text-align: center;
	text-transform: uppercase;
}

.career-one-department-info__text-container {
	p {
		font-size: 14px;
		font-weight: 400;
		line-height: 1.43;
		color: $brand-secondary;
		font-style: normal;
	}

	a {
		font-weight: 500;
		text-decoration: none;
		font-style: normal;
	}
}

@include breakpoint(medium) {
	.career-one-department-info {
		padding-top: 4px;
		padding-bottom: 60px;
	}

	.career-one-department-info__text-container {
		max-width: 788px;
		margin-left: auto;
		margin-right: auto;

		p {
			font-weight: 500;
		}
	}

	.career-vacancy-info__breadcrumbs-wrapper {
		padding-bottom: 32px;
	}

	.career-one-department-info__top-row {
		padding-top: 0;
		padding-bottom: 32px;
	}

	.career-one-department-info__back-link {
		top: unset;
	}
}
