.section__title {
	margin-top: 22px;
	font-family: "AvenirNext LT Pro Cn", sans-serif;
	font-style: normal;
	font-size: 20px;
	font-weight: bold;
	line-height: 26px;
	letter-spacing: 0.07em;
	text-align: center;
	text-transform: uppercase;
	color: $black;
}

.brands-page {
	padding-bottom: 60px;

	&__title {
		margin-bottom: 25px;
		text-align: center;
	}
}

.brands-page__sticky {
	position: sticky;
	top: 0;
	overflow: hidden;
	z-index: 10;
	margin: 0 auto;
	padding: 35px 0 20px;
	background: $background-white;
	transform: translateY(-25px);
	transition: top 0.3s ease-in-out;
	max-width: 1920px;
}

.brands-page__search {
	position: relative;
	margin-bottom: 32px;
}

.brands-search__field {
	width: 100%;
	padding: 14px 52px 14px 12px;
	border: none;
	border-bottom: 1px solid #d3d6d9;
}

.brands-search__icon svg {
	position: absolute;
	top: 14px;
	right: 20px;
}

.brands-search__field::placeholder {
	color: #aaaaaa;
}

.brands-page-tabs {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: 32px;

	&__list {
		display: flex;
		align-items: center;
	}

	&__item {
		position: relative;
	}

	&__item:not(:last-of-type) {
		margin-right: 10px;

		&::before {
			content: "";
			position: absolute;
			top: 50%;
			left: 100%;
			display: block;
			width: 1px;
			height: 13px;
			background-color: #d3d6d9;
			transform: translate(10px, -50%);
		}
	}

	&__item:not(:first-of-type) {
		margin-left: 10px;
	}

	&__link {
		position: relative;
		font-family: $font-avenir-next;
		font-weight: 500;
		text-decoration: none;

		&::before {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			display: block;
			width: 0;
			height: 1px;
			background: $black;
			transition: width 0.3s ease-in-out, color 0.3s ease-in-out,
				font-weight 0.3s ease-in-out;
		}

		@include hover {
			font-weight: 500;
			color: $black;

			&::before {
				width: 100%;
			}
		}
	}
}

.brands-page__filter {
	position: relative;
	z-index: 1;
	display: flex;
	justify-content: flex-start;
	margin-bottom: 20px;
	flex-wrap: wrap;

	.brands-page__anchor {
		width: calc(100% / 9);
		font-family: $font-avenir-next-condensed;
		font-style: normal;
		font-size: 20px;
		font-weight: bold;
		line-height: 30px;
		text-transform: uppercase;
		text-decoration: none;
		color: #808080;
		transition: color 0.3s ease-in-out;
		cursor: pointer;
		user-select: none;

		@include hover {
			color: $black;
		}
	}
}

.brands-page__filter-clear {
	position: relative;
	display: flex;
	visibility: hidden;
	margin: 0 auto;
	background-color: transparent;
	text-align: center;

	&.js-brands-page__filter-clear-visible {
		visibility: visible;
	}
}

.brands-page__filter .brands-page__anchor.selected {
	color: $black;
}

.brands-page__content {
	max-width: 860px;
	margin: 5px auto 0;
	padding: 20px 0;
	border-top: 1px solid #d3d6d9;

	a {
		text-decoration: none;
	}
}

.brands-page__info {
	text-align: right;
}

.menu_role--main {
	display: flex;
	flex-wrap: wrap;
}

.menu-link__route {
	font-size: 14px;
	line-height: 24px;
	color: #808080;
}

.menu-link__head {
	display: inline-block;
	margin-bottom: 11px;
	font-family: $font-avenir-next-condensed;
	font-style: normal;
	font-size: 36px;
	font-weight: 800;
	line-height: 40px;
	letter-spacing: 0.04em;
}

.dropdown-child .menu-link.menu-link--header:not(:first-of-type) {
	margin-top: 20px;
}

.brand-filter-results .menu-link--header:not(.selected) {
	opacity: 0.5;
	user-select: none;
}

.brand-filter-results .menu-link--header:not(.selected) a.highlight {
	opacity: 1;
	pointer-events: all;
	user-select: all;
}

.brand-filter-results
.menu-link--header.highlight
> .menu-link__head
.brands-result
.menu-link--header.highlight
a.highlight {
	opacity: 1;
	pointer-events: all;
	user-select: all;
}

.search-filter-results .menu-link__head,
.search-filter-results a:not(.highlight) {
	opacity: 0.5;
	pointer-events: none;
	user-select: none;
}

.search-filter-results .menu-link__head,
.search-filter-results a.highlight {
	opacity: 1;
	pointer-events: all;
	user-select: all;
}

.menu-link_level--first {
	min-width: 100%;
	padding-right: 20px;
}

.menu-link.menu-link--header {
	padding-bottom: 20px;
	border-bottom: 1px solid #d3d6d9;
}

.page-brands--popular .page-brands__row {
	padding: 10px 0;
	background-color: var(--customizer-bg-color);
}

.page__title {
	font-family: "AvenirNext LT Pro Cn", sans-serif;
	font-style: normal;
	font-size: 30px;
	font-weight: bold;
	line-height: 35px;
	letter-spacing: 0.05em;
	text-align: center;
	text-transform: uppercase;
	color: $black;
}

.page-brands--popular {
	margin-bottom: 40px;
}

@include breakpoint(large) {
	.brands-page__sticky {
		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 50%;
			display: block;
			width: 1920px;
			height: 100%;
			margin: -10px auto;
			background: $white;
			transform: translateX(-50%);
		}

		&.styled--shadow {
			&::before {
				box-shadow: 0 4px 4px rgba(0, 0, 0, 0.05);
			}
		}
	}

	.brands-page__filter {
		justify-content: space-between;
		margin-bottom: 20px;
		flex-wrap: nowrap;
	}

	.menu-link_level--first {
		min-width: 267px;
		padding-right: 20px;
	}

	.menu_role--main {
		flex-wrap: nowrap;
	}

	.menu-link.menu-link--header {
		padding-bottom: 0;
		border-bottom: none;
	}

	.dropdown-child .menu-link.menu-link--header:not(:first-of-type) {
		margin-top: 40px;
	}

	.brands-page {
		padding-bottom: 100px;
	}
}
