.popup {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 21;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	visibility: hidden;
	opacity: 0;
	transition-duration: 0.4s;
	transition-property: visibility, opacity;
	transition-timing-function: ease-out;

	&.popup--open {
		visibility: visible;
		opacity: 1;

		.popup__wrapper {
			transform: translateY(0);
		}
	}
}

.popup__overlay {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: -1;
	background-color: rgba($brand-primary, 0.5);
}

.popup__wrapper {
	position: relative;
	overflow-y: auto;
	width: 100%;
	max-width: 490px;
	padding-top: 60px;
	padding-left: 20px;
	padding-right: 20px;
	transform: translateY(25px);
	transition-duration: 0.4s;
	transition-property: transform;
	transition-timing-function: ease-out;

	&::-webkit-scrollbar {
		width: 0;
		background: transparent;
	}
}

.popup__content-wrapper {
	padding-bottom: 60px;
}

.popup__content {
	position: relative;
	padding: 20px;
	background-color: #fff;
}
