.cart {
	padding-top: 25px;
	padding-bottom: 60px;
}

.cart__container {
	max-width: 1286px;
}

.cart__header {
	padding-bottom: 20px;
}

.cart__title {
	text-align: center;
}

.cart__free-shipping {
	margin-top: 24px;
}

.cart__free-shipping--desktop {
	display: none;
}

.cart__row {
	display: flex;
	flex-wrap: wrap;
}

.cart__row--info {
	margin-top: 40px;
}

.cart__col {
	flex: 0 0 100%;
	max-width: 100%;
}

.cart__col--banner {
	order: 2;
}

.cart__col--refund {
	order: 1;
}

.cart__totals {
	margin-top: 24px;
}

.cart__totals-row {
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	margin-left: -6px;
	margin-right: -6px;

	+ .cart__totals-row {
		margin-top: 8px;
	}
}

.cart__totals-col {
	padding-left: 6px;
	padding-right: 6px;
}

.cart__totals-text {
	font-size: 14px;
	font-weight: 500;
	line-height: 1.43;
	color: $paragraph-primary;
}

.cart__totals-text--secondary {
	font-weight: 400;
	color: $paragraph-secondary;
}

.cart__totals-text--value {
	text-align: right;
}

.cart__buttons {
	margin-top: 16px;
}

.cart__button {
	+ .cart__button {
		margin-top: 20px;
	}
}

.cart__button--submit {
	&::before,
	&::after {
		display: inline-block;
		width: 13px;
		height: 16px;
		margin-top: -4px;
		background-repeat: no-repeat;
		background-position: 50% 50%;
		vertical-align: middle;
	}

	&::before {
		content: "";
