	}

	&::after {
		content: none;
		margin-left: 12px;
		background-image: url("data:image/svg+xml,%3Csvg fill='none' xmlns='http://www.w3.org/2000/svg' class='icon icon-lock' viewBox='0 0 14 17'%0A%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.936 10.434v-.01a1.968 1.968 0 00-1.138-1.75 1.974 1.974 0 00-2.075.255 1.053 1.053 0 00-.264.273c-.653.825-.511 2.059.337 2.736.01.009.022.036.022.065v1.308c0 .59.48 1.069 1.071 1.069h.21a1.07 1.07 0 001.071-1.07v-1.307c0-.026.014-.054.037-.073a1.91 1.91 0 00.73-1.495zm-2.632-.728c.329-.264.737-.314 1.084-.153.345.16.569.503.577.883a.945.945 0 01-.363.736 1.068 1.068 0 00-.403.828v1.31a.099.099 0 01-.1.1h-.21a.099.099 0 01-.099-.1v-1.307c0-.312-.137-.61-.374-.812a1.006 1.006 0 01-.112-1.485z' fill='%23fff' /%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M11.66 5.88h.963c.76 0 1.377.615 1.377 1.374v8.372c0 .759-.616 1.374-1.377 1.374H1.377C.617 17 0 16.385 0 15.626V7.254c0-.76.616-1.375 1.377-1.375h.962V4.47A4.588 4.588 0 016.991.001a4.596 4.596 0 014.67 4.471v1.407zm-1.133 0V4.488a3.465 3.465 0 00-3.536-3.356c-1.9-.03-3.469 1.471-3.518 3.352v1.395h7.054zM1.133 7.253c0-.134.11-.243.243-.243h11.247c.134 0 .243.109.243.243v8.372a.243.243 0 01-.243.242H1.376a.243.243 0 01-.243-.242V7.254z' fill='%23fff' /%3E%3C/svg%3E%0A");
	}
}

.cart__refund {
	margin-bottom: 40px;
}

.cart__refund-accordion {
	.cart__refund-accordion-item {
		border-bottom: 1px solid $border-primary;
	}

	.cart__refund-accordion-item-toggler {
		font-size: 14px;
		font-weight: 500;
		line-height: 1.43;
		letter-spacing: normal;
		text-transform: none;
	}

	.cart__refund-accordion-item-content {
		padding-bottom: 16px;
		font-size: 12px;
		line-height: 1.5;
	}
}

@include breakpoint(medium) {
	.cart {
		padding-top: 40px;
		padding-bottom: 80px;
	}

	.cart__container {
		max-width: 1310px;
	}
}

@include breakpoint(extra-large) {
	.cart__row--info {
		margin-top: 20px;
	}

	.cart__col--products {
		flex: 0 0 63.33%;
		max-width: 63.33%;
	}

	.cart__col--total {
		flex: 0 0 36.67%;
		max-width: 36.67%;
		padding-left: 69px;
	}

	.cart__col--banner {
		order: 1;
	}

	.cart__col--refund {
		order: 2;
	}

	.cart__title {
		text-align: left;
	}

	.cart__free-shipping {
		margin-top: 0;
		margin-bottom: 24px;
	}

	.cart__free-shipping--mobile {
		display: none;
	}

	.cart__free-shipping--desktop {
		display: block;
	}

	.cart__totals {
		margin-top: 0;
		padding: 14px 16px;
		border: 1px solid $border-primary;
	}

	.cart__button--submit {
		&::before {
			content: none;
		}

		&::after {
			content: "";
		}
	}

	.cart__banner {
		margin-top: 60px;
	}

	.cart__refund {
		margin-bottom: 0;
	}
}
