.outfit-all-person-list {
	margin-bottom: 25px;
}

.outfit-all-person-list__breadcrumbs-container {
	margin-top: 6px;
	margin-bottom: 20px;
}

.outfit-all-person-list__heading {
	margin-bottom: 33px;
	font-size: 26px;
	line-height: 1.23;
	letter-spacing: 0.06em;
	text-align: center;
	text-transform: uppercase;
}

.outfit-all-person-list__row {
	display: flex;
	flex-direction: column;
}

.outfit-all-person-list__col {
	width: 100%;
	margin-bottom: 33px;
}

.outfit-all-person-list__image-wrapper {
	position: relative;
	overflow: hidden;
	width: 100%;
	margin-top: auto;
	margin-bottom: 17px;
	padding-top: 133.2%;
}

.outfit-all-person-list__image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.outfit-all-person-list__title {
	line-height: 1;
}

.outfit-all-person-list__link {
	font-size: 24px;
	line-height: 1.25;
	letter-spacing: 0.06em;
	text-transform: uppercase;
	text-decoration: none;
}

.outfit-all-person-list__image-link {
	text-decoration: none;
}

@include breakpoint(medium) {
	.outfit-all-person-list {
		margin-bottom: 88px;
	}

	.outfit-all-person-list__breadcrumbs-container {
		margin-top: 4px;
		margin-bottom: 12px;
	}

	.outfit-all-person-list__heading {
		margin-bottom: 57px;
		font-size: 30px;
		line-height: 1.17;
		letter-spacing: 0.05em;
	}

	.outfit-all-person-list__container {
		padding-left: 40px;
		padding-right: 40px;
	}

	.outfit-all-person-list__row {
		flex-direction: row;
		margin-left: -12px;
		margin-right: -12px;
		flex-wrap: wrap;
	}

	.outfit-all-person-list__col {
		margin-bottom: 0;
		padding-left: 12px;
		padding-right: 12px;
		max-width: 33.33%;

		+ .outfit-all-person-list__col {
			margin-top: 30px;
		}

		&:nth-child(-n + 3) {
			margin-top: 0;
		}
	}

	.outfit-all-person-list__image-wrapper {
		margin-bottom: 12px;
	}

	.outfit-all-person-list__title {
		a {
			font-size: 28px;
			line-height: 1.29;
			letter-spacing: 0.07em;
			text-transform: uppercase;
		}
	}
}
