.announcement-bar {
	padding-top: 6px;
	padding-bottom: 6px;
	background-color: $brand-primary;
}

.announcement-bar__content {
	font-size: 11px;
	font-weight: 500;
	line-height: 1.45;
	letter-spacing: 0.2px;
	text-align: center;
	color: $white;

	p,
	a {
		font-size: inherit;
		font-weight: inherit;
		line-height: inherit;
		letter-spacing: inherit;
		color: inherit;
	}

	a {
		text-underline-position: under;
		white-space: nowrap;
	}
}
