.collection__toolbar {
	padding-top: 42px;
	padding-bottom: 20px;
}

.collection__toolbar-row {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	margin-left: -20px;
	margin-right: -20px;
}

.collection__toolbar-col {
	flex: 0 0 100%;
	max-width: 100%;
	padding-left: 20px;
	padding-right: 20px;
}

.collection__toolbar-filter {
	&.collection__toolbar-filter--desktop {
		display: none;
	}

	&.collection__toolbar-filter--mobile {
		display: flex;
		justify-content: center;
		margin-left: -10px;
		margin-right: -10px;
	}
}

.collection__toolbar-filter-toggle {
	display: inline-block;
	padding: 0;
	background: none;
	font-size: 14px;
	font-weight: 400;
	line-height: 1.43;
	color: $paragraph-primary;
	transition: color 0.25s ease-out;

	&.collection__toolbar-filter-toggle--show {
		display: none;
	}

	@include hover {
		color: $paragraph-secondary;
	}
}

.collection__toolbar-filter-toggle-icon {
	display: inline-block;
	margin-right: 8px;
	vertical-align: middle;
	color: inherit;

	svg {
		display: block;
		width: 18px;

		path {
			fill: currentColor;
		}
	}
}

.collection__toolbar-filter-item {
	display: block;
	width: 50%;
	max-width: 162px;
	padding-left: 10px;
	padding-right: 10px;
}

.collection__toolbar-sort {
	display: flex;
	align-items: center;
	margin-top: 36px;
}

.collection__toolbar-sort-item {
	&.collection__toolbar-sort-item--select {
		display: none;
	}
}

.collection__toolbar-layout-switch {
	display: flex;
	align-items: center;
}

.collection__toolbar-layout-switch-item {
	display: inline-block;
	margin-right: 24px;
	padding: 0;
	border-radius: 0;
	border: 0;
	background: none;
	color: $border-primary;
	transition: color 0.25s ease-out;

	&:last-child {
		margin-right: 0;
	}

	&.collection__toolbar-layout-switch-item--desktop {
		display: none;
	}

	&.active {
		color: $brand-primary;
	}

	svg {
		display: block;
		width: initial;
		height: 16px;

		path {
			fill: currentColor;
		}
	}

	@include hover {
		color: $brand-primary;
	}
}

.collection__products-count {
	margin-top: 20px;
	margin-bottom: 12px;
}

.collection__products-count-value {
	font-size: 14px;
	font-weight: 400;
	line-height: 1.43;
	color: $paragraph-secondary;
}

.collection__products-count-text {
	position: relative;
	cursor: default;

	&::before {
		content: "";
		position: absolute;
		top: -2px;
		left: 100%;
		display: block;
		width: 11px;
		height: 11px;
		margin-left: 2px;
		background-image: url("data:image/svg+xml,%3Csvg fill='none' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 12 12' class='icon icon-tooltip'%0A%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M.54 6a5.46 5.46 0 1010.92 0A5.46 5.46 0 00.54 6zm10.267 0a4.807 4.807 0 11-9.614 0 4.807 4.807 0 019.614 0z' fill='gray' /%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M6.358 6.613c-.046.096-.066.267-.062.513h-.814a17.859 17.859 0 01-.003-.213c0-.264.044-.48.13-.651.088-.17.263-.362.524-.574.262-.213.418-.352.47-.418a.559.559 0 00.117-.345.58.58 0 00-.209-.448c-.14-.124-.327-.187-.563-.187a.853.853 0 00-.57.195c-.154.13-.26.328-.317.593l-.823-.102a1.37 1.37 0 01.487-.97c.3-.265.696-.398 1.185-.398.514 0 .924.134 1.228.403.304.27.456.583.456.94 0 .198-.056.385-.167.561-.112.177-.35.417-.716.721-.19.158-.307.284-.353.38zm.02.823v.896h-.896v-.896h.897z' fill='gray' /%3E%3C/svg%3E%0A");
		background-repeat: no-repeat;
		background-size: contain;
		background-position: 50% 50%;
	}

	&::after {
		content: attr(data-tooltip-text);
		position: absolute;
		bottom: 50%;
		left: 100%;
		display: none;
		min-width: 220px;
		margin-left: 15px;
		padding: 3px 10px;
		border: 0;
		border-radius: 1px;
		background-color: #f0f1f2;
		box-shadow: 0 3px 6px rgba(0, 0, 0, 0.03);
		font-family: $font-avenir-next-condensed-light;
		font-size: 7px;
		font-weight: 500;
		line-height: 1.14;
		letter-spacing: 0.06em;
		text-align: center;
		text-transform: uppercase;
		color: $paragraph-primary;
	}

	@include hover {
		&::after {
			display: block;
		}
	}
}

.collection__content-row {
	display: flex;
	flex-wrap: wrap;
}

.collection__content-col {
	flex: 0 0 100%;
	max-width: 100%;
}

.collection__products {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -15px;

	&[data-columns="1"] {
		.collection__product,
		.collection__banner {
			flex: 0 0 100%;
			max-width: 100%;
			min-height: 468px;

			.collection-banner__media-item {
				padding-top: 70%;
			}
		}
	}

	&[data-columns="2"] {
		.collection__product,
		.collection__banner {
			flex: 0 0 50%;
			max-width: 50%;

			.collection-banner__media-item {
				padding-top: 118%;
			}
		}
	}

	&[data-columns="3"] {
		.collection__product {
			flex: 0 0 33.33%;
			max-width: 33.33%;
		}

		.collection__banner {
			&[data-banner-width="1"] {
				flex: 0 0 33.33%;
				max-width: 33.33%;
			}

			&[data-banner-width="2"] {
				flex: 0 0 66.66%;
				max-width: 66.66%;
			}

			&[data-banner-width="3"] {
				flex: 0 0 100%;
				max-width: 100%;
				min-height: 468px;
			}

			&[data-banner-width="4"],
			&[data-banner-width="5"] {
				display: none;
			}
		}
	}

	&[data-columns="4"] {
		.collection__product {
			flex: 0 0 25%;
			max-width: 25%;
		}

		.collection__banner {
			&[data-banner-width="1"] {
				flex: 0 0 25%;
				max-width: 25%;
			}

			&[data-banner-width="2"] {
				flex: 0 0 50%;
				max-width: 50%;
			}

			&[data-banner-width="3"] {
				flex: 0 0 75%;
				max-width: 75%;
			}

			&[data-banner-width="4"] {
				flex: 0 0 100%;
				max-width: 100%;
				min-height: 468px;
			}

			&[data-banner-width="5"] {
				display: none;
			}
		}
	}

	&[data-columns="5"] {
		.collection__product {
			flex: 0 0 20%;
			max-width: 20%;
		}

		.collection__banner {
			&[data-banner-width="1"] {
				flex: 0 0 20%;
				max-width: 20%;
			}

			&[data-banner-width="2"] {
				flex: 0 0 40%;
				max-width: 40%;
			}

			&[data-banner-width="3"] {
				flex: 0 0 60%;
				max-width: 60%;
			}

			&[data-banner-width="4"] {
				flex: 0 0 80%;
				max-width: 80%;
			}

			&[data-banner-width="5"] {
				flex: 0 0 100%;
				max-width: 100%;
				min-height: 468px;
			}
		}
	}
}

.collection__product {
	flex: 0 0 50%;
	max-width: 50%;
	margin-bottom: 60px;
	padding-left: 15px;
	padding-right: 15px;
}

.collection__no-results {
	padding-top: 60px;
	padding-bottom: 100px;
}

.collection__no-results-title {
	text-align: center;
}

.collection__pagination {
	padding-bottom: 100px;
}

.collection__pagination-progress {
	margin-bottom: 16px;
}

.collection__pagination-progress-viewed {
	margin-bottom: 16px;
	font-size: 13px;
	font-weight: 400;
	line-height: 1.54;
	text-align: center;
	color: $paragraph-secondary;
}

.collection__pagination-progress-bar {
	position: relative;
	overflow: hidden;
	width: 100%;
	max-width: 240px;
	height: 6px;
	margin-left: auto;
	margin-right: auto;
	border-radius: 2.5px;
}

.collection__pagination-progress-bar-track,
.collection__pagination-progress-bar-indicator {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	transition-property: width;
	transition-duration: 1.4s;
	transition-timing-function: ease-in-out;
}

.collection__pagination-progress-bar-track {
	width: 100%;
	background-color: $grey-light;
}

.collection__pagination-progress-bar-indicator {
	width: 0;
	border-radius: 2.5px;
	background-color: $grey-middle;
}

.collection__pagination-loaded-message {
	text-align: center;
	color: $grey-middle;
}

@include breakpoint(medium) {
	.collection__toolbar {
		padding-bottom: 28px;
	}
}

@include breakpoint(large) {
	.collection {
		&.collection--filter-hidden {
			.collection__toolbar-filter-toggle {
				&.collection__toolbar-filter-toggle--show {
					display: inline-block;
				}

				&.collection__toolbar-filter-toggle--hide {
					display: none;
				}
			}

			.collection__products-count-value {
				max-width: 100%;
				padding-left: 0;
			}

			.collection__content-col {
				&.collection__content-col--filters {
					flex: 0 0 0;
					max-width: 0;
					padding-right: 0;
				}

				&.collection__content-col--products {
					flex: 0 0 100%;
					max-width: 100%;
					padding-left: 0;
				}
			}

			.collection__filters {
				display: none;
			}
		}
	}

	.collection__toolbar {
		padding-top: 54px;
		padding-bottom: 8px;
	}

	.collection__toolbar-col {
		&.collection__toolbar-col--filter {
			flex: 0 0 calc(25% + 10px);
			max-width: calc(25% + 10px);
			padding-right: 20px;
		}

		&.collection__toolbar-col--refine-by {
			flex: 0 0 40%;
			max-width: 40%;
			padding-left: 20px;
		}

		&.collection__toolbar-col--sort {
			flex: 0 0 calc(35% - 10px);
			max-width: calc(35% - 10px);
			padding-left: 20px;
		}
	}

	.collection__toolbar-filter {
		&.collection__toolbar-filter--desktop {
			display: block;
		}

		&.collection__toolbar-filter--mobile {
			display: none;
		}
	}

	.collection__toolbar-sort {
		justify-content: flex-end;
		margin-top: 0;
	}

	.collection__toolbar-layout-switch-item {
		margin-right: 8px;

		&:last-of-type {
			margin-right: 0;
		}

		&.collection__toolbar-layout-switch-item--desktop {
			display: inline-block;
		}

		&.collection__toolbar-layout-switch-item--mobile {
			display: none;
		}
	}

	.collection__toolbar-sort-item {
		&.collection__toolbar-sort-item--select {
			display: block;
		}
	}

	.collection__toolbar-layout-switch {
		margin-left: 28px;
	}

	.collection__products-count-value {
		max-width: 75%;
		margin-left: auto;
		padding-left: 20px;
	}

	.collection__content-col {
		&.collection__content-col--filters {
			flex: 0 0 25%;
			max-width: 25%;
			padding-right: 20px;
		}

		&.collection__content-col--products {
			flex: 0 0 75%;
			max-width: 75%;
			padding-left: 20px;
		}
	}

	.collection__products {
		margin-left: -20px;
		margin-right: -20px;
	}

	.collection__product {
		flex: 0 0 33.33%;
		max-width: 33.33%;
		padding-left: 20px;
		padding-right: 20px;
	}
}

@include breakpoint(extra-large) {
	.collection__toolbar-col {
		&.collection__toolbar-col--filter {
			flex: 0 0 19.1667%;
			max-width: 19.1667%;
		}

		&.collection__toolbar-col--refine-by {
			flex: 0 0 49.8333%;
			max-width: 49.8333%;
		}

		&.collection__toolbar-col--sort {
			flex: 0 0 calc(30% - 10px);
			max-width: calc(30% - 10px);
		}
	}

	.collection__products-count-value {
		max-width: 81.8333%;
	}

	.collection__content-col {
		&.collection__content-col--filters {
			flex: 0 0 18.1667%;
			max-width: 18.1667%;
		}

		&.collection__content-col--products {
			flex: 0 0 81.8333%;
			max-width: 81.8333%;
		}
	}

	.collection__product {
		flex: 0 0 25%;
		max-width: 25%;
	}
}

@include breakpoint(extra-wide) {
	.collection__toolbar-col {
		&.collection__toolbar-col--filter {
			flex: 0 0 15.547%;
			max-width: 15.547%;
		}

		&.collection__toolbar-col--refine-by {
			flex: 0 0 54.453%;
			max-width: 54.453%;
			padding-left: 38px;
		}
	}

	.collection__products-count-value {
		max-width: 85.453%;
		padding-left: 48px;
	}

	.collection__content-col {
		&.collection__content-col--filters {
			flex: 0 0 14%;
			max-width: 14%;
			padding-right: 58px;
		}

		&.collection__content-col--products {
			flex: 0 0 86%;
			max-width: 86%;
			padding-left: 58px;
		}
	}
}