.radio {
	display: none;

	&:checked {
		+ label {
			&::before {
				border-color: $text-field;
			}

			&::after {
				display: block;
			}
		}

		&:disabled,
		&.disabled {
			+ label {
				&::before {
					border-color: $text-field-border;
					background-color: $text-field-disabled;
				}

				&::after {
					background-color: $text-field-border;
				}
			}
		}
	}

	+ label {
		position: relative;
		display: inline-block;
		padding-left: 28px;
		font-size: 14px;
		line-height: 1.43;
		color: $brand-secondary;
		cursor: pointer;

		&::before,
		&::after {
			content: "";
			position: absolute;
			top: 50%;
			border-radius: 50%;
			transform: translateY(-50%);
		}

		&::before {
			left: 0;
			width: 16px;
			height: 16px;
			border: 1px solid $text-field-border;
			background-color: $white;
		}

		&::after {
			left: 4px;
			display: none;
			width: 10px;
			height: 10px;
			background-color: $text-field;
		}
	}
}
