.article-card {
	display: flex;
	flex-direction: column;
	justify-content: space-between;

	height: 100%;

	padding-bottom: 22px;
}

.article-card__image-wrapper {
	position: relative;
	overflow: hidden;

	display: block;

	width: 100%;

	padding-top: 82.8%;
}

.article-card__image {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

	width: 100%;
	height: 100%;

	object-fit: cover;
}

.article-card__tags {
	margin-top: 11px;
}

.article-card__tags-row {
	display: flex;
	flex-wrap: wrap;

	margin-top: -5px;
	margin-left: -3px;
	margin-right: -3px;
}

.article-card__tags-column {
	margin-top: 5px;
	padding: 0 3px;
}

.article-card__tag {
	padding-top: 5px;
	padding-bottom: 2px;
	padding-left: 10px;
	padding-right: 10px;

	border: 1px solid $grey-middle;

	font-size: 12px;
	line-height: 1.25;
	letter-spacing: -0.4px;
	color: $brand-secondary;

	border-radius: 50px;
}

.article-card__title {
	margin-top: 11px;

	font-family: $font-primary;

	font-size: 18px;
	line-height: 1.15;
	letter-spacing: -0.15px;
}

.article-card__description {
	margin-top: 19px;

	line-height: 1.25;
	letter-spacing: 1px;
}

.button.article-card__button {
	margin-top: 16px;
	font-size: 15px;
}

@include breakpoint(medium) {
	.article-card {
		padding-bottom: 28px;
	}

	.article-card__image-wrapper {
		padding-top: 73.2%;
	}

	.article-card__tags {
		margin-top: 12px;
	}

	.article-card__title {
		margin-top: 13px;

		font-size: 16px;
		line-height: 1.25;
	}

	.article-card__description {
		margin-top: 14px;
		letter-spacing: 0;
	}

	.button.article-card__button {
		margin-top: 53px;
		font-size: 16px;
	}
}
