.help-center-content-widerruf {
	h1 {
		padding-bottom: 7px;
	}

	.help-center-content__link-container {
		display: flex;
		flex-direction: row;
		align-items: center;
		padding-bottom: 24px;

		a {
			display: flex;
			align-items: center;
			text-transform: inherit;
			flex-direction: row;
		}

		p {
			font-size: 14px;
			font-weight: 500;
			line-height: 1.43;
			font-style: normal;
		}
	}

	.help-center-content__text-content {
		padding-bottom: 24px;
	}
}

@include breakpoint(large) {
	.help-center-content-widerruf {
		h1 {
			padding-bottom: 10px;
		}
	}
}
