.ks-chart-container {
	.ks-table-content-wrapper {
		margin-bottom: 32px;

		.ks-table-header {
			margin-bottom: 21px;
			padding-bottom: 0;
			font-family: $font-avenir-next-condensed !important;
			font-size: 16px;
			font-weight: 500;
			line-height: 1.375;
			letter-spacing: 0.08em;
			text-align: center;
			text-transform: uppercase;
			color: $headline-primary;
		}

		.ks-unit-toggle-wrapper {
			margin-bottom: 10px;

			.ks-unit-toggle {
				margin: 0 -10px;

				.ks-unit-toggle-label {
					padding: 0 10px;
					font-size: 14px;
					line-height: 1.43;
					letter-spacing: normal;
					text-transform: capitalize;
					text-decoration: none;
					text-underline-position: under;
					color: $paragraph-primary;
					transition: all 0.25s ease-out;

					&.kiwi-active {
						font-weight: 500;
						text-decoration: underline;
					}

					+ .ks-unit-toggle-label {
						border-left: 1px solid $border-primary;
					}
				}

				&:hover {
					opacity: 1;
					color: $paragraph-secondary;
				}
			}
		}

		.ks-table {
			td {
				border: 1px solid $border-primary;
			}

			.ks-table-row {
				&:first-of-type {
					.ks-table-cell.ks-table-header-cell {
						font-weight: 500;
					}
				}
			}

			.ks-table-cell {
				padding-top: 6px;
				padding-bottom: 8px;
				padding-left: 12px;
				padding-right: 12px;
			}
		}
	}

	.ks-html-content {
		h1,
		h2 {
			margin-bottom: 21px;
			font-size: 16px;
			line-height: 1.375;
			letter-spacing: 0.08em;
		}

		h3,
		h4,
		h5,
		h6 {
			font-size: 12px;
			line-height: 1.5;
			letter-spacing: 0.06em;
		}

		p {
			margin-bottom: 16px;
		}
	}
}

@include breakpoint(medium) {
	.ks-table-content-wrapper {
		margin-bottom: 32px;

		.ks-table-header {
			margin-bottom: 20px;
		}
	}

	.ks-chart-container {
		.ks-html-content {
			h1,
			h2 {
				margin-bottom: 20px;
			}
		}
	}
}
