.account-menu {
	flex: none;
}

.account-menu__logout {
	display: none;
}

.account-menu__list {
	margin-bottom: 20px;
}

.account-menu__list--desktop {
	display: none;
}

.account-menu__list-item {
	position: relative;
	padding-top: 8px;
	padding-bottom: 8px;
	padding-left: 14px;
	color: $paragraph-secondary;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		opacity: 0;
		width: 2px;
		height: 100%;
		background: $black;
		transition: opacity 0.3s ease-in-out;
	}

	@include hover {
		color: $paragraph-primary;
	}
}

.account-menu__list-item--active {
	color: $paragraph-primary;

	&::before {
		opacity: 1;
	}
}

.account-menu__list-link {
	display: block;
	text-decoration: none;
	color: currentColor;
}

.account-menu__active-link-container {
	position: relative;
	display: flex;
	padding-top: 14px;
	padding-bottom: 14px;
	padding-left: 12px;
	border: 1px solid $border-primary;
	flex-direction: row;
}

.account-menu__active-link-arrow {
	position: absolute;
	top: 50%;
	right: 14px;
	display: block;
	width: 10px;
	height: 5px;
	background-image: url("data:image/svg+xml,%3Csvg width='11' height='5' viewBox='0 0 11 5' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.736026 0.109303C0.563525 -0.0403333 0.288591 -0.0360708 0.121945 0.118822C-0.0447016 0.273715 -0.0399551 0.520584 0.132546 0.67022L4.76694 4.69031C4.9353 4.83635 5.20227 4.8363 5.37056 4.69019L10.0009 0.670102C10.1734 0.520399 10.178 0.273528 10.0113 0.1187C9.84454 -0.036128 9.5696 -0.0402827 9.39717 0.10942L5.06855 3.86753L0.736026 0.109303Z' fill='black'/%3E%3C/svg%3E%0A");
	background-repeat: no-repeat;
	background-size: 10px 5px;
	transform: translateY(-50%);
	transition: transform 0.25s ease-out;
}

.account-menu__active-menu-list.account-menu__mobile-list-link--active
.account-menu__active-link-arrow {
	transform: translateY(-50%) rotate(180deg);
}

.account-menu__list--mobile {
	position: relative;
	z-index: 10;
	width: 100%;
	margin-bottom: 20px;
	background-color: white;

	&::-webkit-scrollbar {
		width: 4px;
	}

	&::-webkit-scrollbar-thumb {
		background-color: $text-field-border;
	}
}

.account-menu__active-menu-list {
	display: flex;
	flex-direction: row;
	align-items: center;
	border-left: 1px solid $border-primary;
	border-bottom: 1px solid $border-primary;
	border-right: 1px solid $border-primary;
	border-top: 1px solid $border-primary;
}

.account-menu__active-menu-item {
	position: relative;
	width: 100%;
	padding-top: 13px;
	padding-bottom: 13px;
	padding-left: 12px;
	font-size: 16px;
	font-weight: normal;
	line-height: 1.12;
	text-transform: capitalize;
	color: $black;
	font-style: normal;
}

.account-menu__mobile-list-link--hiden {
	display: none;
}

.account-menu__mobile-list-link--active {
	display: block;
}

.account-menu__mobile-list {
	position: absolute;
	display: none;
	width: 100%;
	padding-left: 12px;
	background-color: $white;
	overflow-y: scroll;
	max-height: 138px;
	border-left: 1px solid $border-primary;
	border-bottom: 1px solid $border-primary;
	border-right: 1px solid $border-primary;
}

.account-menu__mobile-list--open {
	display: block;
}

.account-menu__mobile-list-item {
	position: relative;
	display: flex;
	list-style: none;
}

.account-menu__mobile-list-link {
	width: 100%;
	padding-top: 13px;
	padding-bottom: 13px;
	font-size: 16px;
	font-weight: normal;
	line-height: 1.12;
	text-transform: capitalize;
	text-decoration: none;
	color: $brand-secondary;
	font-style: normal;
}

.account-menu__mobile-list-item--hidden {
	display: none;
}

@include breakpoint(medium) {
	.account-menu__list--desktop {
		display: block;
	}

	.account-menu__list--mobile {
		display: none;
	}

	.account-menu__logout {
		display: block;
	}

	.account-menu__list-link--flex {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
	}

	.account-menu__order-count {
		width: 18px;
		height: 18px;
		background-color: $black;
		vertical-align: middle;
		font-size: 14px;
		line-height: 1.43;
		text-align: center;
		color: $white;
		border-radius: 50%;
	}

	.account-menu__logout-button {
		width: 100%;
		padding-bottom: 8px !important;
		max-width: 103px;
		font-weight: 500;
	}
}

@include breakpoint(large) {
	.account__col.account__col--menu {
		margin-right: 65px;
	}
}

@include breakpoint(ultra-large) {
	.account__col.account__col--menu {
		margin-right: 128px;
	}
}
.account-menu .select2-selection__rendered {
	opacity: 0;
}
