/*--- Generated styles from app ---*/

.instashop {
	margin-bottom: 60px;

	.fs-has-links {
		display: none;
	}

	.fs-wrapper div.fs-text-container {
		margin: 0;
		background-color: rgba(0, 0, 0, 0.005);
	}

	div.fs-service-icon {
		display: none;
	}

	.fs-lookbook_v2_5.fs-wrapper div.fs-timeline-entry {
		margin: 1px;
	}

	.fs-wide-timeline .fs-entry-container,
	.fs-normal-timeline .fs-entry-container,
	.fs-small-timeline div.fs-entry-container,
	.fs-narrow-timeline div.fs-entry-container {
		padding-top: 0;
	}

	.fs-timeline-entry {
		position: relative;
		overflow: visible;
		height: 442px;
	}

	.fs-normal-timeline .fs-timeline-entry {
		height: 442px;
	}

	.fs-small-timeline .fs-timeline-entry {
		height: 370px;
	}

	.fs-narrow-timeline .fs-timeline-entry {
		height: 210px;
	}

	.fs-wrapper {
		margin: 0 auto;
	}

	.fs-entry-container {
		width: calc(100% / 2 - 24px) !important;
		margin: 25px 12px 105px;
	}

	.fs-text-container {
		position: absolute !important;
		top: unset !important;
		bottom: -85px !important;
		display: block !important;
		opacity: 1 !important;
		width: 100%;
	}

	.fs-timeline-text,
	.fs-text-container {
		padding-left: 0 !important;
	}

	.fs-wrapper div.fs-text-container * {
		text-align: left;
		color: #000000;
	}

	.fs-timeline-text .fs-entry-title {
		overflow: hidden;
		display: -webkit-box;
		margin-top: 14px;
		font-family: $font-avenir-next !important;
		font-size: 16px !important;
		font-weight: 400 !important;
		font-style: normal;
		line-height: 20px;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		text-overflow: ellipsis;
		min-height: 40px;
		max-height: 40px;
	}

	.fs-entry-date {
		font-size: 12px !important;
		text-transform: capitalize;
		color: #808080 !important;
	}

	/*--- Load More button ---*/

	.fs-next-button {
		svg {
			display: none;
		}

		&::before {
			content: "Load more";
			display: inline-block;
			min-width: 240px;
			padding: 18px 20px;
			border: 1px solid #000;
			background-color: #000;
			font-family: "Avenir Next Condensed", sans-serif;
			font-size: 14px;
			font-weight: 400;
			line-height: 1.15;
			letter-spacing: 0.06em;
			text-align: center;
			text-transform: uppercase;
			text-decoration: none;
			color: #fff;
			transition-property: border-color, background-color, box-shadow,
				color;
			transition-duration: 0.25s;
			transition-timing-function: ease-out;
		}

		&:hover {
			&::before {
				border-color: grey;
				background-color: grey;
			}
		}
	}
}

/*--- Custom styles ---*/

.instashop__header {
	margin-bottom: 15px;
}

.instashop__title,
.instashop__subtitle {
	text-align: center;
}

.instashop__breadcrumbs {
	margin-top: 7px;
}

.instashop__title {
	margin-top: 15px;
	margin-bottom: 11px;
}

.instashop__subtitle {
	p {
		font-family: $font-avenir-next !important;
		font-size: 14px;
		font-weight: 500;
		line-height: 20px;
		color: #808080;
	}
}

.fs-product-price-note {
	display: block;
	font-size: 8px;
	text-transform: none;
	color: #808080;
}

.fs-add-to-cart-enabled .fs-option:disabled + .fs-option-label::after {
	content: var(--sold-out-lang, "Sold Out");
}

/*--- Modal custom styles ---*/
.fs-desktop .fs-detail-right {
	min-width: unset;
}

.fs-detail-title {
	font-family: $font-avenir-next !important;
	font-weight: 400;
}

/*-------------------------------*/

@include breakpoint(large) {
	.instashop {
		.fs-entry-container {
			width: calc(100% / 3 - 24px) !important;
		}

		.fs-text-container {
			bottom: -60px !important;
		}

		.fs-entry-date {
			font-size: 14px !important;
		}

		.fs-timeline-text .fs-entry-title {
			-webkit-line-clamp: 1;
			min-height: 20px;
			max-height: 20px;
		}
	}

	.fs-detail-left {
		width: 410px;
		max-width: 410px;
	}

	.fs-desktop .fs-detail-right {
		width: calc(100% - 410px);
		min-width: 610px;
	}

	.fs-text-link-container {
		float: left;
		clear: none;
	}
}

@include breakpoint(wide) {
	.instashop {
		.fs-timeline-entry {
			height: 640px;
		}
	}
}
