.product-form__submit {
	margin-top: 20px;
}

.product-form__submit-row {
	display: flex;
}

.product-form__button {
	&.disabled,
	&[disabled] {
		border-color: $grey-middle;
		background-color: $grey-middle;
	}
}

.product-form__button--main {
	flex-grow: 1;
	display: block;
	max-width: 100%;
}

.product-form__button--wishlist {
	flex: 0 0 52px;
	margin-left: 8px;
	border: 1px solid $brand-primary !important;
	background-color: $white !important;

	&::before {
		margin-left: auto;
		margin-right: auto;
	}

	@include hover {
		border-color: $brand-primary !important;
		background-color: $white !important;
	}
}

.product-form__success-toast {
	position: fixed;
	bottom: 32px;
	left: 50%;
	z-index: 5;
	visibility: visible;
	opacity: 1;
	width: calc(100% - 40px);
	max-width: 400px;
	padding: 20px;
	background-color: $brand-primary;
	transform: translateX(-50%);
	transition: 0.25s all ease-out;
}

.product-form__success-toast--hidden {
	visibility: hidden;
	opacity: 0;
}

.product-form__success-toast-message {
	font-family: $font-avenir-next-condensed;
	font-size: 12px;
	line-height: 1.5;
	text-align: center;
	text-transform: uppercase;
	color: $white;
}
