.recently-viewed-products {
	padding-top: 25px;
	padding-bottom: 53px;
}

.recently-viewed-products__title {
	margin-bottom: 22px;
}

.recently-viewed-products__slider {
	max-width: 100%;
	margin-left: auto;
	margin-right: auto;
}

.recently-viewed-products__slider-slide {
	width: 40.6%;
}

.recently-viewed-products__slider-slide .product-card__image-wrapper {
	padding-top: 121.5%;
}

.recently-viewed-products__slider-pagination {
	margin-top: 28px;
}

@include breakpoint(medium) {
	.recently-viewed-products {
		padding-top: 30px;
		padding-bottom: 61px;
	}

	.recently-viewed-products__slider-container {
		&:not(.initialized) {
			.recently-viewed-products__slider-slide {
				margin-right: 20px;
			}
		}
	}

	.recently-viewed-products__slider-slide {
		width: 28.5%;
	}

	.recently-viewed-products__slider-slide .product-card__image-wrapper {
		padding-top: 138%;
	}
}

@include breakpoint(large) {
	.recently-viewed-products__slider-slide .product-card__image-wrapper {
		padding-top: 133.33%;
	}
}

@include breakpoint(extra-large) {
	.recently-viewed-products {
		padding-top: 43px;
		padding-bottom: 92px;
	}

	.recently-viewed-products__title {
		margin-bottom: 24px;
	}

	.recently-viewed-products__slider-slide {
		width: 14.61%;
	}
}

@include breakpoint(extra-wide) {
	.recently-viewed-products {
		padding-top: 50px;
		padding-bottom: 102px;
	}

	.recently-viewed-products__title {
		margin-bottom: 40px;
	}

	.recently-viewed-products__slider-container {
		&:not(.initialized) {
			.recently-viewed-products__slider-slide {
				margin-right: 40px;
			}
		}
	}

	.recently-viewed-products__slider-slide {
		width: 15.23%;
	}
}
