.size-chart {
	position: relative;
	.size-chart__close-button {
		position: absolute;
		top: 17px;
		right: 17px;
		display: block;
		width: 14px;
		height: 14px;
		padding: 0;
		background-color: transparent;
		color: $paragraph-primary;
		transition: color 0.25s ease-out;
		svg {
			display: block;
			width: 14px;
			path {
				fill: currentColor;
			}
		}
		@include hover {
			color: $paragraph-secondary;
		}
	}
	.size-chart__header,
	.size-chart__scroll-wrapper {
		padding-left: 20px;
		padding-right: 20px;
	}
	.size-chart__scroll-wrapper {
		padding-top: 0;
		padding-bottom: 20px;
		&::-webkit-scrollbar {
			width: 4px;
		}
		&::-webkit-scrollbar-track {
			border-radius: 2.5px;
			background-color: $grey-light;
		}
		&::-webkit-scrollbar-thumb {
			border-radius: 2.5px;
			background-color: $grey-middle;
		}
	}
	.size-chart__wrapper {
		max-width: 714px;
		margin-left: auto;
		transform: translateX(100%);
	}
	.size-chart__header {
		padding-top: 40px;
		padding-bottom: 40px;
		text-align: center;
	}
	.size-chart__header-title {
		font-size: 24px;
		line-height: 1.25;
	}
	.size-chart__header-row {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 26px;
		margin-left: -8.5px;
		margin-right: -8.5px;
	}
	.size-chart__header-col {
		padding-left: 8.5px;
		padding-right: 8.5px;
	}
	.size-chart__header-col--svg svg {
		display: block;
		width: 54px;
	}
	.size-chart__header-icon {
		display: block;
		width: 54px;
	}
	.size-chart__header-text {
		max-width: 210px;
		margin-left: auto;
		margin-right: auto;
		font-size: 11px;
		line-height: 1.45;
		letter-spacing: 0.2px;
		text-align: left;
		color: $paragraph-primary;
	}
}

@include breakpoint(extra-small) {
	.size-chart {
		.size-chart__scroll-wrapper {
			padding-left: 20px;
			padding-right: 20px;
		}
	}
}

@include breakpoint(medium) {
	.size-chart {
		.size-chart__header,
		.size-chart__scroll-wrapper {
			padding-left: 40px;
			padding-right: 40px;
		}
		.size-chart__header {
			padding-top: 20px;
		}
		.size-chart__header-row {
			margin-top: 28px;
			margin-left: -5.5px;
			margin-right: -5.5px;
		}
		.size-chart__header-col {
			padding-left: 5.5px;
			padding-right: 5.5px;
		}
		.size-chart__header-title {
			font-size: 28px;
			line-height: 1.28;
		}
	}
}
