.career-vacancy-info__top-row {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	padding-bottom: 32px;
	flex-direction: row;
}

.career-vacancy-info__back-link {
	display: none;
}

.career-vacancy-info__breadcrumbs-wrapper {
	padding-bottom: 20px;
}

.career-vacancy-info__heading {
	font-size: 26px;
	line-height: 1.23;
	letter-spacing: 0.06em;
	text-align: center;
	text-transform: uppercase;
}

.career-vacancy-info__block {
	padding-bottom: 32px;

	p {
		font-size: 14px;
		font-style: normal;
		font-weight: normal;
		line-height: 1.43;
		color: $brand-secondary;
	}
}

.career-vacancy-info__block-title {
	padding-bottom: 8px;
	font-size: 20px;
	line-height: 1.1;
	letter-spacing: 0.07em;
	text-transform: uppercase;
}

.career-vacancy-info__list-text-container {
	p {
		padding-left: 20px;
		font-size: 14px;
		font-weight: normal;
		line-height: 1.43;
		color: $brand-secondary;
		font-style: normal;
	}

	li {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		padding-top: 10px;
		padding-bottom: 10px;
		flex-direction: row;
	}
}

@include breakpoint(medium) {
	.career-vacancy-info__breadcrumbs-wrapper {
		padding-bottom: 34px;
	}

	.career-vacancy-info__top-row {
		padding-bottom: 40px;
	}

	.career-vacancy-info__heading {
		font-size: 30px;
		line-height: 1.17;
		letter-spacing: 0.05em;
	}

	.career-vacancy-info__block-container {
		margin-bottom: 20px;
		margin-left: auto;
		margin-right: auto;
		max-width: 788px;
	}

	.career-vacancy-info__block {
		padding-bottom: 40px;
	}

	.career-vacancy-info__block-title {
		font-size: 24px;
		line-height: 1.17;
		letter-spacing: 0.08em;
		text-transform: uppercase;
	}
}

@include breakpoint(extra-large) {
	.career-vacancy-info__back-link {
		position: absolute;
		left: 0;
		display: block;
		font-family: $font-avenir-next-condensed;
		text-decoration: none;
	}

	.career-vacancy-info__back-link-text {
		padding-left: 5px;
		font-size: 12px;
		line-height: 1.67;
		letter-spacing: 0.07em;
		text-align: center;
		text-transform: uppercase;
	}
}
