.quantity-selector {
	position: relative;
	overflow: hidden;
	display: flex;
	width: 72px;
	border: 1px solid $text-field-border;
	background-color: $background-white;
}

.quantity-selector__button,
.quantity-selector__input {
	display: block;
	padding-top: 8px;
	padding-bottom: 6px;
	background: transparent;
	font-size: 16px;
	line-height: normal;
	text-align: center;
	color: $text-field;
}

.quantity-selector__button {
	width: 23px;
	padding-left: 0;
	padding-right: 0;
	transition: color 0.25s ease-out;

	svg {
		display: inline-block;
		width: 6.12px;
		vertical-align: middle;

		path {
			fill: currentColor;
		}
	}

	&:disabled {
		color: $grey-middle;
		cursor: default;

		@include hover {
			color: $grey-middle;
		}
	}

	@include hover {
		color: $text-field-border;
	}
}

.quantity-selector__input {
	width: 24px;
	height: unset;
	padding-left: 0;
	padding-right: 0;
	border: 0;
	-moz-appearance: textfield;

	&::-webkit-outer-spin-button,
	&::-webkit-inner-spin-button {
		-webkit-appearance: none;
		margin: 0;
	}

	&:focus {
		border: 0;
	}
}
