.career-vacancy-mail__container {
	.globo-form-app.boxed-layout {
		box-shadow: none !important;
		padding: 0;
		border-radius: unset;
	}

	.globo-form-app {
		max-width: 788px !important;
		margin-top: 0;
		margin-bottom: 0;
		.content {
			margin: 0 -6px;
		}

		.message {
			+ .footer {
				margin-top: 0 !important;
			}
		}

		.message.success {
			+ .footer {
				margin-top: 16px !important;
			}
		}

		.header {
			display: none;
		}

		.globo-form-control {
			margin-bottom: 20px;
			padding-left: 6px;
			padding-right: 6px;

			input {
				display: block;
				height: 50px;
				padding: 14px 12px;
				border: 1px solid #d3d6d9;
				background-color: #fff;
				font-size: 16px;
				font-weight: 400;
				line-height: 1.125;
				color: #000;
				border-radius: unset;
				transition: all 0.25s ease-out;
				box-shadow: none;

				&:focus {
					box-shadow: none;
				}
			}
			input[type="file"] {
				display: flex;
				flex-direction: row;
				justify-content: center;
				align-items: center;
				border: 2px dashed #000000;
			}

			textarea {
				display: block;
				height: 120px;
				padding-top: 12px;
				padding-bottom: 34px;
				padding-left: 12px;
				padding-right: 12px;
				border: 1px solid #d3d6d9;
				background-color: #fff;
				resize: none;
				font-size: 16px;
				font-weight: 400;
				line-height: 1.125;
				color: #000;
				transition-property: border, color;
				transition-duration: 0.25s;
				transition-timing-function: ease-out;
				border-radius: unset;
				box-shadow: none;
			}

			select {
				position: relative;
				display: block;
				height: unset;
				padding-top: 14px;
				padding-bottom: 14px;
				padding-left: 12px;
				padding-right: 30px;
				border: 1px solid #d3d6d9;
				background-color: unset;
				color: #000;
				cursor: pointer;
				background-clip: unset;
				border-radius: unset;
				box-shadow: none;
			}
		}

		.globo-label {
			display: none;
		}

		.footer {
			display: flex;
			flex-direction: row;
			justify-content: center;

			.action {
				display: inline-block;
				padding-top: 18px;
				padding-bottom: 16px;
				padding-left: 16px;
				padding-right: 16px;
				border: 1px solid transparent;
				border-color: #000 !important;
				background-color: #000 !important;
				font-family: "Avenir Next Condensed", sans-serif;
				font-size: 14px;
				font-weight: 400;
				line-height: 1.15;
				letter-spacing: 0.06em;
				text-align: center;
				text-transform: uppercase;
				text-decoration: none;
				color: #fff !important;
				transition-property: border-color, background-color, box-shadow,
					color;
				transition-duration: 0.25s;
				transition-timing-function: ease-out;
				min-width: 216px;

				:hover {
					border-color: #303030;
					background-color: #303030;
					filter: unset;
					box-shadow: none;
				}
			}
		}
	}
}

.career-vacancy-mail__heading {
	margin-bottom: 24px;
	font-size: 26px;
	line-height: 1.23;
	letter-spacing: 0.06em;
	text-align: center;
	text-transform: uppercase;
}

@include breakpoint(medium) {
	.career-vacancy-mail__container {
		padding-bottom: 100px;
	}

	.career-vacancy-mail__heading {
		margin-bottom: 40px;
		font-size: 30px;
		line-height: 1.17;
		letter-spacing: 0.05em;
	}
}
