.badge {
	display: inline-block;
	padding: 4px 10px;
	font-size: 12px;
	font-weight: 400;
	line-height: normal;
	text-align: center;
	cursor: default;
}

.badge--hidden {
	display: none;
}

.badge--big {
	min-width: 140px;
	padding-top: 11px;
	padding-bottom: 7px;
	padding-left: 16px;
	padding-right: 16px;
	font-size: 15px;
	letter-spacing: 0.5px;
}

.badge--icon {
	padding: 0;

	svg {
		display: inline-block;
		vertical-align: middle;
		max-height: 28px;
	}
}

.badge--bestseller {
	background-color: $product-badge-bestseller;
	color: $white;
}

.badge--coming-soon {
	background-color: $product-badge-coming-soon;
	color: $white;
}

.badge--new {
	background-color: $product-badge-new;
	color: $white;
}

.badge--pre-order {
	background-color: $product-badge-pre-order;
	color: $white;
}

.badge--sale {
	background-color: $product-badge-sale;
	color: $white;
}

.badge--staff-pick {
	background-color: $product-badge-staff-pick;
	color: $white;
}

.badge--offline-only {
	background-color: $product-badge-offline-only;
	color: $white;
}

.badge--unisex {
	background-color: $product-badge-unisex;
	color: $white;
}

.badge--sustainability {
	position: absolute;
    bottom: 0;
    right: 0;
    z-index: 3;
	background-color: $product-badge-sustainability;
	color: $white;
	padding: 4px 14px;
	transition: 300ms;
}
