.footer-payments {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	margin-bottom: 10px;
}

.footer-payments__title {
	margin-bottom: 24px;
	font-size: 12px;
	line-height: 1.5;
	text-transform: uppercase;
}

.footer-payments__icons-row {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -10px;
	margin-top: -5px;
}

.footer-payments__icons-column {
	display: flex;
	align-items: center;
	margin-top: 5px;
	padding: 0 10px;
}

.footer-payments__icons {
	margin: 0 auto;
}

.footer-payments__icon {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	width: 61px;
	height: 31px;
	padding: 0 15px;

	svg.icon {
		width: unset;
		height: 20px;
		-webkit-filter: grayscale(100%);
		-moz-filter: grayscale(100%);
		-o-filter: grayscale(100%);
		filter: grayscale(100%);
	}
}

@include breakpoint(medium) {
	.footer-payments {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		margin-bottom: 10px;
	}

	.footer-payments__title {
		margin-bottom: 24px;
		font-size: 12px;
		line-height: 1.5;
		text-transform: uppercase;
	}

	.footer-payments__icons-row {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -10px;
		margin-top: -5px;
	}

	.footer-payments__icons-column {
		display: flex;
		align-items: center;
		margin-top: 5px;
		padding: 0 10px;
	}

	.footer-payments__icon {
		svg.icon {
			width: unset;
			height: 20px;
		}
	}
}

@include breakpoint(large) {
	.footer-payments {
		display: flex;
		margin-bottom: 0;
	}

	.footer-payments__title {
		margin-bottom: 0;
		padding-top: 4px;
		padding-left: 5px;
		padding-right: 5px;
		font-size: 13px;
		line-height: 1.23;
		text-transform: uppercase;
	}

	.footer-payments__icons-row {
		display: flex;
		margin: 0 -10px;
	}

	.footer-payments__icons-column {
		display: flex;
		align-items: center;
		padding: 0 10px;
	}

	.footer-payments__icons {
		margin: 0 10px 0 0;
	}

	.footer-payments__icon {
		justify-content: center;
		width: 38px;
		height: 19px;
		padding: 0;

		svg.icon {
			max-height: 20px;
			max-width: 35px;
		}
	}
}
