.cart-recommendations {
	padding-bottom: 60px;
}

.cart-recommendations__container {
	max-width: 1286px;
}

.cart-recommendations__title {
	margin-bottom: 20px;
	font-size: 24px;
	line-height: 1.25;
	letter-spacing: 0.06em;

	&:empty {
		display: none;
	}
}

.cart-recommendations__content {
	max-width: 1310px;
	margin: 0 auto;
}

.cart-recommendations__content--desktop {
	display: none;
}

.cart-recommendations__cards {
	display: flex;
	flex-wrap: wrap;
	margin-left: -15px;
	margin-right: -15px;
}

.cart-recommendations__cards-item {
	flex: 0 0 50%;
	max-width: 50%;
	margin-top: 60px;
	padding-left: 15px;
	padding-right: 15px;

	&:nth-child(-n + 2) {
		margin-top: 0;
	}

	&:nth-child(n + 5) {
		display: none;
	}
}

.cart-recommendations__slider-slide {
	width: 46%;
	margin-right: 20px;
}

.cart-recommendations__slider-container {
	.slider__pagination.swiper-pagination-bullets {
		display: none;
	}
}

@include breakpoint(extra-small) {
	.cart-recommendations__slider-slide {
		width: 47%;
	}
}

@include breakpoint(medium) {
	.cart-recommendations__container {
		max-width: 1310px;
	}

	.cart-recommendations__title {
		font-size: 28px;
		line-height: 1.28;
		letter-spacing: 0.07em;
	}

	.cart-recommendations__content--mobile {
		display: none;
	}

	.cart-recommendations__content--desktop {
		display: block;
	}

	.cart-recommendations__slider-slide {
		width: 48%;
		margin-right: 24px;
	}
}

@include breakpoint(large) {
	.cart-recommendations__slider-slide {
		width: 21%;
	}
}

@include breakpoint(extra-large) {
	.cart-recommendations__slider-slide {
		width: 17%;
	}
}

@include breakpoint(ultra-large) {
	.cart-recommendations__slider-slide {
		max-width: initial;
		width: 18.2%;
	}
}

@include breakpoint(wide) {
	.cart-recommendations__slider-slide {
		width: 29.5%;
	}
}

@include breakpoint(extra-wide) {
	.cart-recommendations__slider-slide {
		width: 39%;
		margin-right: 40px;
	}
}

@media screen and (min-width: 1440px) {
	.cart-recommendations__content--desktop {
		max-width: calc(1310px + 64px);
		margin-right: 0;
	}
}

@media screen and (min-width: 1441px) {
	.cart-recommendations__content--desktop {
		max-width: 1310px;
		margin-right: auto;
	}
}
