.store-overview-info {
	padding-top: 9px;
	padding-bottom: 14px;
}

.store-overview-info__breadcrumbs {
	margin-bottom: 20px;
}

.store-overview-info__row {
	display: flex;
	flex-direction: column;
}

.store-overview-info__col--first-mobile {
	order: -1;
	padding-bottom: 20px;
}

.store-overview-info__upper-line {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	padding-bottom: 24px;
	flex-direction: row;
}

.store-overview-info__back-link {
	position: absolute;
	left: 0;
	display: none;
	font-family: $font-avenir-next-condensed;
	text-decoration: none;
}

.store-overview-info__back-link-text {
	padding-left: 11px;
}

.store-overview-info__heading {
	font-size: 26px;
	line-height: 1.23;
	letter-spacing: 0.06em;
	text-align: center;
	text-transform: uppercase;
	color: #0c0c0c;
}

.store-overview-info__button {
	font-size: 14px;
	font-weight: 500;
	line-height: 1.14;
	letter-spacing: 0.06em;
	text-transform: uppercase;
	font-style: normal;
	min-width: 335px;
}

.store-overview-info__text-block {
	padding-bottom: 23px;
	p {
		+ a {
			margin-top: 11px;
		}
	}
}

.store-overview-info__text-block-title {
	padding-bottom: 10px;
	font-size: 20px;
	line-height: 1.1;
	letter-spacing: 0.07em;
	text-transform: uppercase;
	max-width: 335px;
}

.store-overview-info__text-block-link {
	letter-spacing: 0.07;
}

.store-overview-info__text-block-text {
	font-size: 14px;
	font-weight: normal;
	line-height: 1.43;
	font-style: normal;
	max-width: 308px;

	a {
		text-decoration: none;
	}
}

.store-overview-info__text-block-text--lightgray {
	font-size: 14px;
	font-weight: normal;
	color: $brand-secondary;
	font-style: normal;
}

.store-overview-info__item-image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.store-overview-info__item-image-wrapper {
	position: relative;
	overflow: hidden;
	width: 100%;
	margin-top: auto;
	padding-top: 106%;
}

.store-overview-info__slider-slide {
	width: 55%;
	height: auto;
}

.store-overview-info__slider-slide.store-overview-info__slider-slide--big {
	width: 100%;
}
.store-overview-info__slider-slide--big {
	.store-overview-info__item-image-wrapper {
		padding-top: 79.2%;
	}
}

.store-overview-info__slider {
	margin-left: -20px;
	margin-right: -20px;
}

.store-overview-info__item {
	display: flex;
	flex-direction: column;
	height: 100%;
}

@include breakpoint(medium) {
	.store-overview-info__breadcrumbs {
		margin-bottom: 13px;
	}

	.store-overview-info__container {
		padding-left: 40px;
		padding-right: 40px;
	}

	.store-overview-info__slider-container {
		padding-left: 37px;
		padding-right: 22px;
	}

	.store-overview-info {
		padding-top: 7px;
		padding-bottom: 60px;
	}

	.store-overview-info__row {
		flex-direction: row;
		margin-left: -31px;
		margin-right: -31px;
	}

	.store-overview-info__col {
		padding-left: 31px;
		padding-right: 31px;
	}

	.store-overview-info__text-block--top-padding {
		padding-top: 16px;
	}

	.store-overview-info__col--wide {
		display: flex;
		justify-content: flex-end;
		width: 67.9%;
		margin-left: auto;
		flex-direction: row;
	}

	.store-overview-info__col--first-mobile {
		order: unset;
		padding-bottom: 0;
	}

	.store-overview-info__upper-line {
		padding-bottom: 37px;
	}

	.store-overview-info__back-link {
		font-size: 12px;
		line-height: 1.67;
		letter-spacing: 0.07em;
		text-transform: uppercase;
	}

	.store-overview-info__heading {
		font-size: 30px;
		line-height: 1.17;
		letter-spacing: 0.05em;
	}

	.store-overview-info__button {
		min-width: 217px;
	}

	.store-overview-info__text-block-title {
		line-height: 1.3;
		max-width: 388px;
	}

	.store-overview-info__text-block-text {
		max-width: 388px;

		&:last-child {
			padding-bottom: 0;
		}
	}

	.store-overview-info__slider {
		width: 100%;
	}

	.store-overview-info__slider-slide {
		width: 50%;
		padding-right: 9px;
	}

	.store-overview-info__slider-slide--big {
		width: 100%;
		padding-right: 0;
	}

	.store-overview-info__item-image-wrapper {
		padding-top: 107.3%;
	}

	.store-overview-info__slider-arrow {
		margin: 0 16px;
	}

	.template-store-overview-fourth {
		.store-overview-info__back-link-text {
			padding-left: 9px;
			letter-spacing: 0.04em;
		}

		.store-overview-info__text-block-title {
			padding-bottom: 6px;
			font-size: 24px;
			font-weight: bold;
			letter-spacing: 0.08em;
			text-transform: uppercase;
			font-style: normal;
		}

		.store-overview-info__text-block-text {
			max-width: 440px;
		}

		.store-overview-info__slider-container {
			padding-left: 8px;
			padding-right: 20px;
		}
	}
}

@include breakpoint(extra-large) {
	.store-overview-info__back-link {
		display: block;
	}
}
