.complementary-product {
	margin-top: 52px;
}

.complementary-product__row {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
}

.complementary-product__col {
	flex: 0 0 100%;
}

.complementary-product__image-wrapper {
	position: relative;
	overflow: hidden;
	padding-top: 133.95%;
}

.complementary-product__image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.complementary-product__meta {
	margin-top: 25px;
}

.complementary-product__heading {
	position: relative;
	padding-bottom: 33px;
	font-size: 26px;
	font-weight: 700;
	line-height: 1.23;
	letter-spacing: 0.06em;
	text-transform: uppercase;

	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		width: 43px;
		height: 1px;
		background-color: $black;
	}
}

.complementary-product__vendor {
	margin-top: 14px;
	font-size: 14px;
	font-weight: 400;
	line-height: 1.42;
	color: $paragraph-primary;
}

.complementary-product__title {
	display: inline-block;
	margin-top: 3px;
	font-size: 12px;
	line-height: 1.5;
	text-decoration: none;
	color: rgba($paragraph-primary, 0.5);
	transition: color 0.25s ease-out;

	@include hover {
		color: $paragraph-primary;
	}
}

.complementary-product__price {
	margin-top: 8px;
}

.complementary-product__price-value {
	font-size: 12px;
	line-height: 1.45;
	color: $paragraph-primary;
}

.complementary-product__price-value--compare-price {
	margin-right: 12px;
	text-decoration: line-through;
	color: $product-old-price;
}

.complementary-product__price-value--sale {
	color: $product-sale;
}

.complementary-product__button {
	width: 100%;
	margin-top: 32px;
}

@include breakpoint(medium) {
	.complementary-product__col--image {
		flex: 0 0 47%;
		max-width: 47%;
	}

	.complementary-product__col--meta {
		flex: 0 0 53%;
		max-width: 53%;
		margin-top: -52px;
	}

	.complementary-product__meta {
		margin-top: 0;
		padding-left: 64px;
	}

	.complementary-product__heading {
		padding-bottom: 55px;
		font-size: 30px;
		line-height: 1.67;
		letter-spacing: 0.05em;
	}

	.complementary-product__button {
		max-width: 228px;
	}
}

@include breakpoint(large) {
	.complementary-product {
		margin-top: 0;
	}
}

@include breakpoint(extra-large) {
	.complementary-product__meta {
		padding-left: 93px;
	}
}

@include breakpoint(wide) {
	.complementary-product__meta {
		padding-left: 218px;
	}
}
