.breadcrumbs-wrapper {
	max-width: 1480px;
	margin: 0 auto;
	padding: 20px 20px 14px;
}

.page-brands--popular {
	display: none;
}
.page-brands {
	padding-top: 33px;
	padding-bottom: 39px;
}

.js-page-brands-incomplete {
	justify-content: center;
}

.page-brands__row {
	display: flex;
	flex-direction: column;
}

.page-brands__col {
	width: 100%;

	&.brands__col--button {
		margin-top: 35px;
	}
}

.page-brands__logos {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	margin-left: -10px;
	margin-right: -10px;

	.slider__arrow {
		visibility: hidden;
	}

	&.initialized {
		.slider__arrow {
			visibility: visible;
		}
	}

	&.less-then-min {
		.slider__arrow {
			visibility: hidden;
			opacity: 0;
		}
	}
}

.page-brands__logos-item {
	width: calc(33.33% - 20px);
	margin-left: 10px;
	margin-right: 10px;
}

.page-brands__image-wrapper {
	position: relative;
	overflow: hidden;
}

.page-brands__image {
	display: block;
	width: 100%;
	max-width: 106px;
	margin: auto;
}

.page-brands__link-wrapper {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.page-brands__button {
	display: block;
	width: 100%;
	margin: 0 auto;
}

@include breakpoint(medium) {
	.page-brands {
		padding-top: 36px;
		padding-bottom: 43px;
	}

	.page-brands__col {
		&.brands__col--button {
			margin-top: 20px;
		}
	}

	.page-brands__logos {
		margin-left: -16px;
		margin-right: -16px;
	}

	.page-brands__logos-item {
		width: calc(20% - 32px);
		margin-left: 16px;
		margin-right: 16px;
	}

	.page-brands__button {
		max-width: 253px;
	}
}

@include breakpoint(large) {
	.page-brands--popular {
		display: block;
	}
}

@include breakpoint(extra-large) {
	.page-brands {
		padding-top: 58px;
		padding-bottom: 92px;
	}

	.breadcrumbs-wrapper {
		max-width: 1520px;
		padding: 20px 40px 14px;
	}
}

@include breakpoint(ultra-large) {
	.page-brands__row {
		align-items: center;
		flex-direction: row;
		margin-left: -30px;
		margin-right: -30px;
	}

	.page-brands__col {
		padding-left: 30px;
		padding-right: 30px;

		&.page-brands__col--button {
			width: 27%;
			margin-top: 0;
		}
	}

	.page-brands__logos-item {
		width: calc(12.5% - 32px);
	}

	.page-brands__button {
		margin-right: 0;
	}
}

@include breakpoint(wide) {
	.page-brands__container {
		max-width: 2030px;
		padding: 0 80px;
	}

	.page-brands__logos-item {
		width: calc(9.09% - 32px);
	}
}

@include breakpoint(extra-wide) {
	.page-brands {
		padding-top: 92px;
		padding-bottom: 120px;
	}
}
