.newsletter-popup__popup:not(.popup--open) {
	display: none;
}

.newsletter-popup__popup-wrapper {
	max-width: 540px;
	padding-left: 20px;
	padding-right: 20px;
}

.newsletter-popup__popup-close {
	position: absolute;
	top: 80px;
	right: 40px;
	z-index: 1;
	margin: 0;
	padding: 5px;
	background-color: transparent;
	color: $brand-primary;
	transition: color 0.25s ease-out;

	svg {
		width: 16px;
		height: 16px;

		path {
			fill: $brand-primary;
		}
	}

	@include hover {
		color: $brand-secondary;
	}
}

.newsletter-popup__popup-content {
	padding: 0;
}

.newsletter-popup__row {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
}

.newsletter-popup__col {
	flex: 0 0 100%;
	max-width: 100%;
}

.newsletter-popup__image-wrapper {
	position: relative;
	overflow: hidden;
	width: 100%;
	padding-top: 67.5%;
}

.newsletter-popup__image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.newsletter-popup__content {
	padding-top: 50px;
	padding-bottom: 50px;
	padding-left: 20px;
	padding-right: 20px;
	text-align: center;
}

.newsletter-popup__logo {
	margin-bottom: 15px;
}

.newsletter-popup__logo-image {
	display: inline-block;
	width: 52px;
}

.newsletter-popup__title {
	font-size: 26px;
	line-height: 1.23;
	letter-spacing: 0.06em;
}

.newsletter-popup__description {
	margin-top: 10px;
	padding-left: 20px;
	padding-right: 20px;
	font-size: 14px;
	line-height: 1.3;
	letter-spacing: 0.3px;
	color: $paragraph-secondary;

	p {
		font-size: inherit;
		line-height: inherit;
		letter-spacing: inherit;
		color: inherit;
	}
}

.newsletter-popup__form {
	margin-top: 20px;
}

.newsletter-popup__form-col {
	flex: 0 0 100%;

	+ .newsletter-popup__form-col {
		margin-top: 14px;
	}
}

.newsletter-popup__form-input {
	height: 52px;
}

.newsletter-popup__form-success,
.newsletter-popup__form-error {
	margin-top: 12px;
	font-size: 12px;
	line-height: 1.5;
}

.newsletter-popup__form-success {
	color: $state-success;
}

.newsletter-popup__form-error {
	color: $state-error;
}

.newsletter-popup__col--image {
  display: none;
}

@include breakpoint(large) {
	.newsletter-popup__popup-wrapper {
		max-width: 900px;
		padding-left: 35px;
		padding-right: 35px;
	}

	.newsletter-popup__popup-close {
		top: 70px;
		right: 45px;
	}

	.newsletter-popup__row {
		min-height: 387px;
	}

	.newsletter-popup__col--image {
		display: flex;
		flex: 0 0 34.3%;
		max-width: 34.3%;
		align-self: stretch;
	}

	.newsletter-popup__col--content {
		flex: 0 0 65.7%;
		max-width: 65.7%;
	}

	.newsletter-popup__image-wrapper {
		height: 100%;
		padding-top: 0;
	}

	.newsletter-popup__content {
		padding-left: 40px;
		padding-right: 40px;
		text-align: left;
	}

	.newsletter-popup__logo {
		margin-bottom: 12px;
	}

	.newsletter-popup__title {
		font-size: 30px;
		line-height: 1.67;
		letter-spacing: 0.05em;
	}

	.newsletter-popup__description {
		padding-left: 0;
		padding-right: 0;
	}

	.newsletter-popup__form-col {
		+ .newsletter-popup__form-col {
			margin-top: 0;
		}
	}

	.newsletter-popup__form-col--input {
		flex: 1 0 69%;
	}

	.newsletter-popup__form-col--button {
		flex: 1 0 31%;
	}
}

@include breakpoint(extra-large) {
	.newsletter-popup__popup-wrapper {
		max-width: 970px;
		padding-left: 35px;
		padding-right: 35px;
	}

	.newsletter-popup__content {
		padding-left: 80px;
		padding-right: 80px;
	}
}

@media (max-width: 991px) {
	.newsletter-popup__image-wrapper {
		display: none;
	}
}
