.textarea,
textarea {
	display: block;
	height: 120px;
	padding-top: 12px;
	padding-bottom: 34px;
	padding-left: 12px;
	padding-right: 12px;
	border: 1px solid $text-field-border;
	background-color: $white;
	resize: none;
	font-size: 16px;
	font-weight: 400;
	line-height: 1.125;
	color: $text-field;
	transition-property: border, color;
	transition-duration: 0.25s;
	transition-timing-function: ease-out;

	-webkit-appearance: none;
	-moz-appearance: none;

	&:focus {
		border: 2px solid $text-field;
	}

	&.disabled,
	&[disabled] {
		border-color: $text-field-border;
		background-color: $state-inactive;
		cursor: default;
		pointer-events: none;
	}

	@include placeholder {
		color: $text-field-placeholder;
	}
}
