@import "variables";
@import "reset";
@import "fonts";
@import "typography";
@import "rte";

* {
	outline: none;
	box-sizing: border-box;
}

html,
body {
	background-color: #fff;
	font-family: $font-primary;
	scroll-behavior: smooth;
}

body {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	padding-top: var(--header-height, 141px) !important;
}

.content {
	flex: 1 0 auto;

	&.content--column {
		display: flex;
		flex-direction: column;
	}
}

svg.icon {
	width: 20px;
}

.visually-hidden {
	position: absolute;
	clip: rect(0 0 0 0);
	width: 1px;
	height: 1px;
	margin: -1px;
}

.hidden {
	display: none !important;
}

picture {
	img {
		filter: blur(0);
		transition-property: filter;
		transition-duration: 0.3s;
	}
}

.lazy {
	img {
		// width: 100%; // may be need remove/rework this solution
		filter: blur(10px);
	}
}

.container {
	display: block;
	max-width: 1440px;
	margin: 0 auto;
	padding: 0 20px;

	&.container--full-width {
		max-width: 100%;
	}

	&.container--no-padding {
		padding-left: 0;
		padding-right: 0;
	}

	&.container--inner {
		max-width: 860px;
	}
}

.row {
	display: flex;
	flex-wrap: wrap;
}

@include breakpoint(medium) {
	.container {
		padding: 0 32px;

		&.container--full-width {
			max-width: 100%;
		}

		&.container--no-padding {
			padding-left: 0;
			padding-right: 0;
		}
	}
}

@include breakpoint(wide) {
	.container {
		max-width: 1944px;
	}
}

#CybotCookiebotDialogTabContent input[type="checkbox"] {
	display: block;
}

.boost-pfs-minicart-wrapper {
	display: none !important;
}

[data-shop-url="https://www.craemerco.de"] a.boost-pfs-filter-scroll-to-top {
	bottom: 100px;
}