.product-free-services {
	margin-top: 16px;
	background-color: $background-light-grey;
}

.product-free-services__header {
	border-bottom: 1px solid $border-primary;
	display: flex;
	flex-direction: row;
}

.product-free-services__header-content {
	position: relative;
	display: flex;
	align-items: center;
	width: calc(100% - 85px);
	flex-direction: row;
	cursor: pointer;

	&::before {
		content: "";
		position: absolute;
		top: 50%;
		right: 16px;
		width: 14px;
		height: 1px;
		background-color: $black;
		transform: translateY(-50%) rotate(-90deg);
		border-radius: 8px;
		transition-property: transform;
		transition-duration: 0.25s;
		transition-timing-function: ease-out;
	}

	&::after {
		content: "";
		position: absolute;
		top: 50%;
		right: 16px;
		width: 14px;
		height: 1px;
		background-color: $black;
		border-radius: 8px;
		transition-property: transform;
		transition-duration: 0.25s;
		transition-timing-function: ease-out;
	}
}

.product-free-services__header-content.product-free-services__header-content--open {
	&::before {
		transform: unset;
	}
}

.product-free-services__header-svg-image {
	display: flex;
	width: 85px;
	height: 80px;
	margin-top: 5px;
	margin-bottom: 5px;
	margin-left: 5px;
}

.product-free-services__header-image-wrapper {
	position: relative;
	overflow: hidden;
	display: block;
	height: 80px;
	padding-top: 0;
}

.product-free-services__header-image {
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.product-free-services__header-title {
	position: relative;
	display: flex;
	align-items: center;
	width: 100%;
	height: 100%;
	padding-left: 22px;
	font-family: $font-avenir-next-condensed;
	font-size: 12px;
	font-weight: 600;
	line-height: 1.5;
	letter-spacing: 0.05em;
	text-transform: uppercase;
	flex-direction: row;
	max-width: 170px;
}

.product-free-services__header-icon {
	margin-left: 5px;

	svg {
		width: 20px;
	}
}

.product-free-services__body {
	padding-left: 22px;
	padding-right: 22px;
}

.product-free-services__items {
	position: relative;
	overflow: hidden;
	max-height: 0;
	transition-property: max-height, padding-top, padding-bottom;
	transition-duration: 0.5s;
	transition-timing-function: ease-out;
}

.product-free-services__item {
	display: flex;
	flex-direction: row;
	align-items: center;

	+ .product-free-services__item {
		margin-top: 12px;
	}
}

.product-free-services__item-icon-wrapper {
	width: 22px;
	height: 22px;
	margin-right: 15px;
}

.product-free-services__item-svg-icon-wrapper {
	width: 22px;
	height: 22px;
	margin-right: 15px;

	svg {
		max-width: 22px;
		max-height: 22px;
	}
}

.product-free-services__item-icon {
	display: block;
	width: 100%;
}

.product-free-services__item-text {
	font-size: 12px;
	font-weight: normal;
	font-style: normal;
	line-height: 1.5;
	color: $brand-primary;
}

.product-free-services__items.product-free-services__items--open {
	max-height: 300px;
	padding-top: 22px;
	padding-bottom: 22px;

	&::before {
		content: unset;
	}
}

.product-free-services__footer {
	display: flex;
	align-items: center;
	padding-top: 10px;
	padding-bottom: 12px;
	padding-left: 18px;
	border-top: 1px solid $border-primary;
	flex-direction: row;
}

.product-free-services__link-wrapper {
	+ .product-free-services__link-wrapper {
		padding-left: 10px;
		border-left: 1px solid $border-primary;
	}
}

.product-free-services__link {
	margin-right: 10px;
	font-size: 12px;
	font-weight: 500;
	line-height: 1.45;
	letter-spacing: -0.3px;
	text-decoration: none;
	border-bottom: 1px solid black;
	font-style: normal;
}

.product-free-services__text {
	font-size: 11px;
	font-weight: normal;
	line-height: 1.45;
	letter-spacing: 0.2px;
	font-style: normal;
}

@include breakpoint(medium) {
	.product-free-services__header-content {
		&::before {
			right: 25px;
		}

		&::after {
			right: 25px;
		}
	}

	.product-free-services__item {
		display: flex;
		flex-direction: row;
		align-items: center;

		+ .product-free-services__item {
			margin-top: 11px;
		}
	}

	.product-free-services__footer {
		padding-top: 11px;
		padding-left: 22px;
	}
}
