.services-promo {
	padding-bottom: 40px;
}

.services-promo__title {
	margin-bottom: 12px;
	font-size: 26px;
	line-height: 1.25;
	letter-spacing: 0.06em;
	color: $paragraph-primary;
}

.services-promo__title--small {
	margin-bottom: 14px;
	font-size: 24px;
	color: $paragraph-primary;
}

.services-promo__row {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
}

.services-promo__col {
	flex: 0 0 100%;
	max-width: 100%;

	+ .services-promo__col {
		margin-top: 32px;
	}
}

.services-promo__banner-wrapper {
	position: relative;
	max-width: 100%;
}

.services-promo__banner-image {
	display: block;
	width: 100%;
}

.services-promo__list {
	max-width: 273px;
	font-size: 14px;
	line-height: 1.43;
	color: $paragraph-secondary;

	p {
		position: relative;
		padding-left: 1em;
		font-size: inherit;
		line-height: inherit;
		color: inherit;

		&::before {
			content: "\2022";
			position: absolute;
			top: 0;
			left: 0;
		}

		+ p {
			margin-top: 0;
		}
	}
}

.services-promo__blocks {
	display: flex;
	margin-top: 30px;
	margin-left: -5px;
	margin-right: -5px;
}

.services-promo__blocks-item {
	flex: 0 0 50%;
	max-width: 50%;
	padding-left: 5px;
	padding-right: 5px;
}

.services-promo__blocks-item-image-wrapper {
	position: relative;
	overflow: hidden;
	padding-top: 93.33%;
}

.services-promo__blocks-item-image {
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.services-promo__blocks-item-caption {
	margin-top: 10px;
	font-size: 14px;
	line-height: 1.49;
	color: $paragraph-primary;
}

.services-promo__text {
	margin-top: 32px;
	font-size: 12px;
	line-height: 1.5;
	color: $paragraph-secondary;
}

@include breakpoint(medium) {
	.services-promo {
		padding-bottom: 91px;
	}

	.services-promo__title {
		font-size: 30px;
		letter-spacing: 0.05em;
	}

	.services-promo__title--small {
		font-size: 28px;
		letter-spacing: 0.07em;
	}

	.services-promo__blocks {
		margin-top: 64px;
		margin-left: -9px;
		margin-right: -9px;
	}

	.services-promo__blocks-item {
		padding-left: 9px;
		padding-right: 9px;
	}

	.services-promo__text {
		margin-top: 38px;
	}
}

@include breakpoint(extra-large) {
	.services-promo__col {
		+ .services-promo__col {
			margin-top: 0;
		}
	}

	.services-promo__col--banner {
		flex: 0 0 calc(50% + 40px);
		max-width: calc(50% + 40px);
	}

	.services-promo__col--blocks {
		flex: 0 0 calc(50% - 40px);
		max-width: calc(50% - 40px);
	}

	.services-promo__banner {
		padding-right: 79px;
	}
}
