.featured-product-card {
	display: flex;
	flex-direction: column;

	width: 100%;
	max-width: 100%;
}

.featured-product-card__title {
	margin-bottom: 1px;

	font-size: 18px;
	line-height: 1.33;
	text-align: center;
}

.featured-product-card__subtitle {
	margin: 0 auto;
	font-size: 15px;
	line-height: 1.25;
	letter-spacing: 0.4px;
	text-align: center;
	max-width: 275px;
}

.featured-product-card__image-wrapper {
	position: relative;
	overflow: hidden;

	display: block;

	margin-top: 15px;

	padding-top: 107.3%;

	object-fit: cover;
}

.featured-product-card__image {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

	width: 100%;
	height: 100%;
}

.featured-product-card__button {
	margin-top: 20px;
	margin-left: auto;
	margin-right: auto;

	padding-top: 17px;
	padding-bottom: 15px;
	padding-left: 64px;
	padding-right: 64px;
}

@media screen and (min-width: 768px) {
	.featured-product-card__title {
		font-size: 15px;
	}

	.featured-product-card__subtitle {
		font-size: 13px;
		max-width: initial;
		letter-spacing: 0.05px;
	}
}
