.contact-us {
	.input {
		height: 46px;
	}

	.help-center-content-contact-us__info-text {
		padding-bottom: 1px;
		font-size: 12px;
		font-weight: normal;
		line-height: 1.5;
		text-align: right;
		font-style: normal;
	}

	.help-center-content-contact-us__form-button {
		height: 52px;
	}
}

.help-center-content-contact-us__form-row {
	display: flex;
	margin-top: 0 !important;
	padding-bottom: 24px;
	flex-direction: column;

	.custom-select {
		width: 100%;
	}

	textarea {
		height: 213px;
	}
}

.help-center-content-contact-us__form-button-container {
	display: flex;
	flex-direction: row;
	justify-content: center;
}

.help-center-content-contact-us__form-col {
	padding-bottom: 24px;
	padding-left: 0;
	padding-right: 0;

	+ .help-center-content-contact-us__form-col {
		padding-left: 14px;
	}

	&:last-child {
		padding-bottom: 0;
	}
}

.help-center-content-contact-us__content-container {
	margin-bottom: 20px;
}

.help-center-content-contact-us__form--no-left-padding,
.help-center-content-contact-us__form-row {
	.select2-selection.select2-selection--single {
		padding-left: 0;
		border-left: unset;
		border-right: unset;
		border-top: unset;
	}

	input {
		border-left: unset;
		border-right: unset;
		border-top: unset;
	}

	.custom-select.custom-select--floating-label .custom-select__label {
		left: 0;
	}
}

.help-center-content-contact-us__form-button {
	max-width: 274px;
	width: 100%;
}

@include breakpoint(medium) {
	.help-center-content-contact-us {
		.form__select-container {
			padding-bottom: 24px;
		}
	}

	.help-center-content-contact-us__form-row {
		flex-direction: row;
		padding-bottom: 24px;

		textarea {
			height: 120px;
		}
	}

	.help-center-content-contact-us__form-col {
		padding-bottom: 0;
	}
}

@include breakpoint(large) {
	.help-center-content-contact-us__form-button-container {
		justify-content: left;
	}
}
