.collection-sort {
	position: relative;

	&.active {
		.collection-sort__button {
			&::after {
				transform: translateY(-50%) rotate(180deg);
			}
		}

		.collection-sort__dropdown {
			display: block;
		}
	}
}

.collection-sort__button {
	padding: 18px 16px !important;
	padding-right: 32px !important;
	border: 1px solid $brand-primary;
	font-family: $font-avenir-next-condensed-light;
	line-height: 1.15;
	letter-spacing: 0.06em;
	text-align: center;
	text-transform: uppercase;
	text-decoration: none;
	color: $brand-primary !important;

	&::after {
		content: "";
		position: absolute;
		top: 50%;
		right: 18px;
		display: block;
		width: 10px;
		height: 5px;
		background-image: url("data:image/svg+xml,%3Csvg width='11' height='5' viewBox='0 0 11 5' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.736026 0.109303C0.563525 -0.0403333 0.288591 -0.0360708 0.121945 0.118822C-0.0447016 0.273715 -0.0399551 0.520584 0.132546 0.67022L4.76694 4.69031C4.9353 4.83635 5.20227 4.8363 5.37056 4.69019L10.0009 0.670102C10.1734 0.520399 10.178 0.273528 10.0113 0.1187C9.84454 -0.036128 9.5696 -0.0402827 9.39717 0.10942L5.06855 3.86753L0.736026 0.109303Z' fill='black'/%3E%3C/svg%3E%0A");
		background-repeat: no-repeat;
		background-size: 10px 5px;
		transform: translateY(-50%);
		transition: transform 0.25s ease-out;
	}
}

.collection-sort__dropdown {
	position: absolute;
	top: 100%;
	left: 0;
	right: 0;
	z-index: 13;
	display: none;
	width: 100%;
	border: 1px solid $border-primary;
	background-color: $white;
}

.collection-sort__dropdown-item {
	+ .collection-sort__dropdown-item {
		margin-top: 8px;
	}

	&.active {
		.collection-sort__value {
			color: $paragraph-primary;
		}
	}
}

.collection-sort__value {
	position: relative;
	overflow: hidden;
	display: block;
	width: 100%;
	padding: 10px 12px;
	background: none;
	font-size: 14px;
	font-weight: 400;
	text-align: left;
	text-overflow: ellipsis;
	color: $paragraph-secondary;
	white-space: nowrap;
	transition: color 0.25s ease-out;

	@include hover {
		color: $paragraph-primary;
	}
}

@include breakpoint(large) {
	.collection-sort__button {
		border: 0;
		font-family: $font-primary;
		line-height: inherit;
		letter-spacing: normal;
		text-align: left;
		text-transform: none;
	}

	.collection-sort__value {
		width: 155px;
	}
}
