.product-card-options {
	padding: 8px;
	background-color: $white;
	text-align: center;
}

.product-card-options__title {
	margin-bottom: 12px;
	font-family: $font-secondary;
	font-size: 12px;
	font-weight: 450;
	line-height: 1.5;
	color: $paragraph-secondary;
}

.product-card-options__list {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	margin-top: -10px;
	margin-left: -8px;
	margin-right: -8px;
	list-style: none;
}

.product-card-options__item {
	margin-top: 10px;
	padding-left: 8px;
	padding-right: 8px;
}

.product-card-options__item-link {
	position: relative;
	display: block;
	padding: 0;
	font-family: $font-secondary;
	font-size: 14px;
	font-weight: 400;
	line-height: 1.5;
	text-decoration: none;
	color: $paragraph-primary;

	&::after {
		content: "";
		position: absolute;
		top: initial;
		bottom: 0;
		left: 0;
		display: block;
		width: 0;
		height: 1px;
		border-radius: 0;
		background-color: $brand-primary;
		transform: none;
		transition: width 0.25s ease;
	}

	@include hover {
		&::after {
			width: 100%;
		}
	}
}
