.select2-container {
	position: static !important;
	width: 100% !important;
	max-width: 100% !important;
	background-color: $white;
	font-size: 16px;
	font-weight: 400;
	color: $brand-secondary;

	&.select2-container--open {
		.select2-selection__arrow {
			transform: translateY(-50%) rotate(180deg);
		}
	}

	.select2-selection.select2-selection--single {
		position: relative;
		display: block;
		padding-top: 14px;
		padding-bottom: 14px;
		padding-left: 12px;
		padding-right: 30px;
		border: 1px solid $text-field-border;
		color: $text-field;
		cursor: pointer;
	}

	.select2-selection__placeholder {
		color: $text-field-placeholder;
	}

	.select2-selection__arrow {
		position: absolute;
		top: 50%;
		right: 14px;
		display: block;
		width: 10px;
		height: 5px;
		background-image: url("data:image/svg+xml,%3Csvg width='11' height='5' viewBox='0 0 11 5' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.736026 0.109303C0.563525 -0.0403333 0.288591 -0.0360708 0.121945 0.118822C-0.0447016 0.273715 -0.0399551 0.520584 0.132546 0.67022L4.76694 4.69031C4.9353 4.83635 5.20227 4.8363 5.37056 4.69019L10.0009 0.670102C10.1734 0.520399 10.178 0.273528 10.0113 0.1187C9.84454 -0.036128 9.5696 -0.0402827 9.39717 0.10942L5.06855 3.86753L0.736026 0.109303Z' fill='black'/%3E%3C/svg%3E%0A");
		background-repeat: no-repeat;
		background-size: 10px 5px;
		transform: translateY(-50%);
		transition: transform 0.25s ease-out;
	}

	.select2-search.select2-search--dropdown {
		display: none;
	}

	.select2-dropdown {
		position: absolute;
		top: 100%;
		left: 0;
		z-index: 1;
		width: 100% !important;
		background-color: $white;
	}

	.select2-results__options {
		overflow-y: auto;
		max-height: 168px;
		padding-left: 12px;
		padding-right: 12px;
		border: 1px solid $text-field-border;
		border-top: 0;

		&::-webkit-scrollbar {
			width: 4px;
		}

		&::-webkit-scrollbar-track {
			background-color: $text-field-disabled;
		}

		&::-webkit-scrollbar-thumb {
			background-color: $text-field-border;
		}
	}

	.select2-results__option {
		padding-top: 10px;
		padding-bottom: 10px;
		transition: color 0.25s ease-out;
		cursor: pointer;

		@include hover {
			color: $brand-primary;
		}
	}
}
