.link {
	position: relative;
	display: inline-block;
	padding: 0;
	background-color: transparent;
	font-family: $font-avenir-next-condensed-light;
	font-size: 12px;
	font-weight: 500;
	line-height: 1.67;
	letter-spacing: 0.07em;
	text-transform: uppercase;
	text-decoration: none;
	color: $brand-primary;

	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		display: block;
		width: 100%;
		height: 1px;
		background-color: $brand-primary;
		transition: width 0.25s ease;
	}

	&.link--reverse {
		&::after {
			width: 0;
		}

		@include hover {
			&::after {
				width: 100%;
			}
		}
	}

	@include hover {
		&::after {
			width: 0;
		}
	}
}
