.account-edit-addresses__form-checkbox-container {
	margin-top: 38px;
	margin-bottom: 22px;
}

.account-edit-addresses__submit-button-container {
	display: flex;
	flex-direction: column;
	margin-top: 20px;
}

.account-edit-addresses__submit-button {
	max-width: 335px;
	height: 52px;
}

.account-edit-addresses__form-row {
	display: flex;
	flex-direction: column;
}

.account-edit-addresses__custom-select-label {
	top: 2px !important;
	left: 13px !important;
}

.account-edit-addresses__form-col {
	+ .account-edit-addresses__form-col {
		margin-top: 14px;
	}
}

.account-edit-addresses {
	.account-content__header-icon {
		width: 36px;
		height: 36px;

		svg {
			width: 100%;
			height: 100%;
		}
	}
}

@include breakpoint(small) {
	.account-edit-addresses__form-row {
		flex-direction: row;
	}

	.account-edit-addresses__form-col {
		+ .account-edit-addresses__form-col {
			margin-top: 0;
		}
	}
}

@include breakpoint(medium) {
	.account-edit-addresses {
		.account-content__header-icon {
			width: 48px;
			height: 48px;
		}

		.account-content__back-link-icon {
			padding-right: 10px;
		}
	}

	.account-edit-addresses__form-checkbox-container {
		margin-top: 27px;
	}

	.account-edit-addresses__submit-button {
		max-width: 274px;
	}
}
