.button,
input[type="button"],
input[type="submit"],
input[type="reset"] {
	display: inline-block;
	padding-top: 18px;
	padding-bottom: 16px;
	padding-left: 16px;
	padding-right: 16px;
	border: 1px solid transparent;
	font-family: $font-avenir-next-condensed-light;
	font-size: 14px;
	font-weight: 400;
	line-height: 1.15;
	letter-spacing: 0.06em;
	text-align: center;
	text-transform: uppercase;
	text-decoration: none;
	transition-property: border-color, background-color, box-shadow, color;
	transition-duration: 0.25s;
	transition-timing-function: ease-out;

	&.button--primary {
		border-color: $brand-primary;
		background-color: $brand-primary;
		color: $white;

		@include hover {
			border-color: $brand-secondary;
			background-color: $brand-secondary;
		}
	}

	&.button--secondary {
		border-color: $brand-primary;
		background-color: $white;
		color: $brand-primary;

		@include hover {
			background-color: $brand-primary;
			color: $white;
		}
	}

	&.button--main {
		min-width: 196px;

		&.button--primary {
			@include hover {
				border-color: $brand-hover;
				background-color: $brand-hover;
			}
		}
	}

	&.button--large {
		padding-top: 8px;
		padding-bottom: 6px;
		font-size: 15px;
		line-height: 1.2;
		letter-spacing: 0.5px;
	}

	&.button--regular {
		padding-top: 8px;
		padding-bottom: 6px;
	}

	&.button--small {
		padding-top: 4px;
		padding-bottom: 2px;
		padding-left: 12px;
		padding-right: 12px;
		font-size: 11px;
		line-height: 1.5;
		letter-spacing: 0.5px;

		.button__icon {
			width: 16px;
			height: 16px;

			svg {
				max-height: 14px;
			}
		}
	}

	&.button--shadow {
		padding-top: 19px;
		padding-bottom: 19px;
		background-color: $white;
		box-shadow: 0 4px 14px rgba(0, 0, 0, 0.07);
		font-family: $font-avenir-next-condensed;
		font-weight: 700;
		line-height: 1.45;
		letter-spacing: 0.08em;

		@include hover {
			box-shadow: 0 4px 14px rgba(0, 0, 0, 0.5);
		}
	}

	&.button--full-width {
		width: 100%;
	}

	&.button--text {
		padding: 0;
		border: 0;
		font-size: 12px;
		line-height: 1.67;
		letter-spacing: 0.07em;

		&.button--text-large {
			font-size: 15px;
			line-height: 1.2;
			letter-spacing: 0.5px;
		}

		&.button--text-regular {
			font-size: 14px;
			line-height: 1.15;
			letter-spacing: 0.06em;
		}

		&.button--text-small {
			font-size: 11px;
			line-height: 1.5;
			letter-spacing: 0.5px;
		}

		@include hover {
			color: $brand-secondary;
		}
	}

	&.button--icon-left,
	&.button--icon-right {
		position: relative;
	}

	&.button--icon-left {
		padding-left: 48px;

		.button__icon {
			left: 16px;
		}

		&.button--small {
			padding-left: 36px;

			.button__icon {
				left: 12px;
			}
		}
	}

	&.button--icon-right {
		padding-right: 48px;

		.button__icon {
			right: 16px;
		}

		&.button--small {
			padding-right: 36px;

			.button__icon {
				right: 12px;
			}
		}
	}

	.button__icon {
		position: absolute;
		top: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 24px;
		height: 24px;
		transform: translateY(-50%);

		svg {
			display: block;
			max-height: 17px;

			path {
				fill: currentColor;
			}
		}
	}

	&.button--wishlist {
		padding: 0;
		border: 0;
		background: transparent;

		&::before {
			content: "";
			display: block;
			width: 13px;
			height: 13px;
			background-image: url("data:image/svg+xml,%3Csvg width='14' height='14' viewBox='0 0 14 14' fill='none' xmlns='http://www.w3.org/2000/svg' class='icon icon-wishlist'%0A%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M.428 4.798A3.87 3.87 0 014.335.973 3.928 3.928 0 017 2.003 3.922 3.922 0 019.66.974a3.872 3.872 0 013.912 3.825v.006a3.776 3.776 0 01-.873 2.415l-.003.004-5.276 5.905a.562.562 0 01-.84 0l-2.638-2.951a1510.948 1510.948 0 01-2.651-2.97l-.001-.001A3.77 3.77 0 01.428 4.8v-.001zm1.725 1.686a2.639 2.639 0 01-.6-1.678A2.746 2.746 0 014.33 2.098h.001a2.806 2.806 0 012.226 1.078.563.563 0 00.886 0 2.803 2.803 0 012.22-1.078h.001a2.748 2.748 0 012.783 2.71 2.64 2.64 0 01-.603 1.68L7 11.909 4.78 9.425a8508.452 8508.452 0 01-2.43-2.72l-.144-.16-.04-.045-.01-.012-.003-.004z' fill='%23000' /%3E%3C/svg%3E%0A");
			background-repeat: no-repeat;
			background-size: 13px 13px;
			background-position: 50% 50%;
			transition: transform 0.25s ease-out;
		}

		@include hover {
			background-color: $white;
			color: $brand-primary;

			&::before {
				transform: scale(1.05) translate3d(0, 0, 0);
			}
		}
	}

	&.button--wishlist-added {
		&::before {
			background-image: url("data:image/svg+xml,%3Csvg fill='none' xmlns='http://www.w3.org/2000/svg' class='icon icon-heart-filled' viewBox='0 0 14 13'%0A%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M13.067 6.583l-5.62 6.22a.604.604 0 01-.894 0l-2.81-3.11A1584.63 1584.63 0 01.919 6.567l-.001-.001A3.944 3.944 0 010 4.03C.027 1.779 1.892-.024 4.161 0A4.21 4.21 0 017 1.086 4.203 4.203 0 019.832 0c2.275-.024 4.14 1.78 4.168 4.03v.006a3.96 3.96 0 01-.93 2.544l-.003.004z' fill='%23000' /%3E%3C/svg%3E%0A");
		}
	}

	&.disabled,
	&[disabled] {
		border-color: $state-inactive;
		background-color: $state-inactive;
		cursor: default;
		pointer-events: none;
	}
}

input[type="submit"],
input[type="reset"],
input[type="button"],
.shopify-challenge__button {
	@extend .button;
	@extend .button--primary;
}
