.product-media {
	position: relative;
	transition: 0.25s opacity;
}

.product-media--initialized {
	visibility: visible;
	opacity: 1;
}

.product-media__badge {
	position: absolute;
	top: 0;
	right: 0;
	z-index: 1;
}

.product-media__badge--sustainable {
	top: unset;
	bottom: 26px;
	padding: 4px 10px;
}

.product-media__slider-container {
	position: relative;

	&:not(.initialized) {
		.product-media__slider-arrow {
			visibility: hidden;
			opacity: 0;
		}
	}
}

.product-media__slide {
	position: relative;
	overflow: hidden;
	display: block;
	padding-top: 124.8%;
}

.product-media__slide-image {
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

.product-media__slider-arrow {
	position: absolute;
	top: 50%;
	z-index: 1;
	display: block;
	opacity: 0.8;
	width: 32px;
	height: 32px;
	padding: 0;
	background-color: $white;
	text-align: center;
	color: #1a1a1a;
	transform: translateY(-50%);
	transition: all 0.25s ease-out;

	&:disabled {
		visibility: hidden;
		opacity: 0;
	}

	svg {
		display: inline-block;
		vertical-align: middle;
		max-width: 4px;

		path {
			fill: currentColor;
		}
	}
}

.product-media__slider-arrow--prev {
	left: 20px;

	svg {
		transform: rotate(180deg);
	}
}

.product-media__slider-arrow--next {
	right: 20px;
}

.product-media__slider-pagination {
	position: static;
	margin-top: 20px;
	font-size: 0;
	text-align: center;

	.swiper-pagination-bullet {
		opacity: 1;
		width: 6px;
		height: 6px;
		margin-left: 4px;
		margin-right: 4px;
		border-radius: 50%;
		background-color: #dfdfdf;
		transition: background-color 0.25s ease-out;

		&.swiper-pagination-bullet-active {
			background-color: #4d4d4f;
		}
	}
}

.product-media__gallery {
	display: none;
}

.product-media__gallery-row {
	display: flex;
	flex-wrap: wrap;
	margin-top: -12px;
	margin-left: -12px;
	margin-right: -12px;
}

.product-media__gallery-col {
	flex: 0 0 50%;
	max-width: 50%;
	margin-top: 12px;
	padding-left: 12px;
	padding-right: 12px;
}

.product-media__gallery-item {
	position: relative;
	overflow: hidden;
	width: 100%;
	padding-top: 131.86%;
}

.product-media__gallery-item-image {
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: 50% 50%;
}

@include breakpoint(medium) {
	.product-media {
		position: sticky;
		top: var(--header-height, 120px);
	}

	.product-media__badge--sustainable {
		bottom: 36px;
	}

	.product-media__slider-pagination {
		margin-top: 30px;
	}
}

@include breakpoint(large) {
	.product-media__slider {
		display: none;
	}

	.product-media__gallery {
		display: block;
	}

	.product-media__badge {
		top: 12px;
	}

	.product-media__badge--sustainable {
		top: unset;
		bottom: 0;
	}
}
