.pagination {
	.pagination__list {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.pagination__item {
		margin-left: 4px;
		margin-right: 4px;

		&.pagination__item--prev {
			margin-left: 0;
			margin-right: 20px;
		}

		&.pagination__item--next {
			margin-left: 20px;
			margin-right: 0;
		}
	}

	.pagination__link {
		position: relative;
		overflow-x: hidden;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 32px;
		height: 32px;
		padding: 0 4px;
		border: 1px solid $border-primary;
		background-color: $white;
		font-size: 14px;
		font-weight: 400;
		line-height: 1.14;
		letter-spacing: 0.06em;
		text-align: center;
		text-decoration: none;
		color: $brand-primary;
		white-space: nowrap;
		transition: all 0.25s ease-out;

		&.pagination__link--current {
			border-color: $brand-primary;
			background-color: $brand-primary;
			color: $white;
		}

		&.pagination__link--prev,
		&.pagination__link--next {
			width: unset;
		}
	}

	a {
		@include hover {
			border-color: $brand-primary;
			background-color: $brand-primary;
			color: $white;
		}
	}
}
